<template>
  <div class="warnMsg">
      <div class='warnMsg-up'>
          <div>
            <img :src="formData.FaceID" /> 
          </div>
          <div>
            <img :src="formData.FaceHitID" /> 
          </div>
      </div>
      <div class='warnMsg-center'>
        <el-row>
            <el-col :span="12">
                <div></div>
            </el-col>
            <el-col :span="12">
                <div></div>
            </el-col>
        </el-row>
      </div>
      <div class='warnMsg-down'> 
          <el-row>
            <el-col :span="12" class='warnMsg-down-left'>
                <div>{{': ' +`${formData.Name}`}}</div>
                <div>{{': ' +`${formData.Gender}`}}</div>
                <div>{{': ' +`${formData.BornTime}`}}</div> 
                <div>{{': ' +`${formData.CredentialType}`}}</div>
                <div>{{': ' +`${formData.CredentialNumber}`}}</div> 
            </el-col>
            <el-col :span="12" class='warnMsg-down-right'>
                <div></div>
                <el-progress type="circle" :percentage="formData.Similarity" :stroke-width='percentage.paddingWidth' :width='percentage.width' :color='percentage.colorPrecent'>
                    <div calss='info'>{{msgInfo}}</div>
                </el-progress>
            </el-col>
          </el-row> 
      </div>
  </div>
</template>

<script>
export default {
  components:{},
  props:{
      pData:{
          type: Object,
          default: () =>{}
      }
  },
  data(){
    return {
        msgInfo: '',
        /* img:{
            capture: require('../../../assets/img/login/hit-user1.jpg'),
            library: require('../../../assets/img/login/hit-user1-d.jpg'),
        }, */
        formData:{
            FaceID: '',
            FaceHitID: '',
            Name: '',
            Gender: '',
            BornTime: '',
            CredentialType: '',
            CredentialNumber: '',
            Similarity: ''
        },
        percentage:{ 
            paddingWidth: 10,
            width: 120,
            colorPrecent: '#20a0ff'
        },  
    }
  },
  watch:{},
  computed:{},
  methods:{},
  created(){
      if(this.pData){ 
          this.formData = this.pData;  
          let type = this.formData.CredentialType;  
          switch (type) {
              case '0':
                this.formData.CredentialType = '' 
                  break;
              case '1':
                  this.formData.CredentialType = '' 
                  break;
              case '2':
                this.formData.CredentialType = '' 
                  break;
              case '3':
                  this.formData.CredentialType = '' 
                  break;
              case '4':
                this.formData.CredentialType = '' 
                  break;
              case '5':
                  this.formData.CredentialType = '' 
                  break;
              default:
                  break;
          } 
          //this.formData.Similarity = this.msgInfo +'\n'+ this.formData.Similarity;
      }
  },
  mounted(){}
}
</script>
<style lang="scss" >
.warnMsg{
    width: 100%;
    .warnMsg-up{
        width: 100%;
        div{ 
          display: inline; 
          padding: 5px;
          img{ 
            width: 228px;
            height: 252px; 
            border: 1px solid #0e94d2; 
            padding: 10px;
          } 
        }  
    }
    .warnMsg-center{
        div{
            text-align: center;
            color: #FFF;
        }
    }
    .warnMsg-down{
        .warnMsg-down-left,.warnMsg-down-right{
             div{
                 margin-bottom: 5px;
                 color: #FFF;
             }
             .el-progress--circle{
                 margin-left: 36px;
             }
        }  
        .warnMsg-down-left{
            text-align: left;
        }
    }   
}
</style>


