<template>
    <div class="libOpt " >
         <el-form :model="formData" ref="formData" :rules="validata" :inline="true" label-width="100px">
            <el-form-item label="" prop="name">
                <el-input v-model="formData.name" class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="type">
               <el-select 
                v-model="formData.type"
                popper-class="m-select-con"
                placeholder="">
                    <el-option
                   
                    v-for="item in list "
                    :key="item.value"
                    :label="item.text"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=""  prop="description"> 
                <el-input v-model="formData.description" :rows="3" type="textarea"  class="j-input" placeholder=""></el-input> 
            </el-form-item>  
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="ok()"></el-button>
        </span>
    </div>
</template>
<script>
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      formData: {},
      constant: [],
      validata: {
        name: [{ required: true, message: "" }],
        type: [{ required: true, message: "" }],
        description: [{ required: true, message: "" }]
      },
      list: [
        {
          text: "",
          value: 2
        },
        {
          text: "",
          value: 3
        },
        {
          text: "",
          value: 4
        }
      ]
    };
  },
  created() {
    this.formData = this.pData.data || {};
    /*  this.getConstant(); */
  },
  methods: {
    getConstant() {
      //
      this.$http.get(`/operation-log/constant`).then(response => {
        this.constant = response.data;
        this.getSearch();
      });
    },
    getSearch() {
      let Type = this.constant.find(res => {
        //
        return res.id == "Type";
      });
      this.list = Type.options || [];
    },
    ok() {
      this.$refs.formData.validate(validate => {
        if (validate) {
          this.$http[this.pData.methods || "post"](
            this.pData.url,
            this.formData
          ).then(response => {
             this.$message({
            message: response.data.msg||"",
            type: "success"
          });
            this.$emit("result", true);
          });
        } else {
          console.log("");
          return false;
        }
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="scss">
.libOpt {
  text-align: center;
  .el-form {
    margin-top: 15px;
    .j-input {
      .el-input__inner {
        border: 1px solid #0e94d2;
      }
    }
  }
  .textButton {
    .el-button {
      background: #1b4272;
      color: #fff;
    }
  }
}
</style>




