<template>
  <div ref="line" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
export default {
  name: "guTags",
  data() {
    // 
    let echarts = require("echarts/lib/echarts");
    // 
    require("echarts/lib/chart/bar");
    // title
    require("echarts/lib/component/tooltip");
    require("echarts/lib/component/title");
    return {
      data: [],
      date: []
    };
  },
  mounted() {
    this.getData();
  },
  created() {},
  methods: {
    getData() {
      this.$http.get("/big-screen/trends").then(response => {
        var d1 = this.$util.resolveObject(response.data);
        this.data.push(d1.values);
        this.date.push(d1.keys);
        console.log( this.data,this.date, "pie");
        this.drawLine();
      });
    },
    drawLine() {
      // domecharts
      let line = this.$echarts.init(this.$refs.line, "shine");
      // 
      var option = {
        color: ["#37a2da"],
        title: {
          text: "",
          textStyle: {
            fontSize: 12,
            padding: "50px",
            color: "#ffffff"
          }
        },
        grid: {
          top: "15%",
          bottom: "15%"
        },
        tooltip: {},
        xAxis: {
          name: "",
          data: this.date[0],
          axisLine: {
            lineStyle: {
              color: "#ffffff"
            }
          },
          axisLabel: {
            show: true,

            textStyle: {
              color: "#ffffff"
            }
          }
        },
        yAxis: {
          splitArea: { show: false },
          splitLine: {
            //
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#ffffff"
            }
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff"
            }
          }
        },

        series: [
          {
            barWidth: 20,
            data: this.data[0],
            name: "test",
            type: "line",
            areaStyle: {},
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#5fb5fe" },
                  { offset: 1, color: "#4c41fe" }
                ])
              }
            }
          }
        ]
      };
      line.setOption(option, true);
    }
  }
};
</script>
<style>
</style>
<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
  width: 99%;
}
</style>



