<template>
   <div class='comparisonHit faceCapture'>
        <div class="base-search">
          <label></label>
          <span class="m-input">
            <input name type="text" v-model="searchForm.name" placeholder="" class="w-200"  clearable/>
          </span>
          <label></label>
            <el-select v-model="searchForm.repoId" popper-class="m-select-con" placeholder="" clearable>
              <el-option
                v-for="(item, index) in constant.lib"
                :key="index"
                :label="item.name"
                :value="item.id"
              > </el-option>
            </el-select>
            <label></label>
            <el-select v-model="searchForm.gender" popper-class="m-select-con" placeholder="" clearable>
              <el-option
                v-for="(item, index) in constant.ger"
                :key="index"
                :label="item.text"
                :value="item.value"
              > </el-option>
            </el-select>
          <a href="javascript:void(0)" @click="search" class="btn-search"></a>
          <a href="javascript:void(0)" @click="openSeniorSearch" class="btn-search-more"></a>
          <a href="javascript:void(0)" @click="add" class="btn-add" v-if="permissionResource.hasPermission('CREATION')"></a>
          <!-- <a href="javascript:void(0)" class="btn-import"></a> 
          <a href="javascript:void(0)" class="btn-del fr"></a> -->
        </div>
        <div class="search-more base-search clearfix device-searh-more" v-show="advancedSearch">
          <div class="search-more-item">
            <label></label>
            <span class="m-input">
                <input name type="text" v-model="searchForm.name" placeholder="" class="w-200"  clearable/>
            </span>
          </div>
          <div class="search-more-item">
            <label></label>
                <el-select v-model="searchForm.repoId" popper-class="m-select-con" placeholder="" clearable>
                <el-option
                    v-for="(item, index) in constant.lib"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                > </el-option>
                </el-select>
          </div>
          <div class="search-more-item">
            <label></label>
            <el-select v-model="searchForm.gender" popper-class="m-select-con" placeholder="" clearable>
              <el-option
                v-for="(item, index) in constant.ger"
                :key="index"
                :label="item.text"
                :value="item.value"
              > </el-option>
            </el-select>
          </div>
          <div class="search-more-item">
            <label></label>
            <el-select v-model="searchForm.credentialType" popper-class="m-select-con" placeholder="" clearable>
                <el-option
                v-for="(item, index) in constant.credentialType"
                :key="index"
                :label="item.text"
                :value="item.value"
                > </el-option>
            </el-select>
          </div>
          <div class="search-more-item">
            <label></label>
            <span class="m-input">
              <input name type="text" v-model="searchForm.credentialNumber" placeholder=""  clearable/>
            </span>
          </div>
          <!-- <div class="search-more-item">
            <label></label>
            <el-select v-model="searchForm.credentialNumber" popper-class="m-select-con" placeholder="">
                <el-option
                v-for="(item, index) in constant.ger"
                :key="index"
                :label="item.text"
                :value="item.value"
                > </el-option>
            </el-select>
          </div> -->
          <div class="search-more-item">
            <label></label>
            <span class="m-input">
              <input name type="text" v-model="searchForm.country" placeholder=""  clearable/>
            </span>
          </div>
          <div class="search-more-item">
            <label></label>
            <span class="m-input">
              <input name type="text" v-model="searchForm.deviceID" placeholder=""  clearable/>
            </span>
          </div> 
          <a href="javascript:void(0)" class="search-more-btn" @click="seniorSearch"></a>
          <a
            href="javascript:void(0)"
            class="search-more-btn btn-hidden"
            @click="hideSeniorSearch"
          ></a>
        </div>
        <div class="line"></div>
        <div >
            <div class="face-list" v-loading="loading"> 
                <ul class="clearfix">
                    <li v-for="(item, key) in messeageList" :key="key" class='zp-face-img'>
                        <div class='msgInfo' @click="showpop(item)"> 
                            <img v-if="imgShow" :src="item.imageUrl" />  
                            <div class="zp-title">
                                <!-- <el-tooltip :content="'' +item.createTime" placement="top" popper-class="atooltip">
                                    <div>{{'' +item.createTime}}</div>
                                </el-tooltip> --> 
                                <el-tooltip :content="''+item.name" placement="top" popper-class="atooltip">
                                    <div>{{'' + item.name}}</div>
                                </el-tooltip>
                                <el-tooltip :content="': ' +item.credentialNumber" placement="top" popper-class="atooltip">
                                    <div>{{': ' +item.credentialNumber}}</div>
                                </el-tooltip> 
                            </div>  
                        </div>
                        <div class='optButton'> 
                            <el-button class='editButton' type="primary" icon="el-icon-edit" @click="edit(item)" v-if="permissionResource.hasPermission('UPDATING')"></el-button>
                            <el-button class='deleteButton' type="danger" icon="el-icon-delete" @click="deleteTab(item.id)" v-if="permissionResource.hasPermission('DELETION')"></el-button>
                        </div>
                    </li> 
                </ul> 
            </div>
        </div> 
        <pagination :pData='pageOption' @init='init'/>
        <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
            <faceOpt ref='formFace' :pData='msgDialogInfo'  @equit='equit'/>
        </msgDialog>
        <msgDialog v-if="dialogVisible.detailDialog" :pDialog='msgDialog' @equit="equit">
            <faceDetail :pData='captureInfo' />
        </msgDialog>
   </div>
</template>
<script>
import pagination from '@/views/xinjiang/common/components/pagination'
import msgDialog from '@/views/xinjiang/common/components/dialog/msgDialog'
import faceOpt from '@/views/xinjiang/faceLibrary/opt/faceOpt'
import faceDetail from '@/views/xinjiang/faceLibrary/opt/faceDetail'
import {imageBaseUrl}  from "@/utils/global"
import {baseUrl} from "@/utils/global"
import {dateArraySort} from '@/utils/datetime'
import { format } from "@/utils/datetime"

export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return { 
        permissionResource : permissionResource,
        loading: false,
        imgShow: false,
        advancedSearch: false, 
        imageBaseUrl:imageBaseUrl,
        constant:{
            lib:[
                {text: '',value: 0},{text: '',value: 1}
            ],
            ger:[
                {text: '',value: 0},{text: '',value: 1}
            ],
            credentialType:[
                {text: '',value: 0},
                {text: '',value: 1},
                {text: '',value: 2},
                {text: '',value: 3},
                {text: '',value: 4},
                {text: '',value: 5} 
            ]
        },
        searchForm:{

        },
        formData:{
            BeginCreateTime: '',
            EndCreateTime: '',
            areaCode: ''
        },
        dateTime: {
          pickerOptionsStart: {
            disabledDate: time => {
              if (this.formData.EndCreateTime) { 
                return time.getTime() > this.formData.EndCreateTime;
              }  
            }
          },
          pickerOptionsEnd: {
            disabledDate: time => {
              if (this.formData.BeginCreateTime) {  
                return time.getTime() < this.formData.BeginCreateTime ;
              }  
            }
          }
        }, 
        captureInfo:{
            capture: '',
            library: '',
            percent: '75%',
            data:{
                name: ''
            }
        },
        dialogVisible:{
            msgDialog: false,
            detailDialog: false
        },
        msgDialog:{
            title: '',
            dialogVisible: true,
            width: '50%' 
        }, 
        pageOption:{
            currentPageNum: 1,  
            pageSize: 10, 
            firstPage: 1,
            lastPage: 7, 
            total: 70, 
            url: 'vcpinterface/ConvFaceReceive/FindPage' 
        },
        messeageList:[
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
        ]
    };
  },
  components: { pagination, msgDialog, faceOpt,faceDetail },
  methods:{
    openSeniorSearch() { 
      this.advancedSearch = !this.advancedSearch;
    },
    dataInit(){
        this.$http.post(baseUrl + `/vcpface/facerepository/findByPage`).then(response =>{
            this.constant.lib = response.data.res.data.map(item => (({name, id}) => ({name, id}))(item)); 
        })
    },
    hideSeniorSearch() {
      this.advancedSearch = false;
    },
    init(formData) { 
        this.loading = true; 
        this.imgShow = false; 
        if(formData) {
          formData.pageNum = formData.PageNum;
        }
        this.$http.post(`/vcpface/face/findByPage`, Object.assign({
          pageSize: 21 
            /* pageSize: 21,
            pageNum: pageNum,
            para: {
                type: 0
            } */
        },{para: this.searchForm},formData)
      ,{headers:{"Content-Type": "application/json"}}).then(response =>{ 
            this.pageOption = {
                  currentPageNum: 1,
                  pageSize: 21,
                  firstPage: 1,
                  lastPage: (response.data.res.totalRow % 21 == 0) ? response.data.res.totalRow / 21 : parseInt(response.data.res.totalRow / 0) + 1 , 
                  total: response.data.res.totalRow, 
                  url: '/vcpface/face/findByPage' 
            }   
            let Host = imageBaseUrl;
            let data = response.data.res.data; 
            let newArry = [];
            for(let i = 0; i < data.length; i++) {
                //data[i].name = '' + data[i].name;
                //data[i].createTime = '' + format(data[i].createTime);
                data[i].imageUrl = Host + data[i].imageUrl;  
                //data[i].credentialNumber = ': ' + data[i].credentialNumber; 

                // this.$http.get(`/regisger-object/get-area-name/${data[i].AreaCode}`).then(response =>{
                //     data[i].AreaCode = '' + response.data;
                // }); 
                // this.$http.get(`/regisger-object/device-id/${data[i].DeviceID}`).then(response =>{
                //     data[i].DeviceID = '' + response.data
                // }); 
                newArry.push(data[i]);
            }
            this.messeageList = dateArraySort(newArry, 'createTime'); 
            console.log('tag', this.messeageList)
            this.loading = false;
            this.imgShow = true;
        })
    },
    seniorSearch() {
      /* let param = {};
      if (this.treeNode.id) {
        param.areaId = this.treeNode.id;
      }
      Object.assign(param, this.searchForm);
      this.$refs.deviceList.searchTableData(param); */
      let data = JSON.parse(JSON.stringify(this.searchForm));  
      this.init(data);
    },
    search(){
        let data = JSON.parse(JSON.stringify(this.searchForm));  
        this.init(data);
    },
    edit(formData){
        this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: formData};
      this.dialogVisible.msgDialog = true; 
      this.$nextTick(()=>{ 
        this.$refs.formFace.editInit(); 
      }) 
    },
    deleteTab(formId){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/vcpface/face/delete`,{id: formId}).then(response =>{
            if(response.data.resultCd == 0){
              this.$notify({
                title: '',
                message: '!!',
                type: 'success'
              });
              this.init();
            } 
          })
        }); 
    },
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    }, 
    showpop(data){
        this.msgDialog = {
          title: "",
          dialogVisible: true,
          width: "520px" 
        },
        this.captureInfo = data;
        this.dialogVisible.detailDialog = true;
    },
    equit(){
        this.init(); 
        this.dialogVisible.msgDialog = false;
        this.dialogVisible.detailDialog = false;
    },
    //
    changeTime(value, status) {  
      let time = new Date(value).getTime();  
      switch (status) {
          case 'startTime':
              this.formData.BeginCreateTime = time; 
              break;
          case 'endTime':
              this.formData.EndCreateTime = time; 
              break;        
          default:
              break;
          }
    },
  },
  created(){ 
    this.init(); 
    this.dataInit();
  },
  mounted(){  
  }
};
</script>

<style scoped lang="scss">
@import "../../../assets/css/style1.css"; 
.faceCapture{ 
  .search-more{
        display: block;
  }
    .face-list{
        //padding: 20px 20px 0 23px;
        // margin: 0 10px 15px 0px;
        .clearfix{
            height: auto; 
            min-height: 402px;
            max-height: 1050px;
            overflow: auto;
            .zp-face-img{  
                width: 207px; 
                border: 0px;
                .msgInfo{
                    cursor: pointer; 
                    margin-left: 3%; 
                    img{
                        width: 150px; 
                        height: 150px;
                        margin-top: -10px;
                    }
                    .zp-title{
                        height: 33px;
                        line-height: 12px;
                        color: #67cafe; 
                        font-size: 10px;
                        .el-tooltip{
                            margin-top: 3px;
                            margin-bottom: 5px; 
                        } 
                        div{
                                text-align: left;
                                width:177px; 
                                text-overflow:ellipsis;
                                overflow:hidden;
                                white-space:nowrap;  
                            }
                        /* div:hover { 
                            white-space:normal;
                            height:300px;  
                            transition-duration:0.5s;
                            transition-timing-function:ease-in;  
                        } */
                    }
                } 
                .optButton{
                    height: 30px;
                    width: 100%;
                    .el-button{
                        width: 42%;
                        height: 28px; 
                        color: #fff;
                    }
                    .editButton{ 
                        background: #66b1ff;
                        border-color: #66b1ff;
                    }
                    .deleteButton{
                        margin-left: 15px;
                        background: #F56C6C;
                        border-color: #F56C6C;
                    }
                }
            } 
        }  
              
    } 
    /* .pageItem{
        width: 19%!important;
        padding: 37px 20px 0 20px!important;
        margin: 0 5px 15px 0px!important;
        background: url(../../../assets/img/login/face-bg.png) no-repeat top center!important;
        .hit-img-real{
            width: 172px!important;
            height: 172px!important;
            img{
                width: 160px!important;
                height: 160px!important;
            }
        }
    } */
}
</style> 


