<template>
    	<div class="zzjggl">
      <div class="panel">
          <div class="base-search">
              <label></label>
              <span class="m-input">
                  <el-input class="w-200" placeholder=""></el-input>
              </span>
             
              <a href="javascript:void(0)" class="btn-search"  @click="search()"></a>    
              <a href="javascript:void(0)" class="btn-add" @click="showpop()"></a>       
              <a href="javascript:void(0)" class="btn-del fr"></a>
           
          </div>
          
          <div class="line"></div>
          <div>
              <table-grid :option="option">
                  <template slot-scope="row">
                      <el-button type="success" @click="openAddOrEditView(row.rowData)" size="mini"></el-button>
                      <el-button type="warning" @click="delEmployee(row.rowData)" size="mini"></el-button>
                  </template>
              </table-grid>
          </div>
      </div>
       <msg-dialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
        <dialog-opt :pData='msgDialog.data'></dialog-opt>
      </msg-dialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogOpt from "@/views/xinjiang/system/opt/dictionaryOpt"
export default {
    data() {
    return {
      dialogVisible: {
        msgDialog: false
      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "520px",
        data: ""
      },
      option: {
        isShowTableNav: false,
        hasCustomColum: true,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          tableTitle: [
            { prop: "account", label: "ID" },
            { prop: "enterpriseName", label: "" },
            { prop: "name", label: "" },
            { prop: "phone.number", label: "" },
            { prop: "phone.number", label: "" },
            { prop: "phone.number", label: "" },
            { prop: "phone.number", label: "" },
            { prop: "phone.number", label: "" },
            { prop: "phone.number", label: "" }
          ]
        },
        customLabel: "",
        // url: "/operation-log",
        type: "get",
        notRequestSever: false,
        param: {
          pageSize: 9,
          pageNum: 1
        }
      },
      value: ""
    };
  },
  methods: {
    showpop() {
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.dialogVisible.msgDialog = false;
    }
  },
  components: {
    "table-grid": tableGrid,
     "msg-dialog": msgDialog,
    "dialog-opt": dialogOpt
  }
}
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
}
.sub-page .left-menu li {
  text-align: left;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
</style>





