<template>
  <div class="faceOpt">
      <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="100px">
            <el-form-item label="" prop="name">
                <el-input v-model="formData.name" class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="repoId">
                <el-select v-model="formData.repoId" popper-class="m-select-con" placeholder="">
                    <el-option
                        v-for="(item, index) in constant.lib"
                        :key="index"
                        :label="item.name"
                        :value="item.id"
                    ></el-option>
                </el-select>
            </el-form-item> 
            <el-form-item label="" prop="credentialType">
                <el-select v-model="formData.credentialType" popper-class="m-select-con" placeholder="">
                    <el-option
                        v-for="(item, index) in constant.credentialType"
                        :key="index"
                        :label="item.text"
                        :value="item.value"
                    ></el-option>
                </el-select> 
            </el-form-item>
            <el-form-item label="" prop="credentialNumber">
                <el-input v-model="formData.credentialNumber"  class="j-input" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item label=""  prop="gender"> 
                <el-select v-model="formData.gender" popper-class="m-select-con" placeholder="">
                    <el-option
                        v-for="(item, index) in constant.ger"
                        :key="index"
                        :label="item.text"
                        :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>   
            <el-form-item label="" prop="nationality">
                <el-input v-model="formData.nationality"  class="j-input" placeholder=""></el-input>
            </el-form-item>  
            <el-form-item label="" prop="country">
                <el-input v-model="formData.country"  class="j-input" placeholder=""></el-input>
            </el-form-item>  
            <el-form-item label="">
                <el-input type="textarea"  class="j-input"  v-model="formData.description" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="needAuth" class='is-required'> 
                <el-upload
                    :on-change="changeUpload" 
                    action="" 
                    :show-file-list="false"
                    :auto-upload="false" 
                    accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF" 
                    >
                    <div style="width:195px;height:195px;background-color: #fff;border-radius: 8px;">
                        <img v-if="imgShow" :src="formData.imageUrl" class="avatar"  style="width:195px;height:195px;border-radius: 8px;position:relative;margin-left:0px!important" /> 
                    </div> 
                    <el-button size="small" type="primary" ></el-button>
                </el-upload> 
            </el-form-item>
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="equit"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
        </span>
  </div>
</template>

<script> 
import {imageBaseUrl}  from "@/utils/global" 

export default {
  components:{
      
  },
  props:{
      pData:{
          type: Object,
          defalut: () =>{}
      }
  },
  data(){
    return {
        imageUrl:'',
        imgShow: false,
        constant:{
            lib:[
                
            ],
            ger:[
                {text: '',value: '0'},{text: '',value: '1'}
            ],
            credentialType:[
                {text: '',value: '0'},
                {text: '',value: '1'},
                {text: '',value: '2'},
                {text: '',value: '3'},
                {text: '',value: '4'},
                {text: '',value: '5'} 
            ]
        },
        fileDatas:{
            fileName:''
        },
        formData:{
            imageUrl: ''
        },
        rule1:{
            name: [{ required: true, message: "", trigger: "change" }],
            repoId: [{ required: true, message: "", trigger: "blur" }],
            gender: [{ required: true, message: "", trigger: "blur" }],
            credentialType: [{ required: true, message: "", trigger: "change" }],
            credentialNumber: [{ required: true, message: "", trigger: "change" }],
            algVersion: [{ required: true, message: "", trigger: "change" }] 
        },
        fileReader: new FileReader(),//
    }
  },
  watch:{},
  computed:{},
  methods:{   
      editInit(){  
        this.$nextTick(() =>{   
            this.imgShow = true;  
            this.formData = this.pData.data;  
            this.$util.imagePathToBase64(this.formData.imageUrl).then(res=>{
                this.formData.imageUrl = res;  
            });
        }); 
      },
      dataInit(){
        this.$http.post(`/vcpface/facerepository/findByPage`).then(response =>{
            this.constant.lib = response.data.res.data.map(item => (({name, id}) => ({name, id}))(item));
            if(this.pData.repoId){ 
                this.formData.repoId = this.pData.repoId;
            }
        })
        if(this.pData.type == ''){ 
            this.imgShow = true;
            this.$set(this.formData, 'imageUrl', this.pData.data.imageUrl); 
        } 
      }, 
      equit(){
          this.$emit('equit');
      },
      ok(){
        this.$refs.formData.validate((valid) => { 
            if(valid){
                let type = this.pData.type; 
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `vcpface/face/save`,msg: ''}: {urlType: this.$http.put,url: `vcpface/face/update`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime; 
                let imgData = {imgBase64: (this.formData.imageUrl.split(","))[1]};
                //let imgData = this.formData.imageUrl; 
                //let imgData = {imgBase64: this.formData.imageUrl.replace('data:image/jpeg;base64,','')};  
                this.$http.post(`/vcpface/faceImageUpload/uploadImage`, imgData).then(response =>{
                    this.formData.imageUrl = response.data.res.imageUrl; 
                    this.imageUrl = response.data.res.imageUrl;
                    dataOption.urlType(dataOption.url,this.formData,{ 
                    }).then(response =>{ 
                        if(response.data.resultCd == 0){ 
                            this.$notify({
                                title: '',
                                message: dataOption.msg,
                                type: 'success'
                            }); 
                            this.$emit('equit');
                        } else {
                            this.formData.imageUrl = imageBaseUrl + this.imageUrl;
                        }
                    })   
                })  
            } 
        }) 
      },  
      changeUpload(file) {  
        if(file.size / 1024 / 1024 < 1){
            this.fileReader.readAsDataURL(file.raw);
            let base64Str = "";
            this.fileReader.onload = () => {
                this.formData.imageUrl  = this.fileReader.result;
                /* this.$set('imageUrl', this.formData, this.fileReader.result);
                console.log(this.formData.imageUrl); */
            } 
            this.imgShow = true; 
        } else {
            this.$message({
                message: '1M!!',
                type: 'warning'
            });
        }
      }
  },
  created() { 
      this.dataInit(); 
    //   if(this.pData){ 
    //     this.formData.repoId = this.pData.repoId;
    //   }
      
  },
  mounted(){},
}
</script>
<style lang="scss" >
.faceOpt{
    text-align: center;
    .el-form{ 
        margin-top: 15px;
        .j-input{
            .el-input__inner{ 
                border: 1px solid #0e94d2;
            } 
        } 
        .el-input__inner{
            width: 197px;
        }
        .el-textarea__inner{
            background: #0d254a;
            border-color: #0e94d2;
        }
    } 
    .textButton{ 
        .el-button{
            background: #1b4272;
            color:#fff;
        }
    }  
}
</style>


