import Base from '../Base';
import PermissionResource from './PermissionResource';
import PermissionModule from './PermissionModule';
class PermissionResources extends Base{
    constructor(){
        super();
        this.resources = [];
        this.menu = [];
        this.activeMenu = "";
    }

    setData(resources){
        for (let i = 0; i < resources.length; i++) {
            if (
              resources[i].permissionModule.menu &&
              (!resources[i].permissionModule.attachTo || JSON.stringify(resources[i].permissionModule.attachTo) == "{}")
            ) {
              let permissionResource = new PermissionResource();
              let permissionModule = new PermissionModule();
              permissionModule.setData(resources[i].permissionModule);
              permissionResource.setData(resources[i]);
              if (
                !this.activeMenu &&
                resources[i].permissionModule.uris &&
                resources[i].permissionModule.uris.length > 0
              ) {
                //url
                this.activeMenu = permissionModule.path;
              }
              this.menu.push(permissionModule.deepClone());
              this.resources.push(permissionResource);
            }
        }

        this.menu = this.listToTree(this.menu, {
            code: "id",
            parent: "parentId",
            children: "children"
        });

    }

    getChildrenMenu(path){
        let id = "";
        for(let i = 0 ; i < this.resources.length ; i++){
            if(this.resources[i].permissionModule.path == path){
                id = this.resources[i].permissionModule.id;
                break;
            }
        }
        if(id){
            let menu = getMenuData(this.menu , id);
            return this.deepCopyObject(menu);
        }
        return [];
    }

    getParentChildrenMenu(path){
        let parentId = "";
        for(let i = 0 ; i < this.resources.length ; i++){
            if(this.resources[i].permissionModule.path == path){
                parentId = this.resources[i].permissionModule.parentId;
                break;
            }
        }
        if(parentId){
            let menu = getMenuData(this.menu , parentId);
            return this.deepCopyObject(menu);
        }
        return [];
    }

    getPermissiomResource(path){
        for(let i = 0 ; i < this.resources.length ; i++){
            if(this.resources[i].permissionModule.path == path){
                return this.resources[i];
            }
        }
        return {};
    }
}

function getMenuData(menu,id){
    for(let i = 0 ; i < menu.length ; i++){
        if(menu[i].id == id){
            return menu[i].children;
        }
    }
    return [];
}

export default PermissionResources;


