<template>
    	<div class="zzjggl">
      <div class="panel">
          <div class="base-search">
              <label></label>
              <span class="m-input">
                  <el-input class="w-200" placeholder=""></el-input>
              </span>
              <a href="javascript:void(0)" class="btn-search"  @click="search()"></a>    
              <a href="javascript:void(0)" class="btn-add"  @click="add()" v-if="permissionResource.hasPermission('CREATION')"></a>       
              <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')"></a>
           
          </div>
          
          <div class="line"></div> 
          <div>
              <table-grid :option="option"  ref="orgList">
                  <template slot-scope="row">
                      <a href="javascript:void(0)" @click="edit(row)" title="" v-if="permissionResource.hasPermission('UPDATING')">
                        <i class="btn-icon icon-16"></i>
                      </a>
                      <a href="javascript:void(0)" @click="deleteTab(row)" title="" v-if="permissionResource.hasPermission('DELETION')">
                        <i class="btn-icon icon-17"></i>
                      </a>
                  </template>
              </table-grid>
          </div>
      </div>
       <msg-dialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
        <dialog-opt :pData='msgDialogInfo' @equit="equit"></dialog-opt>
      </msg-dialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogOpt from "@/views/xinjiang/system/opt/orgOpt";
export default {
  data() { 
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      dialogVisible: {
        msgDialog: false
      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "520px",
        data: ""
      }, 
      option: {
        isShowTableNav: false,
        hasCustomColum: true,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          tableTitle: [
            { prop: "name", label: "" },
            { prop: "area.id", label: "" }, 
            { prop: "parentName", label: "id" },
            { prop: "creator", label: "" },
            {
              prop: "createdDate",
              label: "",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.createdDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          tableBodyOverFlow: false,
          fixedHeight: 360,
          hasCustomColum: true,
          opeColumnWidth: 220
        },
        customLabel: "",
        url: "/organization",
        type: "get",
        notRequestSever: false,
        param: {
          
        }
      },
    };
  },
  methods: {
    deleteTab(row){ 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/organization/${row.rowData.id}`).then(response =>{ 
            this.$notify({
              title: '',
              message: '!!',
              type: 'success'
            });
            this.equit(); 
          })
        });
    }, 
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){ 
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.$refs.orgList.flushTableData();
      this.dialogVisible.msgDialog = false;
    }
  },
  components: {
    "table-grid": tableGrid,
    "msg-dialog": msgDialog,
    "dialog-opt": dialogOpt
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
}
.sub-page .left-menu li {
  text-align: left;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
</style>



