<template>
   <div class='comparisonHit panel'>
        <!-- <div class="base-search"> 
            <el-form ref="formData" :inline="true" class='comparisonHitForm' :model="formData"> 
                <el-form-item label=":" >
                  <el-date-picker
                   popper-class="m-select-con"
                    type="date"
                    placeholder=""
