                    :picker-options="dateTime.pickerOptionsStart"
                    :editable="false" 
                    @change="(value)=>changeTime(value,'startTime')" 
                  >
                  </el-date-picker>
                </el-form-item>
                <el-form-item label=":" >
                  <el-date-picker
                   popper-class="m-select-con"
                    type="date"
                    placeholder="" 
                    :editable="false" 
                    v-model="formData.EndCreateTime"
                    :picker-options="dateTime.pickerOptionsEnd" 
                    @change="(value)=>changeTime(value,'endTime')"
                  >
                  </el-date-picker>
                </el-form-item> 
                <el-form-item label=":" > 
                    <el-select popper-class="m-select-con" v-model="formData.areaCode" clearable>
                        <el-option 
                            :key="index" 
                            v-for="(item,index) in constant.areaCode" 
                            :label="item.name" 
                            :value="item.id"> 
                        </el-option> 
                    </el-select> 
                </el-form-item> 
                <el-form-item label=":" > 
                    <el-input v-model="formData.DeviceID"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size='medium' type="primary" icon="el-icon-search" @click="search"></el-button> 
                </el-form-item> 
            </el-form>
        </div> -->
        <div class="base-search">
            <label></label>
            <span class="m-input-date">
                <el-date-picker 
                        type="date"
                        popper-class="m-select-con"
                        placeholder=""
                        v-model="formData.BeginCreateTime"
                        :picker-options="dateTime.pickerOptionsStart"
                        :editable="false" 
                        @change="(value)=>changeTime(value,'startTime')" 
                    >
                    </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
                <el-date-picker 
                        type="date"
                        popper-class="m-select-con"
                        placeholder="" 
                        :editable="false" 
                        v-model="formData.EndCreateTime"
                        :picker-options="dateTime.pickerOptionsEnd" 
                        @change="(value)=>changeTime(value,'endTime')"
                    >
                    </el-date-picker>
            </span>
            <label></label>
            <el-select popper-class="m-select-con" v-model="formData.AreaCode" clearable>
                <el-option 
                    :key="index" 
                    v-for="(item,index) in constant.areaCode" 
                    :label="item.name" 
                    :value="item.id"> 
                </el-option> 
            </el-select> 
            <label></label>
            <el-select popper-class="m-select-con" v-model="formData.DeviceID" clearable>
                <el-option 
                    :key="index" 
                    v-for="(item,index) in constant.DeviceID" 
                    :label="item.name" 
                    :value="item.deviceID"> 
                </el-option> 
            </el-select> 
            <a href="javascript:void(0)" class="btn-search" @click="search()"></a>
        </div>
         <div class="tjbb-item" v-loading="loading">
            <div class="sbtj-tjt" >
                <div class="hit-list"><!--s-->
                    <ul class="clearfix">
                        <li v-for="(item, key) in messeageList" :key="key" >
                            <a class='msgInfo' @click="showpop(item)">  
                                <div class="hit-img-real"><img v-if="imgStatus" :src="item.FaceID" >
                                    <h1></h1>
                                </div>
                                <div class="hit-precent">
                                    <el-progress type="circle" :percentage="item.Similarity" :stroke-width='percentage.paddingWidth' :width='percentage.width' :color='percentage.colorPrecent'></el-progress>
                                </div>
                                <div class="hit-img-database"><img v-if="imgStatus" :src="item.FaceHitID" >
                                    <h1></h1>
                                </div> 
                                <div class="hit-title"> 
                                    <el-tooltip :content="item.DeviceID" placement="top" popper-class="atooltip">
                                        <div>{{item.DeviceID}}</div>
                                    </el-tooltip> 
                                    <el-tooltip :content="item.AreaCode" placement="top" popper-class="atooltip">
                                        <div>{{item.AreaCode}}</div>
                                    </el-tooltip>
                                    <el-tooltip :content="''+item.CreateTime" placement="top" popper-class="atooltip">
                                        <div>{{''+item.CreateTime}}</div>
                                    </el-tooltip>
                                </div>
                            </a>
                        </li> 
                    </ul>
                </div>
            </div>
        </div> 
        <div>
            <pagination :pData='pageOption' @init='init'/>
        </div>
        <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
            <warnMsg :pData='warnInfo' />
        </msgDialog>
   </div>
</template>
<script>
import pagination from '@/views/xinjiang/common/components/pagination'
import msgDialog from '@/views/xinjiang/common/components/dialog/msgDialog'
import warnMsg from '@/views/xinjiang/comparison/warnMsg'
import {baseUrl} from "@/utils/global"
import {imageBaseUrl}  from "@/utils/global"
import {dateArraySort} from '@/utils/datetime'
export default {
  data() {
    return { 
        baseUrl:baseUrl,
        imageBaseUrl:imageBaseUrl,
        loading: false,
        imgStatus: false,
        constant:{
            areaCode:[],
            DeviceID:[]
        },
        formData:{
            BeginCreateTime: '',
            EndCreateTime: '',
            areaCode: ''
        },
        dateTime: {
          pickerOptionsStart: {
            disabledDate: time => {
              if (this.formData.EndCreateTime) { 
                return time.getTime() > this.formData.EndCreateTime;
              }  
            }
          },
          pickerOptionsEnd: {
            disabledDate: time => {
              if (this.formData.BeginCreateTime) {  
                return time.getTime() < this.formData.BeginCreateTime ;
              }  
            }
          }
        }, 
        warnInfo:{
            capture: require('../../../assets/img/login/hit-user1.jpg'),
            library: require('../../../assets/img/login/hit-user1-d.jpg'),
            percent: '75%',
            data:{
                name: ''
            }
        },
        dialogVisible:{
            msgDialog: false
        },
        msgDialog:{
            title: '',
            dialogVisible: true,
            width: '600px' 
        },
        percentage:{ 
            paddingWidth: 6,
            width: 60,
            colorPrecent: '#20a0ff'
        },
        pageOption:{
            currentPageNum: 1,  
            pageSize: 10, 
            firstPage: 1,
            lastPage: 7, 
            total: 70, 
            
            url: 'vcpinterface/ConvCompHistory/FindPage' 
        },
        messeageList:[
            {
                FaceID: '',
                FaceHitID: '',
                CreateTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                Similarity: 75.2
            },{
                FaceID: '',
                FaceHitID: '',
                CreateTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                Similarity: 75.2
            },{
                FaceID: '',
                FaceHitID: '',
                CreateTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                Similarity: 75.2
            },{
                FaceID: '',
                FaceHitID: '',
                CreateTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                Similarity: 75.2
            },{
                FaceID: '',
                FaceHitID: '',
                CreateTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                Similarity: 75.2
            },{
                FaceID: '',
                FaceHitID: '',
                CreateTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                Similarity: 75.2
            } 
        ]
    };
  },
  components: { pagination, msgDialog, warnMsg },
  methods:{ 
      // 
    sortDataArray(array){
        array.sort(function(a, b) {
             return b.CreateTimeNew> a.CreateTimeNew ? 1 : -1;
        })
    }, 
      init(formData) {  
          this.loading = true;
          this.imgStatus =false;
          this.$http.post(`vcpinterface/ConvCompHistory/FindPage`,  Object.assign( 
          {
            PageSize: 12
          },
          formData
        ) ,{headers:{"Content-Type": "application/json"}}).then(response =>{
              this.pageOption = {
                currentPageNum: 1,  
                pageSize: 12, 
                firstPage: 1,
                lastPage: (response.data.Res.TotalRow % 12 == 0) ? response.data.Res.TotalRow / 12 : parseInt(response.data.Res.TotalRow / 12) + 1 , 
                total: response.data.Res.TotalRow, 
                url: 'vcpinterface/ConvCompHistory/FindPage' 
              } 
              console.log(this.pageOption, ''); 
              let Host = imageBaseUrl;
              let data = response.data.Res.Data; 
              data.forEach(item =>{
                  item.FaceID = Host + item.FaceID + '.jpg'; 
                  item.FaceHitID = Host + item.FaceHitID + '.jpg';  
                  item.Similarity = parseFloat(Math.round(item.Similarity * 100) / 100); 
                  this.$http.get(`/regisger-object/get-area-name/${item.AreaCode}`).then(response =>{
                      item.AreaCode = '' +  response.data;
                  });  
                  this.$http.get(`/regisger-object/device-id/${item.DeviceID}`).then(response =>{
                      item.DeviceID = '' + response.data;
                  }); 
              })
              this.messeageList = dateArraySort(data,'CreateTime'); 
              console.log(this.messeageList); 
              this.loading = false;
              this.imgStatus = true;
          })
      },
      dataInit(){ 
        this.$http.get(`/regisger-object/area-tree`).then(response =>{
            this.constant.areaCode = response.data.map(item => (({name, id}) => ({name, id}))(item)); 
        })
        this.$http.get('/regisger-object/device-name').then(response =>{
            this.constant.DeviceID = response.data.map(item => (({name, deviceID}) => ({name, deviceID}))(item)); 
        })
      },
      showpop(data){
          this.warnInfo = data; 
          this.dialogVisible.msgDialog = true;
      },
      equit(){
          this.dialogVisible.msgDialog = false;
      },
      search(){
          let data = JSON.parse(JSON.stringify(this.formData));  
          this.init(data);
      },
      //
      changeTime(value, status) {  
        let time = new Date(value).getTime();  
        switch (status) {
            case 'startTime':
                this.formData.BeginCreateTime = time; 
                break;
            case 'endTime':
                this.formData.EndCreateTime = time; 
                break;        
            default:
                break;
            }
      },
  },
  created(){ 
    this.init();
    this.dataInit(); 
  },
  mounted(){  
  }
};
</script>

<style scoped lang="scss">
@import "../../../assets/css/style1.css";
@import 'comparisonHit.scss'; 

</style>


