<template>
    <div class="zzjggl">
      <div class="panel">
            <div class="base-search">
              <label></label>
              <span class="m-input">
                  <el-input class="w-200" v-model="searchForm.algName" placeholder=""></el-input>
              </span>
              <label></label>
              <span class="m-input">
                  <el-input class="w-200" v-model="searchForm.algCode" placeholder=""></el-input>
              </span>
              <a href="javascript:void(0)" class="btn-search" @click="search"></a>
              <a href="javascript:void(0)" class="btn-add" @click="add" v-if="permissionResource.hasPermission('CREATION')"></a>
              <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')"></a>
            </div>
            <div class="line"></div>           
            <div class="base-table">
                <table-grid :option="option" ref="analysisList">
                    <template slot-scope="row">
                      <!-- <a href="javascript:void(0)"  title="">
                        <i class="btn-icon icon-12"></i>
                      </a>
                      <a href="javascript:void(0)" title="">
                        <i class="btn-icon icon-13"></i>
                      </a> -->
                      <a href="javascript:void(0)" title="" @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')" >
                        <i class="btn-icon icon-16"></i>
                      </a>
                      <a href="javascript:void(0)" title="" @click="deleteTab(row)" v-if="permissionResource.hasPermission('DELETION')">
                        <i class="btn-icon icon-17"></i>
                      </a>
                      <!-- <a href="javascript:void(0)" title="">
                        <i class="btn-icon icon-14"></i>
                      </a> -->
                    </template>
                </table-grid>
            </div>
        </div>
        <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
            <dialogEdit :pData='msgDialogInfo' @equit='equit'></dialogEdit>
        </msgDialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogEdit from "@/views/xinjiang/analysis/opt/analyAdd"; 

export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      searchForm:{
        algName: '',
        algCode: ''
      },
      dialogVisible:{
        msgDialog: false
      },
      msgDialogInfo:{

      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "620px",
        data: ""
      },
      option: {
        isShowTableNav: false,
        tableOption: {
          singleSelect: true,
          isShowSelect: true, 
          hasCustomColum: true,
          tableTitle: [
            { prop: "algName", label: "" },
            { prop: "algCode", label: "" },
            { prop: "algType", label: "" },
            { prop: "status", label: "" },
            { prop: "creator", label: "" },
            {
              prop: "createTime",
              label: "",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.modifiedDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          opeColumnWidth: 220,
          customRes: true
        },
        customLabel: "",
        url: "/vcpalgaccess/alg/findByPage",
        type: "post",
        notRequestSever: false,
        param: {
          /*pageSize: 10,
          pageNum: 1,
          para: {
            algName: "",
            algCode: "FACE"
          }*/
        }
      }
    };
  },
  methods: {
    deleteTab(row){ 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/vcpalgaccess/alg/delete`,{id: row.rowData.id}).then(response =>{
            if(response.data.resultCd == 0){
              this.$notify({
                title: '',
                message: '!!',
                type: 'success'
              });
              this.equit();
            } 
          })
        });
    }, 
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.$refs.analysisList.flushTableData();
      this.dialogVisible.msgDialog = false;
    },
    search(){
      //let param = {}; 
      if (this.searchForm) { 
        this.option.param = Object.assign({pageSize: 10, pageNum: 1,},{para: this.searchForm});
      }  
      this.$refs.analysisList.searchTableData(this.option.param);
    }
  },
  components: {
    "table-grid": tableGrid,
    dialogEdit,
    msgDialog
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page .t-head,.base-search{
  text-align:left;
}
</style>




