<template>
  <div>
    <div style="width:100%">
    <ul>
      <li>
        <span style="width: 20%;display: inline-block;text-align:left;color:rgba(229, 97, 84)"></span>
        <span style="width: 38%;display: inline-block;text-align:center;color:rgba(229, 97, 84)"></span>
        <span style="width: 38%;display: inline-block;text-align:center;color:rgba(229, 97, 84)"></span>
      </li>
    </ul>
    </div>
    <div class="echartsRight">
      <vueSeamless :data="APIIpList" :class-option="option">
        <ul >
          <li v-for="(item, index) in APIIpList" :key="index">
            <span style="width:20%;display: inline-block;text-align:left" :title="item.name" v-text="item.name"></span>
            <span style="width: 38%;display: inline-block;text-align:center" :title="item.alarmType.text" v-text="item.alarmType.text"></span>
            <span style="width:38%;display: inline-block;text-align:center" :title='$util.formatDateByArg(item.createdDate, "yyyy-MM-dd")' >{{$util.formatDateByArg(item.createdDate, "yyyy-MM-dd")}}</span>
          </li>
        </ul>
      </vueSeamless>
    </div>
  </div>
</template>

<script>
import vueSeamless from "vue-seamless-scroll";
export default {
  components: {
    vueSeamless
  },
  props: {
    pDatas: {
      type: String,
      default: () => {}
    }
  },
  name: "wrap",
  data() {
    return {
      datas: [],
      option: {
        step: 0.5,
        limitMoveNum: "",
        openTouch: false,
        waitTime: 0,
        direction: 1,
        singleHeight: 0
      },
      APIIpList: [
      ]
    };
  },
  computed: {},
  watch: {
    pDatas(val) {
      this.getData(val)
    },
  },
  created() {
    this.getData()
  },
  methods: {
    getData(val){
      if(val){
        this.$http.get("/big-screen/operation-log/"+val)
         .then(response => {
           this.APIIpList = response.data.data;
           this.option.limitMoveNum = this.APIIpList.length;
         });
      }else{
        this.$http.get("/big-screen/operation-log")
         .then(response => {
           this.APIIpList = response.data.data;
           this.option.limitMoveNum = this.APIIpList.length;
         });
      }
       
    }
  }
};
</script>
<style scoped>
li {
  list-style-type: none;
  font-size: 14px;
  color:white;
}
p {
  margin: 0;
  height: 30px;
  width: 100%;
}
.content p {
  position: absolute;
}
.echartsRight {
  overflow: hidden;
  height: 90px;
  width: 100%;
  color: #666666;
  line-height: 20px;
}
.echartsRight span {
  font-size: 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow:hidden;
}
.echartsRight .rowup {
  -webkit-animation: 10s rowup linear infinite normal;
  animation: 10s rowup linear infinite normal;
  position: relative;
}
</style>



