<template>
  <div ref="line" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
export default {
  name: "guTags",
  data() {
    // 
    let echarts = require("echarts/lib/echarts");
    // 
    require("echarts/lib/chart/bar");
    // title
    require("echarts/lib/component/tooltip");
    require("echarts/lib/component/title");
    return {
      data: [],
      date:[]
    };
  },
  mounted() {
    this.getData();
  },
  created() {},
  methods: {
    getData() {
      this.$http.get("/big-screen/count-alarm").then(response => {
        var arr1 = this.$util.resolveObject(response.data.disConnect);
        var arr2 =  this.$util.resolveObject(response.data.weakPassword);
        var arr3 =  this.$util.resolveObject(response.data.hardwarefailure);
        var arr4 =  this.$util.resolveObject(response.data.clockAnomaly);
        this.data.push(arr1.values);
        this.data.push(arr2.values);
        this.data.push(arr3.values);
        this.data.push(arr4.values);
        this.date.push(arr1.keys)
        this.drawLine();
        console.log(this.data);
      });
    },
    drawLine() {
      // domecharts
      let line = this.$echarts.init(this.$refs.line, "shine");
      // 
      var option = {
        color: ["#ffdb5c", "#67e0e3", "#e062ae", "#ff9f7f"],
        title: {
          text: "",
          textStyle: {
            fontSize: 12,
            padding: "50px",
            color: "#ffffff"
          }
        },
        legend: {
          orient: "horizontal",
          left: "center",
          type: "scroll",
          data: [
            "",
            "",
            "",
            ""
          ],
          top: "top",
          itemWidth: 5,
          itemHeight: 4,
          formatter: "{name}",
          textStyle: {
            color: "#FFFFFF"
          }
        },
        grid: {
          top: "15%",
          bottom: "15%"
        },
        tooltip: {},
        xAxis: {
          name: "",
          data: this.date[0],
          axisLine: {
            lineStyle: {
              color: "#ffffff"
            }
          },
          axisLabel: {
            show: true,

            textStyle: {
              color: "#ffffff"
            }
          }
        },
        yAxis: {
          splitArea: { show: false },
          splitLine: {
            //
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#ffffff"
            }
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff"
            }
          }
        },

        series: [
          {
            data: this.data[0],
            name: "",
            type: "line"
          },
          {
            data: this.data[1],
            name: "",
            type: "line"
          },
          {
            data: this.data[2],
            name: "",
            type: "line"
          },
          {
            data: this.data[3],
            name: "",
            type: "line"
          }
        ]
      };
      line.setOption(option, true);
    }
  }
};
</script>
<style>
</style>
<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
  width: 99%;
}
</style>



