<template>
    <div class='ivuAdd'> 
        <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="100px">
            <el-form-item label="IVU" prop="ivuName">
                <el-input v-model="formData.ivuName" class="j-input" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item label="" prop="algCode">
                <!-- <el-input v-model="formData.algCode "  class="j-input" placeholder=""></el-input> -->
                <el-select v-model="formData.algCode" popper-class="m-select-con" placeholder="">
                    <el-option
                    v-for="item in deviceTypeData"
                    :key="item.value"
                    :label="item.text"
                    :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item> 
            <el-form-item label="" prop="mfCode"> 
                <el-select v-model="formData.mfCode" popper-class="m-select-con" placeholder="">
                    <el-option
                    v-for="item in devicefactory"
                    :key="item.value"
                    :label="item.text"
                    :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item> 
            <el-form-item label=""  prop="serverId"> 
                <el-input v-model="formData.serverId"  class="j-input" placeholder=""></el-input> 
            </el-form-item> 
            <el-form-item label="" prop="algVersion">
                <el-input v-model="formData.algVersion"  class="j-input" placeholder=""></el-input>
            </el-form-item>   
            <el-form-item label=""  prop="validDate">  
                <el-date-picker 
                    type="date"
                    popper-class="m-select-con"
                    placeholder=""
                    v-model="formData.validDate"
                    :picker-options="dateTime.pickerOptionsStart"
                    :editable="false"  
                    @change="(value)=>changeTime(value,'startTime')" 
                >
                </el-date-picker>
            </el-form-item> 
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="equit"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
        </span>
	    <!-- <div class="btn-quit pt0"><a href="javascript:void(0)"> </a></div> -->
    </div>
</template>
<script>
import { format } from "@/utils/datetime"

export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data(){
    return {
        formData:{
            ivuName: '',
            mfCode: '',
            algVersion: '',
            serverId: '', 
            algCode : ''
        },
        devicefactory:[],
        deviceTypeData:[{
            text: '',
            value: 'FACE_RECOGNITION' 
        },{
            text: '',
            value: 'VEHICLE_RECOGNITION'
        }],
        //
        rule1:{
          ivuName: [{ required: true, message: "IVU", trigger: "change" }],
          mfCode: [{ required: true, message: "", trigger: "change" }],
          algCode: [{ required: true, message: "", trigger: "change" }],
          algVersion: [{ required: true, message: "", trigger: "change" }],
          serverId: [{ required: true, message: "", trigger: "change" }],
          validDate: [{ required: true, message: "", trigger: "change" }] 
        },
        dateTime: {
          pickerOptionsStart: {
            disabledDate: time => {
              if (this.formData.EndCreateTime) { 
                return time.getTime() > this.formData.EndCreateTime;
              }  
            }
          } 
        }, 
    }
  },
  watch:{},
  computed:{},
  methods:{ 
    //
    changeTime(value, status) {  
        this.formData.validDate = format(value).replace(' 00:00:00',''); 
        /* let time = new Date(value).getTime();  
        switch (status) {
            case 'startTime':
                this.formData.validDate = format(time); 
                break;       
            default:
                break;
        } */
    },
    ok(){ 
        this.$refs.formData.validate((valid) => { 
            if(valid){
                let type = this.pData.type; 
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `/vcpalgaccess/ivu/save`,msg: ''}: {urlType: this.$http.put,url: `/vcpalgaccess/ivu/update`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime;
                dataOption.urlType(dataOption.url,this.formData).then(response =>{ 
                    if(response.data.resultCd == 0){ 
                        this.$notify({
                            title: '',
                            message: dataOption.msg,
                            type: 'success'
                        }); 
                        this.$emit('equit');
                    }
                })
            } 
        })
    },
    equit(){
        this.$emit('equit');
    },
    dataInit(){
      this.$http.get(`/regisger-object/constant`).then(response =>{
        this.devicefactory = (response.data.filter(item => item.id == 'Brand'))[0].options.map(item => (({text, value}) => ({text, value}))(item));  
      })
    } 
  },
  mounted(){ 
    this.dataInit();
  },
  created(){ 
    let type = this.pData.type; 
    switch (type) {
        case '':
            this.formData = {};
            break;
        case '':
            this.formData = this.pData.data;
            break;
        default:
            break;
    }  
  }
};
</script>
<style scoped lang="scss">
//@import "../../../../assets/css/style1.css";
/* .dialog{
    height: 366px;
} */
.ivuAdd{
    text-align: center;
    .el-form{ 
        margin-top: 15px;
        .j-input{
            .el-input__inner{ 
                border: 1px solid #0e94d2;
            } 
        } 
        .el-date-editor{
            width: 182px;
        }
    } 
    .textButton{ 
        .el-button{
            background: #1b4272;
            color:#fff;
        }
    }  
}

</style>




