import util from "./util.js"
let dealData = {};
/**
 * 
 */
dealData.responDataToFormData = function (responData, formData,value="value") {
    if (!responData) {
        return;
    } else {
        for (let i = 0; i < formData.length; i++) {
            if (formData[i].key) {
                formData[i][value] = util.readObjectKey(responData, formData[i].key);
            } else {
                continue;
            }
        }
    }
}

/**
 * 
 */
dealData.formDataToRequestData = function (formData) {
    let requestData = {};
    for (let i = 0; i < formData.length; i++) {
        let keys = formData[i].key.split(".");
        let temp = requestData;
        for (let j = 0; j < keys.length; j++) {
            if (j == keys.length - 1) {
                temp[keys[j]] = formData[i].value;
            } else {
                temp[keys[j]] = {};
                temp = temp[keys[j]];
            }
        }
    }
    return requestData;
}

export default dealData;


