import Base from '../Base'

class PermissionUser extends Base{
    constructor(){
        super();
        Object.assign(this,{
            id : '',
            account : '',
            enabled : undefined,
            name : '',
            parentId : '',
            password : '',
            permissionGroups : [],
            permissionRoles : [],
            remark: '',
            root : '',
            text : '',
        })
    }
}

export default PermissionUser;


