<template>
    <div class="home-content">
        <!-- s -->
        <div class="right">
            <!-- s -->
            <div class="box box-09">
              <box-right-one :list="boxRightOne"></box-right-one>
              <div class="box-btn">
                <span class="btn" @click="resetDevice()" v-if="permissionResource.hasPermission('UPDATING')"></span>
                <span class="btn" v-if="permissionResource.hasPermission('UPDATING')"></span>
              </div>
              <div class="box-btn">
                <span class="btn" @click="changePwd()" v-if="permissionResource.hasPermission('UPDATING')"></span>
                <span class="btn" v-if="permissionResource.hasPermission('UPDATING')"></span>
              </div>
            </div>
            <!-- e -->
        </div>
        <!-- e -->
        <!-- s -->
        <div class="left">
            <!-- s -->
            <div class="box box-07">
               <div class="box-title"></div>
               <div>
                 <div>
                    <p class="box-white box-progress"></p>
                    <el-progress  :percentage="parseFloat(boxLeftData.percentage)" :color="'#4ED0F4'" :stroke-width="15"></el-progress>
                 </div>
                 <div class="content">
                   <box-left-one :list="boxLeftOne"></box-left-one>
                 </div>
               </div>
            </div>
            <!-- e -->
            <!-- s -->
            <div class="box box-08">
               <box-right-one :list="boxLeftTwo"></box-right-one>
               <div style="height:230px;width:100%">
                  <box-left-two 
                  :list="boxLeftThree.lists" 
                  :legendData="boxLeftThree.legendData"
                  :colorList="commonColor" 
                  v-if="boxLeftThree.show"></box-left-two>
               </div>
            </div>
            <!-- e -->
        </div>
        <!-- e -->
        <!-- s -->
        <div class="center">
            <div class="map" style="height:538px;width:100%">
              <fly-line-map deviceUrl="/asset-distribution/device-distribution/" :hasline="false" @clickMap="clickMap" @clickDevice="clickDevice"></fly-line-map>  
            </div><!--527px538px-->
        </div>
        <msg-dialog v-if="msgDialog.changPwd.dialogVisible.msgDialog" :pDialog='msgDialog.changPwd.msgDialog' @equit="equit">
        <dialog-opt-pwd :pData='msgDialog.changPwd.msgDialog.data' @result="dialogOpt"></dialog-opt-pwd>
      </msg-dialog>
    </div>
</template>
<script>
import boxRightOne from "@/views/xinjiang/assetDistribution/components/boxOne";
import boxLeftOne from "@/views/xinjiang/assetDistribution/components/boxTwo";
import boxLeftTwo from "@/views/xinjiang/assetDistribution/components/pie";
import flyLineMap from "@/views/xinjiang/common/components/flyLineMap";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogOptPwd from "@/views/xinjiang/assetDistribution/opt/changePwd";
export default {
  name: "assetDistribution",
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      commonColor: ["#f9f139", "#50cb74", "#8c73fe", "#f48421", "#d1451a"],
      constant: [],
      boxLeftData: {
        percentage: 0
      },
      boxLeftOne: {
        lists: [
          {
            label: "",
            key: "onlineNum",
            image: require("../../../assets/img/login/icon_sbzx.png"),
            value: 0
          },
          {
            label: "",
            image: require("../../../assets/img/login/icon_sblx.png"),
            key: "offlineNum",
            value: 0
          }
        ]
      },
      boxLeftTwo: {
        title: "",
        lists: [
          {
            label: "",
            value: 0,
            class: "box-yellow",
            key: "type.intelligentIintegrationMachine",
            image: require("../../../assets/img/login/icon_04.png")
          },
          {
            label: "",
            value: 0,
            class: "box-yellow",
            key: "type.smartCamera",
            image: require("../../../assets/img/login/icon_05.png")
          },
          {
            label: "",
            value: 0,
            class: "box-yellow",
            key: "type.vehicleFrontEndCamera",
            image: require("../../../assets/img/login/icon_06.png")
          }
        ]
      },
      boxLeftThree: {
        show: false,
        legendData: [],
        lists: []
      },
      boxRightOne: {
        title: "",
        lists: [
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "deviceID",
            image: require("../../../assets/img/login/icon_20.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "name",
            image: require("../../../assets/img/login/icon_21.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "organizationCode",
            image: require("../../../assets/img/login/icon_22.png")
          },
          {
            label: "MAC",
            value: 0,
            class: "box-white",
            key: "mac",
            image: require("../../../assets/img/login/icon_23.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "location",
            image: require("../../../assets/img/login/icon_24.png")
          },
          {
            label: "ID",
            value: 0,
            class: "box-white",
            key: "ip",
            image: require("../../../assets/img/login/icon_25.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "username",
            image: require("../../../assets/img/login/icon_26.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "password",
            image: require("../../../assets/img/login/icon_27.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "brand.text",
            image: require("../../../assets/img/login/icon_28.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "phone",
            image: require("../../../assets/img/login/icon_29.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "locationInfo",
            image: require("../../../assets/img/login/icon_30.png")
          },
          {
            label: "",
            value: 0,
            class: "box-white",
            key: "latitudeorlongitude",
            image: require("../../../assets/img/login/icon_31.png")
          }
        ],
        deviceId: "",
        deviceCode: ""
      },
      msgDialog: {
        changPwd: {
          dialogVisible: {
            msgDialog: false
          },
          msgDialog: {
            title: "",
            dialogVisible: true,
            width: "520px",
            data: {}
          }
        }
      },
      areaId: vm.$areaId
    };
  },
  created() {
    this.getConstant();
  },
  mounted() {
    this.getRegister(this.areaId);
    this.getDeviceAccessInfo(this.areaId);
  },
  methods: {
    /**
     * 
     * areaId  id
     */
    clickMap(areaId) {
      this.areaId = areaId;
      this.getRegister(areaId);
      this.getDeviceAccessInfo(areaId);
    },
    clickDevice(id) {
      this.getDeviceInfo(id);
      this.boxRightOne.deviceId = id;
    },
    getConstant() {
      //
      this.$http.get(`/asset-distribution/register-object/constant`).then(response => {
        this.constant = response.data;
      });
    },
    getRegister(_data) {
      /**
       * 
       */
      this.$http
        .get(`/asset-distribution/device-register-info/${_data}`)
        .then(response => {
          let data = {
            onlineNum: response.data.onlineNum,
            offlineNum: response.data.offlineNum
          };
          let rate = data.onlineNum / this.getTotalData(data);
          try {
            this.boxLeftData.percentage = Number.isNaN(rate)
              ? 0
              : (data.onlineNum / this.getTotalData(data) * 100).toFixed(2);
          } catch (e) {
            this.boxLeftData.percentage = 0;
          }
          /*  this.boxLeftData.percentage = Number.isNaN(rate)
            ? 0
            : (data.onlineNum / this.getTotalData(data) * 100 ).toFixed(1); */
          this.$dealData.responDataToFormData(
            response.data,
            this.boxLeftOne.lists
          );
        });
    },
    getDeviceAccessInfo(_data) {
      this.boxLeftThree.show = false;
      this.$http
        .get(`/asset-distribution/device-access-info/${_data}`)
        .then(response => {
          this.getEchartPieData(response.data.brand).then(e => {
            this.boxLeftThree.lists = e.arr;
            this.boxLeftThree.legendData = e.name;
            this.boxLeftThree.show = true;
          });
          this.$dealData.responDataToFormData(
            response.data,
            this.boxLeftTwo.lists
          );
        });
    },
    getEchartPieData(_data) {
      /**echarts
       * 
       * {
       *  yitu:1,
       * dahua:2
       * }
       * :
       * 1=>arr:[ series
       * {
       * name:"",
       * value:1
       * }
       * ]
       * 2=>name:["",""]legend
       * */

      let arr = [],
        arrName = [];
      return new Promise(resolve => {
        let newArr = this.constant.find(res => {
          //
          return res.id == "Brand";
        });
        for (let key in _data) {
          newArr.options.forEach(element => {
            if (key == element.value.toLowerCase()) {
              arr.push({
                name: element.text,
                value: _data[key]
              });
              arrName.push(element.text);
            }
          });
        }
        resolve({ arr: arr, name: arrName });
      });
    },
    getDeviceInfo(_data) {
      this.$http.get(`/regisger-object/${_data}`).then(response => {
        response.data.password = response.data.password.replace(/./g, "*");
        this.$dealData.responDataToFormData(
          response.data,
          this.boxRightOne.lists
        );
        this.boxRightOne.deviceCode = response.data.deviceID;
        this.boxRightOne.lists[
          this.boxRightOne.lists.length - 1
        ].value = `${response.data.coordinateX.toFixed(
          2
        )} ${response.data.coordinateY.toFixed(2)}`;
      });
    },
    getTotalData(_data) {
      /**
       * 
       */
      let count = 0;
      for (let key in _data) {
        count += _data[key];
      }
      return count;
    },
    resetDevice() {
      console.log("de", this.boxRightOne.deviceCode);
      if (this.boxRightOne.deviceId == "") return;
      this.$http
        .post(` /regisger-object/device-reboot/${this.boxRightOne.deviceId}`, {
          deviceID: this.boxRightOne.deviceCode
        })
        .then(response => {});
    },
    changePwd() {
      this.msgDialog.changPwd.msgDialog.data.methods = "post";
      this.msgDialog.changPwd.msgDialog.data.url = "/vcpface/facerepository/savedere";
      this.msgDialog.changPwd.dialogVisible.msgDialog = true;
    },
    equit() {
      this.msgDialog.changPwd.dialogVisible.msgDialog = false;
    },
    dialogOpt(_data) {
      if (_data) {
        this.equit();
      }
    }
  },
  components: {
    "box-right-one": boxRightOne,
    "box-left-one": boxLeftOne,
    "box-left-two": boxLeftTwo,
    "fly-line-map": flyLineMap,
    "msg-dialog": msgDialog,
    "dialog-opt-pwd": dialogOptPwd
  }
};
</script>

<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.home .box,
.home .right,
.home .left {
  width: 304px;
}
.home .home-content {
  height: 100%;
}
.home {
  .right,
  .left {
    height: 100%;
  }
  .center {
    margin-left: 304px;
    margin-right: 304px;
    width: calc(100% - 608px);
    .map {
      width: 527px;
      margin: 0 auto;
      position: relative;
      left: 0;
    }
  }
}
.box-title {
  text-align: left;
}
.home .box-07 {
  height: 229px;
  background: url(../../../assets/img/login/bg_box_07.png) 0 0 no-repeat;
  overflow: visible;
  .box-progress {
    text-align: left;
    margin-left: 20px;
    margin-top: 20px;
  }
  .el-progress {
    position: relative;
    margin-top: 5px;
    font-size: 16px;
  }

  .content {
    margin-top: 20px;
  }
}
.home .box-08 {
  height: 424px;
  background: url(../../../assets/img/login/bg_box_08.png) 0 0 no-repeat;
  overflow: visible;
  position: relative;
  .box-img {
    width: 38px;
    height: 38px;
  }
}
.home .box-09 {
  height: 670px;
  background: url(../../../assets/img/login/bg_box_09.png) 0 0 no-repeat;
  overflow: visible;
  .box-btn {
    margin-top: 20px;
    .btn {
      display: inline-block;
      width: 77px;
      height: 33px;
      line-height: 33px;
      margin-right: 20px;
      background: url(../../../assets/img/login/btn_box_09.png) 0 0 no-repeat;
      color: #fff;
      cursor: pointer;
    }
  }
}
</style>
<style lang="scss">
.el-progress__text {
  position: absolute;
  margin-top: -25px;
  left: 50%;
  color: #fff;
}
.home .box-08 {
  .box-yellow {
    float: right;
  }
}
.box-yellow {
  color: #fcc328;
  font-size: 21px;
}
.box-white {
  color: #fff;
}
</style>





