import Base from '../Base'
import mapping from './mapping'
class PermissiomModule extends Base{
    constructor(){
        super();
        let vm = this;
        Object.assign(this,{
            attachTo : {},
            enabled : undefined,
            identification : '',
            menu : '',
            name : '',
            ownerType : '',
            parentId : '',
            rank : '',
            remark : '',
            root : '',
            uris : [],
            path : '',
        });


        let identification = "";
        Object.defineProperty(this,"identification",{
            get: function () {
                return identification;//returnname
            },
            set:function(value){
                if(mapping[value]){
                    this.path = mapping[value].url;
                }
                identification = value;
            }
        });

    }


}

export default PermissiomModule;


