<template>
    <div class="deviceReport">
      <div class="clearfix">
        <div class="tjbb-item one" >
            <div class="t-head">
                <span class="name"></span>
                <span class="time">
                    <a v-for="(item,index) in areaOption.searchList" 
                    :key="index" href="javascript:void(0)" 
                    :class="item.active?'selected':''" @click="searchDevice(index,item.value)" v-if="permissionResource.hasPermission('RETRIEVE')">{{item.text}}</a>
                </span>
                <a  class="btn-down-pic" ref="downOne" @click="saveImage('one')" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
            </div>
            <div class="tjt" style="height:265px; width:100%;">
                <echarts-one :xAxisList="areaOption.xAxisList"  :list="areaOption.list" ref="lineOne" v-if="areaOption.lineShow"> </echarts-one>
            </div>
        </div>
        <div class="tjbb-item three">
            <div class="t-head">
                <span class="name"></span>
                <span class="time">
                    <a v-for="(item,index) in sceneOption.searchList" 
                    :key="index" href="javascript:void(0)" 
                    :class="item.active?'selected':''" @click="searchScene(index,item.value)" v-if="permissionResource.hasPermission('RETRIEVE')">{{item.text}}</a>
                </span>
                <a  class="btn-down-pic" ref="downThree" @click="saveImage('Three')" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
            </div>
            <div class="tjt" style="height:265px; width:100%">
                <echarts-one :xAxisList="sceneOption.xAxisList"  
                :list="sceneOption.list"  v-if="sceneOption.lineShow" ref="lineThree"> </echarts-one>
            </div>
        </div>
      </div>
      <div class="tjbb-item mt12 two clearfix">
          <div class="t-head">
              <span class="name red-name"></span>
              <span class="time">
                  <a v-for="(item,index) in alarmOption.searchList" 
                  :key="index" href="javascript:void(0)" 
                  :class="item.active?'selected':''" @click="searchAlarm(index,item.value)" v-if="permissionResource.hasPermission('RETRIEVE')">{{item.text}}</a>
              </span>
              <a ref="downTwo" @click="saveImage('two')" class="btn-down-pic" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
          </div>
          <div class="tjt" style="height:265px; width:100%">
              <echarts-one :xAxisList="alarmOption.xAxisList"  :list="alarmOption.list"  ref="lineTwo" v-if="alarmOption.lineShow"></echarts-one>
          </div>
      </div>
    </div>
</template>
<script>
import echartsOne from "@/views/xinjiang/common/components/lineEchart/line";
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      sceneOption: {
        lineShow: false,
        xAxisList: [],
        list: [],
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        dataSearch: {}
      },
      areaOption: {
        lineShow: false,
        xAxisList: [],
        list: [],
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        dataSearch: {}
      },
      alarmOption: {
        lineShow: false,
        xAxisList: [],
        list: [],
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        dataSearch: {}
      }
    };
  },
  mounted() {
    this.getAlarmData(
      `operation-log/alarm-data/week`,
      this.areaOption.dataSearch
    );
    this.getDeviceData(
      `regisger-object/assert-data/week`,
      this.alarmOption.dataSearch
    );
    this.getScene(
      `regisger-object/scene-data/week`,
      this.sceneOption.dataSearch
    );
  },
  methods: {
    saveImage(_data) {
      if (_data == "one") {
        this.$refs.lineOne.saveImg().then(e => {
          this.$refs.downOne.href = e;
          this.$refs.downOne.download = ".png";
        });
      } else if (_data === "two") {
        this.$refs.lineTwo.saveImg().then(e => {
          this.$refs.downTwo.href = e;
          this.$refs.downTwo.download = ".png";
        });
      } else {
        this.$refs.lineThree.saveImg().then(e => {
          this.$refs.downThree.href = e;
          this.$refs.downThree.download = ".png";
        });
      }
      return false;
    },
    getDeviceData(url, _data) {
      this.getHttp({
        elem: "areaOption",
        loadElem: "one",
        url: url,
        _data: _data,
        methods: "get"
      });
    },
    getAlarmData(url, _data) {
      this.getHttp({
        elem: "alarmOption",
        loadElem: "two",
        url: url,
        _data: _data,
        methods: "get"
      });
    },
    getLineDate(_data) {
      const ordered = {};
      Object.keys(_data)
        .sort()
        .forEach(function(key) {
          ordered[key] = _data[key];
        });
      let _arr = [],
        _arrTwo = [];
      for (let k in ordered) {
        _arr.push(_data[k]);
        _arrTwo.push(k);
      }
      return { _arr: _arr, _arrTwo: _arrTwo };
    },
    searchAlarm(_index, _data) {
      this.alarmOption.searchList.forEach(e => {
        e.active = false;
      });
      this.$set(this.alarmOption.searchList[_index], "active", true);
      switch (_data) {
        case 0:
          this.getAlarmData(
            `operation-log/alarm-data/week`,
            this.alarmOption.dataSearch
          );
          break;
        case 1:
          this.getAlarmData(
            `operation-log/alarm-data/month`,
            this.alarmOption.dataSearch
          );
          break;
        case 2:
          this.getAlarmData(
            `operation-log/alarm-data/year`,
            this.alarmOption.dataSearch
          );
          break;
        default:
          break;
      }
    },
    searchDevice(_index, _data) {
      this.areaOption.searchList.forEach(e => {
        e.active = false;
      });
      this.$set(this.areaOption.searchList[_index], "active", true);
      switch (_data) {
        case 0:
          this.getDeviceData(
            `regisger-object/assert-data/week`,
            this.areaOption.dataSearch
          );
          break;
        case 1:
          this.getDeviceData(
            `regisger-object/assert-data/month`,
            this.areaOption.dataSearch
          );
          break;
        case 2:
          this.getDeviceData(
            `regisger-object/assert-data/year`,
            this.areaOption.dataSearch
          );
          break;
        default:
          break;
      }
    },
    getScene(url, _data) {
      this.getHttp({
        elem: "sceneOption",
        loadElem: "three",
        url: url,
        _data: _data,
        methods: "get"
      });
    },
    getHttp(_option = {}) {
      /**
       * _option:{
       * elem:"xxxx",areaOption,
       * loadElem:"xxx"dom
       * methods:"post",
       * url:"xxx",
       * _data:{}
       * echartName"xxx"echarts
       * echartData"xxx"echarts
       * }
       */
      let option = JSON.parse(JSON.stringify(_option));
      this[option.elem].lineShow = false;
      this.$http
        .setTargetLoadType(option.loadElem || ".report")
        [option.methods || "post"](option.url, option._data || {})
        .then(response => {
          let _data = response.data;
          this[option.elem].xAxisList = _data.dateArray;
          this[option.elem].list = _data.dataList.map((item, index, arr) => {
            let newArr = {
              name: item[option.echartName || "address"],
              data: item[option.echartData || "addressNumList"]
            };
            return newArr;
          });
          this[option.elem].lineShow = true;
        });
    },
    searchScene(_index, _data) {
      this.sceneOption.searchList.forEach(e => {
        e.active = false;
      });
      this.$set(this.sceneOption.searchList[_index], "active", true);
      switch (_data) {
        case 0:
          this.getScene(
            `regisger-object/scene-data/week`,
            this.sceneOption.dataSearch
          );
          break;
        case 1:
          this.getScene(
            `regisger-object/scene-data/month`,
            this.sceneOption.dataSearch
          );
          break;
        case 2:
          this.getScene(
            `regisger-object/scene-data/year`,
            this.sceneOption.dataSearch
          );
          break;
        default:
          break;
      }
    }
  },
  components: {
    "echarts-one": echartsOne
  }
};
</script>

<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page .t-head,
.base-search {
  text-align: left;
}
.sub-page .t-head .time a {
  margin-left: 15px;
}
.one {
  width: 50%;
  display: inline-block;
}
.three {
  width: calc(50% - 5px);
  display: inline-block;
  vertical-align: top;
}
</style>



