<template>
     <div style="width: 100%;">
        <div class="box-title">{{list.title}}</div>
        <ul>
            <li class="box-list" 
            v-for="(item,index) in list.lists" :key="index" >
              <p>
                <img :src="item.image" class="box-img" :alt="item.image"/>
                <span class="list-value box-white">{{item.label}}</span>
                <span :class="item.class" class="list-value" :style="item.style" :title="item.value">{{item.value}}</span>
              </p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Object,
      default: () => {
        return {
          title: "",
          lists: []
        };
      }
    }
  }
};
</script>
<style scoped lang="scss">
@import "../../../../assets/css/style1.css";
.box-title {
  text-align: left;
}
.box-list {
  width: 100%;
  position: relative;
  text-align: left;

  .box-img {
    width: 30px;
    height: 30px;
    vertical-align: middle;
  }
  p {
    width: 100%;
    word-break: break-all;
    padding: 0 24px 0;
    height: 30px;
    line-height: 30px;
    margin-top: 12px;
    color: #fff;
    font-size: 16px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>



