<template>
    <div class="opt" >
        <el-form :model="dataForm" ref="formData" :rules="validata" :inline="true" label-width="100px">
            <el-form-item label="" prop="identification">
                <el-input v-model="dataForm.identification" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="remark"> 
                <el-input v-model="dataForm.remark" :rows="3" type="textarea"  class="j-input" placeholder=""></el-input> 
            </el-form-item>  
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="sumbit()"></el-button>
        </span>
    </div>
</template>
<script>
export default {
   props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      dataForm: {},
      validata: {
        identification: [{ required: true, message: "" }],
        remark: [{ required: true, message: "" }],
      }
    };
  },
  created(){
    this.expendFunction();
    if(this.$util.isEmpty(this.pData.data))return;
    this.dataForm = JSON.parse(JSON.stringify(this.pData.data));
  },
  methods: {
     expendFunction() {
      Function.prototype.before = function(beforefn) {
        let _self = this; //
        return function() {
          beforefn.apply(this, arguments); //this
          return _self.apply(this, arguments); //
        };
      };
      Function.prototype.after = function(afterfn) {
        let _self = this;
        return function() {
          let ret = _self.apply(this, arguments); //return
          afterfn.apply(this, arguments);
          return ret;
        };
      };
    },
    sumbit(){
      this.ok.before(()=>{
        this.dataForm.name=this.dataForm.identification;
      })();
    },
    ok() {
      this.$refs.formData.validate(validate => {
        if (validate) {
          this.$http.setTargetLoadType(".opt")[this.pData.methods || "post"](
            this.pData.url,
            this.dataForm
          ).then(response => {
            this.$emit("result", true,response);
          });
        } else {
          console.log("");
          return false;
        }
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="scss">
.opt {
  text-align: center;
  .el-form {
    margin-top: 15px;
    .j-input {
      .el-input__inner {
        border: 1px solid #0e94d2;
      }
    }
  }
  .textButton {
    .el-button {
      background: #1b4272;
      color: #fff;
    }
  }
}
</style>






