<template>
  <div class="zzjggl">
    <div class="panel">
      <div class="base-search">
        <label></label>
        <span class="m-input">
            <el-input class="w-200" v-model="searchForm.serverName" placeholder=""></el-input>
        </span>
        <label></label>
        <span class="m-input">
            <el-input class="w-200" v-model="searchForm.areaName" placeholder=""></el-input>
        </span>
        <a href="javascript:void(0)" class="btn-search" @click="search"></a>
        <a href="javascript:void(0)" class="btn-add" @click="add()" v-if="permissionResource.hasPermission('CREATION')"></a>
        <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')"></a>
      </div>
      <div class="line"></div>           
      <div>
          <table-grid :option="option" ref="sysList">
              <template slot-scope="row" >
                <a href="javascript:void(0)" title="" @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')">
                  <i class="btn-icon icon-16"></i>
                </a>
                <a href="javascript:void(0)" title="" @click="deleteTab(row)" v-if="permissionResource.hasPermission('DELETION')">
                  <i class="btn-icon icon-17"></i>
                </a>
              </template>
          </table-grid>
      </div>
    </div>
    <msg-dialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
        <dialogEdit :pData='msgDialogInfo' @equit="equit"></dialogEdit>
    </msg-dialog>
  </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogEdit from "@/views/xinjiang/analysis/opt/sysAdd"; 
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      dialogVisible: {
        msgDialog: false
      },
      searchForm:{
        serverName: '',
        areaCode: ''
      }, 
      msgDialogInfo:{},
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "520px",
        data: ""
      },
      option: {
        isShowTableNav: false, 
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          hasCustomColum: true, 
          tableTitle: [
            { prop: "serverName", label: "" },
            { prop: "areaName", label: "" },
            { prop: "areaCode", label: "" },
            { prop: "address", label: "" }, 
            {
              prop: "createTime",
              label: "",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.modifiedDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          opeColumnWidth: 220,
          customRes: true 
        },
        customLabel: "",
        url: "/vcpalgaccess/server/findByPage",
        type: "post",
        headers:{"Content-Type": "application/json"},
        notRequestSever: false,
        param: {
          /* pageSize: 10,
          pageNum: 1,
          para: {
            serverName: "",
            areaCode: 'null'
          } */
        }
      }
    };
  },
  methods: { 
    deleteTab(row){ 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/vcpalgaccess/server/delete`,{id: row.rowData.id}).then(response =>{
            if(response.data.resultCd == 0){
              this.$notify({
                title: '',
                message: '!!',
                type: 'success'
              });
              this.equit();
            } 
          })
        });
    },  
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.$refs.sysList.flushTableData();
      this.dialogVisible.msgDialog = false;
    },
    search() {
      //let param = {}; 
      if (this.searchForm) { 
        this.option.param = Object.assign({pageSize: 10, pageNum: 1,},{para: this.searchForm});
      }  
      this.$refs.sysList.searchTableData(this.option.param);
    }
  },
  components: {
    "table-grid": tableGrid,
    "msg-dialog": msgDialog,
    dialogEdit
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page .t-head,
.base-search {
  text-align: left;
}
</style>




