/**
 * base64
 */
util.getBase64 = function (prefix, base64) {
  if (base64) {
    if (base64.indexOf("data:") == -1) {
      base64 = prefix + base64;
    }
    return base64;
  } else {
    return "";
  }
}


/**
 * @description : .
 * @author  houjianhong
 * @argument : obj : 
 *             key : 
 * @returns : 
 */

util.readObjectKey = function (obj, key) {
  let keys = key.split(".");
  let result = "";
  let newObj = util.copyObject(obj);//

  for (let i = 0; i < keys.length; i++) {
    if (i == 0) {
      if (!newObj) {
        result = "";
        return result;
      } else {
        result = newObj[keys[i]];
      }
    } else {
      if (!result) {
        result = "";
        return result;
      } else {
        result = result[keys[i]];
      }
    }
  }
  return result;
}

/**
 * @description  
 * 
 * @author houjianhong
 * 
 * @argument arr<array> 
 * 
 */
util.uniqued = function (arr) {
  //let newArr = util.copyObject(arr);
  let newArr = arr;
  newArr = newArr.reduce(function (item, next) {
    newArr[next.prop] ? '' : newArr[next.prop] = true && item.push(next);
    return item;
  }, []);
  return newArr;
}

util.distinct = function (arr){
  return Array.from(new Set(arr))
}

/**
 * @description  
 * 
 * @author houjianhong
 * 
 * @argument obj<Object> 
 * 
 * @return result {
 *             keys[],key
 *             values [],value 
 *         }
 * 
 */
util.resolveObject = function (obj) {
  let values = [];
  let result = {};
  let keys = Object.keys(obj);
  keys.sort((a, b) => {
    if (Number(a) > Number(b)) {
      return 1;
    } else {
      return -1;
    }
  });
  for (let i = 0; i < keys.length; i++) {
    values.push(obj[keys[i]]);
  }
  result.keys = keys;
  result.values = values;
  return result;

}

/**
 * @description  ididid
 * 
 * @argument arr<Object> list
 * 
 * @author : houjianhong
 * 
 * @return result  : [] 
 * 
 * 
 */

util.getParents = function (arr, param) {
  let defaultparam = {
    prop: {
      id: "id",
      parentId: "parentId"
    },
    searchId: ""
  }

  Object.assign(defaultparam, param);
  let id = defaultparam.prop.id;
  let searchId = defaultparam.searchId;
  let parentId = defaultparam.prop.parentId;

  for (let i = 0; i < arr.length; i++) {
    let temp = arr[i];
    if (!temp) {
      continue;
    }
    if (temp[id] == searchId) {
      let parents = util.getParents(arr, Object.assign(defaultparam, { searchId: temp[parentId] }));
      parents.push(JSON.parse(JSON.stringify(temp)));
      return parents;
    }
  }
  return [];
}


util.getBase64Image = function (img) {
  var canvas = document.createElement("canvas");
  canvas.width = img.width;
  canvas.height = img.height;
  var ctx = canvas.getContext("2d");
  ctx.drawImage(img, 0, 0, img.width, img.height);
  var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
  var dataURL = canvas.toDataURL("image/" + ext);
  return dataURL;

}

/**
 * @description base64
 */

util.imagePathToBase64 = function (path) {
  return new Promise((resolve, reject) => {
    var tempImage = new Image();
    tempImage.setAttribute('crossOrigin', 'anonymous');
    tempImage.src = path;
    tempImage.onload = function () {
      var baseSrc = util.getBase64Image(tempImage);
      resolve(baseSrc);
    }
  })
}

util.getBase64Str = function (fileObeject) {
  let fileReader = new FileReader();
  return new Promise((resolve, reject) => {
    let file = fileObeject.raw;
    if (file) {
      fileReader.readAsDataURL(file);
      let base64Str = "";
      fileReader.onload = () => {
        base64Str = fileReader.result;
      }
      fileReader.onerror = function (error) {
        reject(error);
      };
      fileReader.onloadend = function () {
        let res = {
          fileName: file.name,
          base64Str: base64Str
        }
        resolve(res);
      };

    } 
  });

}


export default util;



