<template>
    <div class="libOpt " >
         <el-form :model="formData" ref="formData" :rules="validata" :inline="true" label-width="100px">
            <el-form-item label="" prop="oldPassword">
                <el-input v-model="formData.oldPassword" type="password" class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label=""  prop="newPassword"> 
                <el-input v-model="formData.newPassword" type="password" class="j-input" placeholder=""></el-input> 
            </el-form-item> 
            <el-form-item label=""  prop="againPassword"> 
                <el-input v-model="formData.againPassword" type="password" class="j-input" placeholder=""></el-input> 
            </el-form-item> 
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="ok()"></el-button>
        </span>
    </div>
</template>
<script>
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      formData: {},
      validata: {
        oldPassword: [{ required: true, message: "" }],
        newPassword: [{ required: true, message: "" }],
        againPassword: [{ required: true,validator:(rule, value, callback) => {
            if (!value) {
                callback(new Error(""));
            } else if (value !== this.formData.newPassword) {
                callback(new Error("!"));
            } else {
                callback(new Error(""));
            }}
        }]
      }
    }
  },
  created() {
    this.formData = this.pData.data || {};
  },
  methods: {
    ok() {
      this.$refs.formData.validate(validate => {
        if (validate) {
          this.$http[this.pData.methods || "post"](
            this.pData.url,
            this.formData
          ).then(response => {
             this.$message({
            message: response.data.msg||"",
            type: "success"
          });
            this.$emit("result", true);
          });
        } else {
          console.log("");
          return false;
        }
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="scss">
.libOpt {
  text-align: center;
  .el-form {
    margin-top: 15px;
    .j-input {
      .el-input__inner {
        border: 1px solid #0e94d2;
      }
    }
  }
  .textButton {
    .el-button {
      background: #1b4272;
      color: #fff;
    }
  }
}
</style>




