<template>
    	<div class="zzjggl">
      <div class="panel">
          <div class="base-search">
              <label></label>
              <span class="m-input">
                  <el-input class="w-200"  v-model="searchForm.account" placeholder=""></el-input>
              </span> 
              <a href="javascript:void(0)" class="btn-search"  @click="search()"></a>    
              <a href="javascript:void(0)" class="btn-add" @click="add()" v-if="permissionResource.hasPermission('CREATION')"></a>       
              <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')"></a>
           
          </div> 
          <div class="line"></div>
          <div>
              <table-grid :option="option"  ref="useList">
                  <template slot-scope="row">
                      <a href="javascript:void(0)" @click="edit(row)" title="" v-if="permissionResource.hasPermission('UPDATING')">
                        <i class="btn-icon icon-16"></i>
                      </a>
                      <a href="javascript:void(0)" @click="deleteTab(row)" title="" v-if="permissionResource.hasPermission('DELETION')">
                        <i class="btn-icon icon-17"></i>
                      </a>
                  </template>
              </table-grid>
          </div>
      </div>
      <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
          <dialogEdit :pData='msgDialogInfo' @equit='equit'></dialogEdit>
      </msgDialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogEdit from "@/views/xinjiang/system/opt/userOpt"; 
export default {
  data() { 
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return { 
      permissionResource : permissionResource,
      searchForm:{

      },
      dialogVisible:{
        msgDialog: false
      },
      msgDialogInfo:{

      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "520px",
        data: ""
      },
      option: {
        isShowTableNav: false,
        hasCustomColum: true,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          tableTitle: [
            { prop: "account", label: "" },
            /* { prop: "enterpriseemail", label: "" },
            { prop: "enterpriseemail", label: "" }, */
            { prop: "email", label: "" },
            { prop: "phoneNumber", label: "" },
            { prop: "enabled", label: "",
              format: function(row, column, cellValue, index) {
                  return (
                    <div>
                      {row.enabled == true ? '' : ''}
                    </div>
                  );
                }
            },
            {
              prop: "createdDate",
              label: "",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.createdDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          tableBodyOverFlow: false,
          fixedHeight: 360,
          hasCustomColum: true,
          opeColumnWidth: 220
        },
        customLabel: "",
        url: "/account",
        type: "get",
        notRequestSever: false,
        param: {
          
        }
      },
      value: ""
    };
  },
  methods: {
    deleteTab(row){ 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/account/${row.rowData.id}`).then(response =>{ 
            this.$notify({
              title: '',
              message: '!!',
              type: 'success'
            });
            this.equit(); 
          })
        });
    }, 
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){  
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    search(){
      let data = Object.assign({size: 10, page: 1},this.searchForm); 
      this.$refs.useList.searchTableData(data);
    },
    equit() {
      this.$refs.useList.flushTableData(); 
      this.dialogVisible.msgDialog = false;
    },
  },
  components: {
    "table-grid": tableGrid,
    msgDialog, 
    dialogEdit
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
}
.sub-page .left-menu li {
  text-align: left;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
</style>



