<template>
    <div>
        <el-dialog  :visible.sync="show" @close="close()" width="450px" :close-on-click-modal="false" :modal-append-to-body="false">
            <el-tabs v-model="activeName" @tab-click="changeTab">
                <el-tab-pane :label="item.label" :name="item.label" v-for="(item,index) in option" :key="index">
                    <el-input
                        placeholder=""
                        v-model="fileName"
                        :disabled="true"
                        class="fileName">
                    </el-input>
                    <el-upload
                        class="upload-demo upload"
                        :action="item.url"
                        :before-upload="handleBeforeUpload"
                        :on-change="handleChange"
                        :on-success="handleSuccess"
                        :on-error="handleError"
                        :limit="2"
                        :name="item.name"
                        :data="item.data ? item.data : {}"
                        :show-file-list="false"
                        ref ="fileUpload"
                        :key="index">
                        <el-button size="small" type="primary"></el-button>
                    </el-upload>
                    <div class="modelDown" v-if="item.downLoad">
                        <a :href="item.downLoad"></a>
                    </div>
                    <div v-else>
                        <p>{{item.tip}}</p>
                    </div>
                </el-tab-pane>
            </el-tabs>         
        </el-dialog>
    </div>
</template>

<script>
/**
 * 
 * [{
        url : '/employee/import-excel',//
        name : 'file', // key
        type : ["xls","xlsx"] // 
        size : 1024 // 
        downLoad : "xxx"//
        tip :  download
        label : tab
    }]

     
    openView() 
    closeView() 
 * 
 */
export default {
    props:['option'],
    data() {
        return {
            activeName : "",
            fileName : "",
            show : false,
            activeInfo : {},
        }
    },

    mounted() {
        this.init();
    },

    methods: {
        init(){
            if(this.option.length > 0){
                this.activeName = this.option[0].label;
                this.activeInfo = this.option[0];
                this.activeInfo.index = 0;
            }
        },
        openView(){
            this.init();
            this.show = true;
        },
        closeView(){
            this.show = false;
        },
        changeTab(tab, event){
            this.fileName = "";
            this.activeInfo = this.option[tab.index];
            this.activeInfo.index = tab.index;
        },
        handleBeforeUpload(file,fileList){
            if(this.activeInfo.type){
                if(!this.$util.fileInType(file.name,this.activeInfo.type)){
                    this.$message({
                        message: ','+this.activeInfo.type.join(","),
                        type: 'warning'
                    });
                    this.fileName = "";
                    this.$refs.fileUpload[this.activeInfo.index].abort(file);
                    this.$refs.fileUpload[this.activeInfo.index].clearFiles();
                    return false;
                }
            }
            
        },
        handleSuccess(response, file, fileList){
            this.$message({
                message: '',
                type: 'success'
            });
        },
        handleError(response, file, fileList){
            let details =JSON.parse(response.message);
            let tempArr = [];
            // error.response.data.message != undefined ? tempArr.push(error.response.data.message) : '';
            if (details.details != undefined) {
                Array.from(details.details, (result) => {
                    tempArr.push(result.type + " : "+ result.message);
                })
            }
            customMessage.warning({ message: details.message, duration: 2000, detailMessage:tempArr});
        },
        handleChange(file,fileList){
            this.fileName = file.name;
            if(fileList.length > 1){
                fileList.splice(0,1);
            }
        },
        close(){
            this.$emit("close");
            this.fileName = "";
            this.$refs.fileUpload[this.activeInfo.index].clearFiles();
            this.activeInfo = {};
            this.activeName = "";
        }
    },

    watch :{
        // visible(newValue , oldValue){
        //     this.show = this.visible;
        // }
    },

    computed :{
        
    },
    
    components :{

    }

}
</script>

<style  scoped>
.upload{
    display: inline-block;
}
.fileName{
    width: 270px;
}
.modelDown{
    margin-top: 10px;
}

</style>













