<template>
    <div>
        <div class="box-title">{{data.title}}</div>
        <div class="container">
            <p  v-for="(item,index) in data.lists"  >
                <img :src="item.image" :alt="item.image"><span>{{item.label}}</span></p>
        </div>
    </div>
</template>
<script>
export default {
  name: "box-two",
  props: {
    list: {
      type: Object,
      default: () => {
        return {
          title: "",
          lists: []
        };
      }
    }
  },
  data() {
    return {
      data: {}
    };
  },
  created() {
    this.data = this.list;
  },
  methods: {
    updateData() {}
  }
};
</script>
<style scoped>
@import "../../../../assets/css/style1.css";
.box-title {
  text-align: left;
}
img{
  transition: all 0ms ease-in 0s
}
</style>



