<template>
    <div ref="tu" style="height:100%; width:100%"></div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array,
      default: () => {
        return [];
      }
    },
    legendData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    colorList: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      chart: null,
      currentName: "",
      currentIndex: 0,
      data: [],
      option: {
        color: this.colorList,
        graphic: [
          {
            //
            type: "text",
            z: 100,
            left: "center",
            top: "40%",
            style: {
              x: 0,
              y: 0,
              fill: "#fff",
              text: "fef",
              font: 'bolder 15px "Microsoft YaHei", sans-serif',
              textAlign: "center" // 
            }
          },
          {
            //
            type: "text",
            z: 100,
            left: "center",
            top: "50%",
            style: {
              x: 0,
              y: 0,
              fill: "#fff",
              text: "0",
              font: 'bolder 16px  "Microsoft YaHei", sans-serif',
              textAlign: "center" // 
            }
          }
        ],
        legend: {
          bottom: 10,
          icon: "rect",
          itemWidth: 15,
          textStyle: {
            color: "#7dd7f8"
          },
          data: []
        },
        tooltip: {
          trigger: "item",
          formatter: function(param) {
            return (
              param.marker +
              "" +
              param.name +
              "<br>" +
              "&nbsp;&nbsp;&nbsp;&nbsp;" +
              param.value
            );
          }
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["50%", "70%"],
            center: ["50%", "45%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: true
              }
            },
            itemStyle: {
              normal: {
                borderColor: "#0a1d3c",
                borderWidth: 3
              }
            },
            data: []
          }
        ]
      }
    };
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.tu);
    this.data = this.list;
    this.getData();
  },
  methods: {
    getData() {
      this.option.series[0].data = this.data;
      this.option.legend.data = this.legendData;
      this.chart.on("mouseover", params => {
        if (params.componentType != "graphic") {
          this.getTimeData(params.name, params.value, params.color);
        }
      });
      this.getTimeData(
        this.option.legend.data[0],
        this.option.series[0].data[0].value,
        this.colorList[0]
      );
      this.chart.setOption(this.option);
    },
    getTimeData(name, value, color) {
      //graphic
      if (this.option.legend.data) {
        const colors = color || "#fff";
        this.option.graphic[0] != undefined
          ? ((this.option.graphic[0].style.text = `${name}`),
            (this.option.graphic[0].style.fill = colors))
          : "";
        this.option.graphic[1] != undefined
          ? (this.option.graphic[1].style.text = `${value}`)
          : "";
        this.chart.setOption(this.option);
        this.chart.dispatchAction({
          type: "highlight",
          name: name
        });
      }
    }
  }
};
</script>




