<template>
  <div class="faceSynthesis">
    <div class="base-search">
        <label></label>
        <span class="m-input-date">
            <el-date-picker 
                    type="date"
                    popper-class="m-select-con"
                    placeholder=""
                    v-model="dataSearch.BeginCreateTime"
                    :picker-options="dateTime.pickerOptionsStart"
                    :editable="false" 
                    @change="(value)=>changeTime(value,'startTime')" 
                  >
                  </el-date-picker>
        </span>
        <span class="mid-text"></span>
        <span class="m-input-date">
            <el-date-picker 
                    type="date"
                    popper-class="m-select-con"
                    placeholder="" 
                    :editable="false" 
                    v-model="dataSearch.EndCreateTime"
                    :picker-options="dateTime.pickerOptionsEnd" 
                    @change="(value)=>changeTime(value,'endTime')"
                  >
                  </el-date-picker>
        </span>
        <label></label>
        <el-select popper-class="m-select-con" v-model="dataSearch.AreaCode" clearable>
            <el-option 
                :key="index" 
                v-for="(item,index) in constant.areaCode" 
                :label="item.name" 
                :value="item.id"> 
            </el-option> 
        </el-select> 
        <label></label>
        <el-select popper-class="m-select-con" v-model="dataSearch.DeviceID" clearable>
            <el-option 
                :key="index" 
                v-for="(item,index) in constant.DeviceID" 
                :label="item.name" 
                :value="item.deviceID"> 
            </el-option> 
        </el-select> 
        <a href="javascript:void(0)" class="btn-search" @click="search()"></a>
    </div>
    <div >
        <div class="face-list" v-loading="loading"> 
            <ul class="clearfix">
                <li v-for="(item, key) in messeageList" :key="key" class='zp-face-img'>
                    <div class='msgInfo' @click="showpop(item)"> 
                        <div v-if="imgShow" class='radian' :class="[item.Similarity > 60 ? item.Similarity >= 90 ? greenStatus : blueStatus : redStatus]">{{item.Similarity.toFixed(2)}}</div>
                        <img v-if="imgShow" :src="item.FaceID" />  
                        <div class="zp-title">
                            <el-tooltip :content="item.CreateTime" placement="top" popper-class="atooltip">
                                <div>{{item.CreateTime}}</div>
                            </el-tooltip> 
                        </div>  
                    </div>
                    <div class='optButton'> 
                        <img :src="Img.loc" @click="getMap(item.getMap)"/>
                        <img :src="Img.comparise" @click="getComarise(item)"/>
                        <img :src="Img.user" />
                        <img :src="Img.film" />
                    </div>
                </li> 
            </ul> 
        </div>
    </div> 
    <pagination :pData='pageOption' @init='init'/>
    <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
        <captureMsg :pData='captureInfo'/>
    </msgDialog>
  </div>
</template>

<script>
import {imageBaseUrl}  from "@/utils/global"
import {dateArraySort} from '@/utils/datetime'
import pagination from '@/views/xinjiang/common/components/pagination'
import msgDialog from '@/views/xinjiang/common/components/dialog/msgDialog'
import captureMsg from '@/views/xinjiang/comparison/captureMsg'

export default {
  components:{pagination, msgDialog, captureMsg},
  props:{},
  data(){
    return {
        loading: false,
        imgShow: false,
        greenStatus: 'greenStatus',
        blueStatus: 'blueStatus',
        redStatus: 'redStatus',
        imageBaseUrl:imageBaseUrl,
        imgBase64: '',
        dialogVisible:{
            msgDialog: false
        },
        msgDialog:{
            title: '',
            dialogVisible: true,
            width: '600px' 
        },
        captureInfo:{},
        Img:{
          loc: require('../../../assets/images/loc.png'),
          comparise: require('../../../assets/images/pic.png'),
          user: require('../../../assets/images/user.png'),
          film: require('../../../assets/images/video.png') 
        },
        pageOption:{
            currentPageNum: 1,  
            pageSize: 10, 
            firstPage: 1,
            lastPage: 7, 
            total: 70, 
            url: 'vcpinterface/ConvFaceReceive/FindPage' 
        },
        messeageList:[{
            FaceID: '',
            createTime: ''
        },{
            FaceID: '',
            createTime: ''
        }],
        constant:{
            areaCode: [],
            DeviceID: []
        },
        dataSearch:{
            BeginCreateTime:'',
            EndCreateTime: '',
            areaCode: ''
        },
        treeList: [],  
        dateTime: {
          pickerOptionsStart: {
            disabledDate: time => {
              if (this.dataSearch.EndCreateTime) { 
                return time.getTime() > this.dataSearch.EndCreateTime;
              }  
            }
          },
          pickerOptionsEnd: {
            disabledDate: time => {
              if (this.dataSearch.BeginCreateTime) {  
                return time.getTime() < this.dataSearch.BeginCreateTime;
              }  
            }
          }
        }, 
    }
  },
  watch:{},
  computed:{},
  methods:{
    getMap(obj){ 
        this.$util.imagePathToBase64(obj.imgBase64).then(res=>{
            this.imgBase64 = res; 
            let mapObj = {imgBase64: this.imgBase64,AreaCode: obj.AreaCode, DeviceID: obj.DeviceID};  
            this.$router.push({ path: '/newIndex/newTrajectory', query: {data: mapObj}});//
        });
        
    },
    getComarise(obj){  
        this.$router.push({ path: '/newIndex/newFace/mainLib', query: {data: {imgBase64: obj.FaceID}}});
        /* this.$http.post(`/vcpface/face/searchface`, {imgBase64: obj.FaceID}).then(response =>{  
            this.$router.push({ path: '/newIndex/newFace/mainLib', query: {data: response.data.res.data}}); // this.searchForm
        }) */
        //this.$router.push({ path: '/newIndex/newFace/newFaceRetrieve', query: {data: obj}});
    },
    init(formData) {  
          this.loading = true;
          this.imgShow =false;
          this.$http.post(`vcpinterface/ConvCompHistory/FindPage`,  Object.assign( 
          {
            PageSize: 12, 
            Type: 1 
          },
          formData
        ) ,{headers:{"Content-Type": "application/json"}}).then(response =>{
              this.pageOption = {
                currentPageNum: 1,  
                pageSize: 12, 
                firstPage: 1,
                lastPage: (response.data.Res.TotalRow % 12 == 0) ? response.data.Res.TotalRow / 12 : parseInt(response.data.Res.TotalRow / 12) + 1 , 
                total: response.data.Res.TotalRow, 
                url: 'vcpinterface/ConvCompHistory/FindPage' 
              } 
              console.log(this.pageOption, ''); 
              let Host = imageBaseUrl;
              let data = response.data.Res.Data;  
              data.forEach(item =>{ 
                  item.FaceID = Host + item.FaceID + '.jpg'; 
                  item.ImageID = Host + item.ImageID + '.jpg';  
                  item.FaceImageUrl = item.FaceID;
                  item.ImageUrl = item.ImageID; 
                  item.getMap = {AreaCode: item.AreaCode, DeviceID: item.DeviceID, imgBase64: item.FaceImageUrl};
                  item.Similarity = parseFloat(Math.round(item.Similarity * 100) / 100);  
                  item.LocationMarkTime = ': ' + item.CreateTime; 
                  this.$http.get(`/regisger-object/get-area-name/${item.AreaCode}`).then(response =>{
                      item.AreaCode = '' + response.data;
                  }); 
                  this.$http.get(`/regisger-object/device-id/${item.DeviceID}`).then(response =>{
                      item.DeviceID = '' + response.data;
                  }); 
              })
              this.messeageList = dateArraySort(data,'CreateTime'); 
              console.log(this.messeageList); 
              this.loading = false;
              this.imgShow = true;
          })
      },
    showpop(data){ 
        this.captureInfo = Object.assign(data, {type: ''}); 
        this.dialogVisible.msgDialog = true;
    },
    equit(){
        this.dialogVisible.msgDialog = false;
    },
    dataInit(){
        this.$http.get(`/regisger-object/area-tree`).then(response =>{
            this.constant.areaCode = response.data.map(item => (({name, id}) => ({name, id}))(item)); 
        })
        /* this.$http.get(`/regisger-object/constant`).then(response =>{
            this.constant.DeviceID = (response.data.filter(item => item.id == 'Brand'))[0].options.map(item => (({text, value}) => ({text, value}))(item));  
        }) */ 
        this.$http.get('/regisger-object/device-name').then(response =>{
            this.constant.DeviceID = response.data.map(item => (({name, deviceID}) => ({name, deviceID}))(item)); 
        })
    },
    search(){
        let data = JSON.parse(JSON.stringify(this.dataSearch));  
        this.init(data);
    }, 
    //
    changeTime(value, status) {  
        let time = new Date(value).getTime();  
        switch (status) {
            case 'startTime':
                this.formData.BeginCreateTime = time; 
                break;
            case 'endTime':
                this.formData.EndCreateTime = time; 
                break;        
            default:
                break;
        }
    },
    handleChange(value) {
      this.dataSearch.AreaCode = value[value.length - 1];
    } 
  },
  created(){},
  mounted(){
      this.init();
      this.dataInit();
  }
}
</script>
<style lang="scss" scoped>
@import "../../../assets/css/style1.css";
.greenStatus{
    background: #0ed255;
}
.blueStatus{
    background: #67cafe;
}
.redStatus{
    background: #c30e25;
}
.faceSynthesis{
    .radian{
        z-index: 1000;
        position: absolute;
        width: 46px;
        height: 36px;
        border-radius: 0 0 100px;
        //background-color: red; 
        margin-top: -9px; 
        margin-left: 1px;
        color: #fff;
        padding: 3px;
        font-size: 10px;
    }
    .face-list{
        //padding: 20px 20px 0 23px;
        // margin: 0 10px 15px 0px;
        .clearfix{
            height: auto; 
            min-height: 402px;
            max-height: 1050px;
            overflow: auto;
            .zp-face-img{  
                width: 207px; 
                border: 0px;
                .msgInfo{
                    cursor: pointer; 
                    margin-left: 3%; 
                    position: relative;
                    img{
                        width: 150px; 
                        height: 150px;
                        margin-top: -10px; 
                        border: 1px solid #0e94d2;
                    }
                    .zp-title{ 
                        height: 20px;
                        line-height: 18px;
                        color: #67cafe; 
                        font-size: 10px;
                        .el-tooltip{
                            margin-top: 3px;
                            margin-bottom: 5px; 
                        } 
                        div{
                                text-align: left;
                                width:177px; 
                                text-overflow:ellipsis;
                                overflow:hidden;
                                white-space:nowrap;  
                            }
                        /* div:hover { 
                            white-space:normal;
                            height:300px;  
                            transition-duration:0.5s;
                            transition-timing-function:ease-in;  
                        } */
                    }
                } 
                .optButton{
                    height: 30px;
                    .el-button{
                        width: 75px;
                        height: 28px; 
                        color: #fff;
                    }
                    .editButton{ 
                        background: #66b1ff;
                        border-color: #66b1ff;
                    }
                    .deleteButton{
                        background: #F56C6C;
                        border-color: #F56C6C;
                    }
                }
            } 
        }  
              
    } 
    .optButton{
        img{
            width: 26px;
            height: 26px;
            margin-left: 10px;
            cursor: pointer;
        }
    } 
}
</style>


