<template>
  <div>
    <div class="grow-up-navTitle tableTitle" v-if="isShowTableNav">
      <div class="tableTitleLeft">
        <span class="iconfont icon-liebiaomoshi_kuai"></span>
        <span></span>
      </div>
      <div class="tableTitleRight">
        <slot name="opt"></slot>
      </div>
    </div>
    <table-component
      :table-data="tableData"
      :table-option="tableOption"
      :page-data="pageData"
      v-loading="loading"
      :custom-label="customLabel"
      @page-change="pageChange"
      ref="tableComponent"
      @click-table="clickTable"
      @cell-dblclick="clickdbTable"
      @table-select-change="tableSelectChange"
      @select="selectRow"
      :fixed="fixed"
      :height="height"
    >
      <template slot="expand" slot-scope="scope">
        <slot name="expand" :row-data="scope.rowData"></slot>
      </template>
      <el-table-column
        type="index"
        :index="indexMethod"
        slot="index"
        label=""
        width="55"
        v-if="this.option.tableOption.isShowIndex"
      ></el-table-column>
      <template slot-scope="scope">
        <!--  -->
        <slot :row-data="scope.rowData"></slot>
      </template>
    </table-component>
  </div>
</template>
<script >
/**
 * option : { //
        tableOption :{//
          isReserveSelection : false// false
          tableUnique : id //  isReserveSelectiontrue
          expandable : 
          singleSelect : false // false
          isShowSelect true,  //   false
          hasCustomColum : true, //    false
          isShowIndex: false, //    false
          hasDrillDownCell: false,// false
          drillDownProp : {//(hasDrillDownCelltrue)
            unique : 'codeId', //
            drillDownFlag : 'tag' //
            url : '', // ,
            param : {},//
            requestArg: '',//unique
            drillDownColumn : '',//
          }
          tableTitle : [{ // proptableData
                prop : "sortnumber", //
                label : "" // 
                width : "180",//
                drillDown :  false, //,hasDrillDownCelltrue
                format: function(row, column, cellValue, index){}//
            }],
           tableBodyOverFlow : true //body
           prop { //
             totalcount : "xxxx"//totalcount
             data  "xxx"//data
           }
 *       },
        height : String/number, //,body
 *      customLabel  '', //,hasCustomColumtrue
 *      fixed : "right/left", //,false
 *      type : 'get', //postget  get
 *      param  {}, //,  :pageSize, pageNum
 *      url : '', //
 *      isShowPage  true //true
 *      notRequestSever//
 *      isShowTableNav : true, //true
 *      showTitle : true,//,true
 *      baseParam:{//
 *      }
 *
 * }
 *
 * 
 *     click-table :  row, column, cell, event
 *     table-select-change    selection
 * 
 * 
 * 
 *      opt : 
 *       : 
 * 
 *
 * 
 *      slot-scope = "row"
 *json   rowData {}pageData
 *
 * API:
 *     clearSelection() : 
 *     setCurrentRow(row) : singleSelecttrue
 *     setRowSelections(rows)  
 *     getSelectedRow() : 
 *     getCurrentRow() : singleSelecttrue
 *     flushTableData() : 
 *     searchTableData(param) : 
 *     flushTableDataTthisPage() : 
 *
 *
 */
import util from "@/utils/util";
import tableComponent from "./tableComponent.vue";
import axios from "@/utils/axios"; 
import { format } from "@/utils/datetime"

export default {
  name: "tableGrid",
  props: ["option"],
  data: function() {
    return {
      multipleSelection: [],
      isShowTableNav:
        this.option.isShowTableNav == false ? this.option.isShowTableNav : true,
      tableData: [],
      pageData: {
        pageSize: this.option.param.size,
        isShowPage:
          this.option.isShowPage == false ? this.option.param.isShowPage : true,
        total: 0
      },
      customLabel: this.option.customLabel,
      fixed: this.option.fixed || false,
      height:
        this.option.height < 1 ? "-webkit-fill-available" : this.option.height,
      tableOption: this.option.tableOption,
      hasDrillDownCell: this.option.tableOption.hasDrillDownCell ? true : false,
      drillDownProp: this.option.tableOption.drillDownProp,
      type: this.option.type ? this.option.type : "get",
      url: this.option.url,
      param: this.option.param,
      baseParam: this.option.baseParam || {},
      loading: false,
      reqVersion: 0,
      notRequestSever: this.option.notRequestSever,
      tableTreeData: [],
      showTitle: this.option.showTitle == false ? false : true,
      prop : this.option.tableOption.prop || {
        totalcount : "totalcount",
        totalRow: "totalRow",
        data : "data",
        page : "page"
      },
      customRes : this.option.tableOption.customRes ? true : false
    };
  },

  created: function() {
    if (this.showTitle) {
      this.titleDeal();
    }
    if (this.hasDrillDownCell) {
      //
      this.loopProp();
    }
    if (!this.notRequestSever) {
      this.getTableData();
    }
  },

  mounted: function() {},

  methods: {
    titleDeal() {
      //
      let vm = this;
      let props = this.tableOption.tableTitle;

      for (let i = 0; i < props.length; i++) {
        if (props[i].format) {
          continue;
        } else {
          props[i].format = function(row, column, cellValue, index) {
            let content = util.readObjectKey(row, props[i].prop);
            return <div title={content}>{content}</div>;
          };
        }
      }
    },
    loopProp: function() {
      //
      var vm = this;
      var props = this.tableOption.tableTitle;
      for (let i = 0; i < props.length; i++) {
        if (props[i].drillDown) {
          props[i].format = vm.propFormat;
        }
      }
    },
    getTableData: function() {
      var _this = this;
      let param = {};
      param = Object.assign(param,_this.baseParam, _this.param);
      let _request = axios.get;
      if (_this.type === "post") {
        _request = axios.post;
      }
      _this.loading = true;
      _this.reqVersion++;
      let version = _this.reqVersion;
      _this.tableData = [];

      _request(_this.url, param)
        .then(function(response) {
          if (version !== _this.reqVersion) {
            return;
          }
          if (_this.pageData.isShowPage) {
            _this.tableData = util.readObjectKey(response.data, _this.prop.data) || []; 
            _this.pageData.total = util.readObjectKey(response.data, _this.prop.totalcount) || 0;
            if(response.data.res && _this.customRes){ 
              _this.tableData = util.readObjectKey(response.data.res, _this.prop.data) || [];  
              let data = _this.tableData;
              let newArry = [];
              for(let i = 0; i < data.length; i++){
                data[i].createTime = format(data[i].createTime); 
                data[i].status = data[i].status == 1 ? '' : '';
                //data[i].algCode = data[i].algCode == 'FACE_RECOGNITION' ? '' : '';
                let type = data[i].algType;
                switch (type) {
                  case 'video':
                    data[i].algType = ''
                    break;
                  case 'image':
                    data[i].algType = ''
                    break;
                  case 'audio':
                    data[i].algType = ''
                    break;
                  case 'word':
                    data[i].algType = ''
                    break;
                  case 'other':
                    data[i].algType = ''
                    break;
                  default:
                    break;
                } 
                newArry.push(data[i]);
              }
              _this.tableData = newArry;
              /* _this.tableData = _this.tableData.map(item =>{
                item.createTime = format(_this.tableData.createTime);
                item.status = item.status == 1 ? '' : '';
              }) */
              _this.pageData.total = util.readObjectKey(response.data.res, _this.prop.totalRow) || 0; 
            }
          } else {
            _this.tableData = util.readObjectKey(response.data) || [];
            _this.pageData.total = 0;
            if(response.data.res){ 
              _this.tableData = util.readObjectKey(response.data.res, _this.prop.data) || []; 
              _this.tableData.createTime = this.format(_this.tableData.createTime);
              _this.pageData.total = util.readObjectKey(response.data.res, _this.prop.totalRow) || 0;
            }
          }
        })
        .catch(function(e) {
          if (version !== _this.reqVersion) {
            return;
          }
          _this.tableData = [];
          _this.pageData.total = 0;
          _this.$emit("error", e); //
          console.log(e);
        })
        .then(() => {
          //   catch
          _this.loading = false;
          _this.tableTreeData = _this.tableData;
        });
    },

    pageChange: function(pageIndex) {
      //this.param.pageNum = pageIndex;
      if (this.pageData.isShowPage) {
        this.param[this.prop.page] = pageIndex; //
      }
      this.getTableData();
    },

    clickTable: function(row, column, cell, event) {
      if (this.hasDrillDownCell) {
        //
        if (
          this.drillDownProp.drillDownColumn &&
          column.property != this.drillDownProp.drillDownColumn
        ) {
          this.$emit("click-table", row, column, cell, event);
          return;
        }
        if (row[this.drillDownProp.drillDownFlag]) {
          if (row.open) {
            //
            row.open = false;
            for (let i = 0; i < this.tableTreeData.length; i++) {
              if (
                row[this.drillDownProp.unique] ==
                this.tableTreeData[i][this.drillDownProp.unique]
              ) {
                this.tableTreeData[i].children = null;
                break;
              }
            }
            var tableList = [];
            this.changeTreeToList(this.tableTreeData, tableList);
            this.tableData = tableList;
          } else {
            //
            row.open = true;
            let arg = this.drillDownProp.requestArg;
            let unique = this.drillDownProp.unique;
            if (arg) {
              //
              this.drillDownProp.param[arg] = row[unique];
            } else {
              this.drillDownProp.param[unique] = row[unique];
            }
            this.addTableData(row);
          }
        }
      }
      this.$emit("click-table", row, column, cell, event);
    },
    clickdbTable: function(row, column, cell, event) {
      this.$emit("cell-dblclick", row, column, cell, event);
    },
    addTableData: function(row) {
      let vm = this;
      let _request = vm.$http.get;
      if (vm.type === "post") {
        _request = vm.$http.post;
      }
      _request(vm.drillDownProp.url, vm.drillDownProp.param).then(function(
        response
      ) {
        for (let i = 0; i < vm.tableTreeData.length; i++) {
          if (
            row[vm.drillDownProp.unique] ==
            vm.tableTreeData[i][vm.drillDownProp.unique]
          ) {
            vm.tableTreeData[i].children = response.data.data;
            break;
          }
        }
        var tableList = [];
        vm.changeTreeToList(vm.tableTreeData, tableList);
        vm.tableData = tableList;
      });
    },

    //
    changeTreeToList(tableTree, tableList) {
      for (var i = 0; i < tableTree.length; i++) {
        tableList.push(tableTree[i]);
        if (tableTree[i].children) {
          this.changeTreeToList(tableTree[i].children, tableList);
          tableTree[i].children = null;
        }
      }
    },

    //
    getDrillDownData: function() {},

    //
    tableSelectChange: function(selection) {
      this.$emit("table-select-change", selection);
    },

    //
    selectRow: function(selection, row) {
      this.$emit("table-select", selection, row);
    },

    clearSelection: function() {
      this.$refs.tableComponent.clearSelection();
    },

    //
    setCurrentRow: function(row) {
      this.$refs.tableComponent.setCurrentRow(row);
    },

    //
    setRowSelections: function(remenmberSelectRows) {
      this.$refs.tableComponent.setRowSelections(remenmberSelectRows || []);
    },

    //
    getCurrentRow() {
      return this.$refs.tableComponent.getCurrentRow();
    },

    //
    getSelectedRow: function() {
      return this.$refs.tableComponent.getSelectedRow();
    },

    flushTableData: function() {
      this.$refs.tableComponent.changeCurrentPage(1);
      this.pageChange(1);
    },

    flushTableDataTthisPage: function(pageNum) {
      this.$refs.tableComponent.changeCurrentPage(pageNum);
      this.pageChange(pageNum);
    },

    searchTableData: function(param) {
      this.param = param;
      this.flushTableData();
    },

    // 
    indexMethod(index) {
      const vm = this;
      let { param, pageData } = vm;
      if (!pageData.isShowPage) {
        return index;
      }
      let pageNum = Number.parseInt(param.pageNum);
      pageNum = pageNum > 0 ? pageNum : 1;
      let pageSize = Number.parseInt(pageData.pageSize);
      pageSize = pageSize > 0 ? pageSize : 10;
      return (pageNum - 1) * pageSize + index + 1;
    },

    //
    propFormat(row, column, cellValue, index) {
      var vm = this;
      if (row[vm.drillDownProp.drillDownFlag]) {
        //,
        if (row.open) {
          return (
            <div class="drillDownColumn">
              <i class="el-icon el-icon-minus"></i>
              {cellValue}
            </div>
          );
        } else {
          return (
            <div class="drillDownColumn">
              <i class="el-icon el-icon-plus"></i>
              {cellValue}
            </div>
          );
        }
      } else {
        return cellValue;
      }
    }

    //
    /* clickEspecialCell : function(row, column, cell, event){
            this.$emit("click-especial-cell", row, column, cell, event);
        }, */
  },

  components: {
    "table-component": tableComponent
  },

  watch: {
    "option.url"() {
      this.url = this.option.url ? this.option.url : "";
    },
    "option.tableOption.drillDownProp"() {
      this.drillDownProp = this.option.tableOption.drillDownProp;
    },
    "option.tableOption.drillDownProp.param"() {
      this.drillDownProp = this.option.tableOption.drillDownProp;
    }
  }
};
</script>
<style scoped>
.tableTitle {
  border: 1px solid #ccc;
  height: 56px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.drillDownColumn {
  cursor: pointer;
}
.tableTitleLeft {
  margin-left: 17px;
}
.tableTitleRight {
  margin-right: 10px;
}
</style>



