<template>
   <div class="carReport">
      <div class="clearfix">
        <div class="base-search">
            <label></label>
            <span class="m-input-date">
                <el-date-picker
                popper-class="m-select-con"
                 v-model="areaOption.dataSearch.startDate"
                 :picker-options="areaOption.startOptions"
                value-format="yyyy-MM-dd"
                type="date"
                placeholder="">
                </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
                <el-date-picker
                popper-class="m-select-con"
               v-model="areaOption.dataSearch.endDate"
               :picker-options="areaOption.endOptions"
               value-format="yyyy-MM-dd"
                type="date"
                placeholder="">
                </el-date-picker>
            </span>
            <label></label>
            <span class="m-input">
               <el-cascader
              v-model="areaOption.dataSearch.areaId"
              :options="areaOption.treeList"
             @change="handleChange"
              clearable
              popper-class="m-cascader"
              ></el-cascader>
            </span>
            <a href="javascript:void(0)" class="btn-search" @click="normalSearch()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
        </div>
         <div class="tjbb-item">
             <div class="sbtj-tjt">
                <div class="tjt-left">
                <div class="t-head">
                <span class="name"></span>
                <span class="time">
                    <a v-for="(item,index) in areaOption.searchList" 
                    :key="index" href="javascript:void(0)" 
                    :class="item.active?'selected':''" @click="search(index,item.value)">{{item.text}}</a>
                </span>
                <a  ref="down" @click="saveImage()" class="btn-down-pic" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
                </div>
                    <div id="tu1" style="height:435px; width:100%">
                        <div style="height:435px; width:100%">
                          <echarts-one 
                            :xAxisList="areaOption.xAxisList"  
                            :list="areaOption.list" 
                            ref="line" 
                            v-if="areaOption.lineShow">
                          </echarts-one>
                        </div>
                    </div>
                </div>
                <div class="tjt-right">
                    <div  style="height:435px; width:100%">
                        <echarts-two :list="areaOption.listPie" v-if="areaOption.pieShow"></echarts-two>
                    </div>
                    <div class="tu2-title" v-if="areaOption.pieShow"><p></p></div>
                    <div class="count" v-if="areaOption.pieShow"><p>{{areaOption.listPieCount}}</p></div>
                </div>
            </div>
        </div>
      </div>
     <div class="clearfix bottom-main">
         <div class="base-search">
            <label></label>
            <span class="m-input-date">
                <el-date-picker
                popper-class="m-select-con"
                 v-model="sceneOption.dataSearch.startDate"
                 :picker-options="sceneOption.startOptions"
                value-format="yyyy-MM-dd"
                type="date"
                placeholder="">
                </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
                <el-date-picker
                popper-class="m-select-con"
               v-model="sceneOption.dataSearch.endDate"
               :picker-options="sceneOption.endOptions"
               value-format="yyyy-MM-dd"
                type="date"
                placeholder="">
                </el-date-picker>
            </span>
            <label></label>
            <span class="m-input">
               <el-cascader
              v-model="sceneOption.dataSearch.areaId"
              :options="areaOption.treeList"
             @change="handleSceneChange"
              clearable
              popper-class="m-cascader"
              ></el-cascader>
            </span>
            <label></label>
              <span >
                  <el-select 
                  popper-class="m-select-con"
                  v-model="sceneOption.dataSearch.SceneCode"
                  placeholder="">
                      <el-option
                      v-for="item in sceneCodeEnumList"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      </el-option>
                  </el-select>
              </span>
            <a href="javascript:void(0)" class="btn-search" @click="searchScene()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
        </div>
         <div class="tjbb-item">
             <div class="sbtj-tjt">
                <div class="tjt-left">
                <div class="t-head">
                <span class="name"></span>
                <span class="time">
                    <a v-for="(item,index) in sceneOption.searchList" 
                    :key="index" href="javascript:void(0)" 
                    :class="item.active?'selected':''" @click="searchScene(index,item.value)">{{item.text}}</a>
                </span>
                <a  ref="downTwo" @click="saveSceneImage()" class="btn-down-pic" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
                </div>
                    <div id="tu1" style="height:435px; width:100%">
                        <div style="height:435px; width:100%">
                             <echarts-one :xAxisList="sceneOption.xAxisList"  :list="sceneOption.list" ref="lineTwo" v-if="sceneOption.lineShow"></echarts-one>
                        </div>
                    </div>
                </div>
                <div class="tjt-right">
                    <div  style="height:435px; width:100%">
                        <echarts-two :list="sceneOption.listPie" v-if="sceneOption.pieShow"></echarts-two>
                    </div>
                    <div class="tu2-title" v-if="sceneOption.pieShow"><p></p></div>
                    <div class="count" v-if="sceneOption.pieShow"><p>{{sceneOption.listPieCount}}</p></div>
                </div>
            </div>
        </div>
     </div>
   </div>
</template>
<script>
import echartsOne from "@/views/xinjiang/common/components/lineEchart/line";
import echartsTwo from "@/views/xinjiang/common/components/pieEchart/normalPie";
import mixins from "@/views/xinjiang/reportNew/mixins/mixins";
export default {
  mixins: [mixins],
  created() {
    this.areaOption.searchList = [
      {
          text: "",
          value: 0,
          index: 0,
          active: true
        },
        {
          text: "",
          value: 1,
          index: 1,
          active: false
        },
        {
          text: "",
          value: 2,
          index: 2,
          active: false
        }
    ];
    this.sceneOption.searchList = [
      {
          text: "",
          value: 0,
          index: 0,
          active: true
        },
        {
          text: "",
          value: 1,
          index: 1,
          active: false
        },
        {
          text: "",
          value: 2,
          index: 2,
          active: false
        }
    ];
  },
  mounted() {
    this.getAreaData(`car-statistics/car-data/week`, this.areaOption.dataSearch);
  },
  methods: {
    getAreaData(_url,  _data) {
       this.getAxios({
        elem: "areaOption",
        url: _url,
        methods:"get",
        _data: _data
      });
    },
    getScene(_data) {
      this.getHttp({
        elem: "sceneOption",
        url: `vcpinterface/ConvVehicle/StatisticsBySceneCode`,
        _data: _data,
        echartName: "scene",
        echartData: "sceneNumList"
      });
    },
    search(_index, _data) {
      this.areaOption.xAxisList = [];
      this.areaOption.list.forEach(e => {
        if (e.data.length != 0) {
          e.data = [];
        }
      });
      this.areaOption.searchList.forEach(e => {
        e.active = false;
      });
      this.$set(this.areaOption.searchList[_index], "active", true);
      switch (_data) {
        case 0:
          this.getAreaData(`car-statistics/car-data/week`, this.areaOption.dataSearch);
          break;
        case 1:
          this.getAreaData(`car-statistics/car-data/month`, this.areaOption.dataSearch);
          break;
        case 2:
          this.getAreaData(`car-statistics/car-data/year`, this.areaOption.dataSearch);
          break;
        default:
          break;
      }
    },
    normalSearch() {
      this.areaOption.xAxisList = [];
      this.areaOption.list.forEach(e => {
        if (e.data.length != 0) {
          e.data = [];
        }
      });
      this.areaOption.searchList.forEach(e => {
        e.active = false;
      });
      this.getAreaData("car-statistics/car-data", this.areaOption.dataSearch);
    }
  },
  components: {
    "echarts-one": echartsOne,
    "echarts-two": echartsTwo
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.tjt-right {
  width: 66%;
}
.tjt-left {
  width: 34%;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
.bottom-main {
  margin-top: 20px;
}
</style>




