<template>
    <div class="sub-page">
		<!-- s -->
      <div class="left-menu">
        <ul>
          <li 
          :key="index"
				 v-for="(item,index) in childrenRouter">
				 <router-link :to="{path:item.path}" :class="item.active?'selected':''" v-if="item.name!='1'">{{item.name}}</router-link></li>
        </ul>
        <span class="b-01"></span>
        <span class="b-02"></span>
        <span class="b-03"></span>
        <span class="b-04"></span>
      </div>
		<!-- e -->
		 <router-view></router-view>
    </div>
</template> 
<script>
import mixins from "@/views/xinjiang/common/mixins/mixins";
export default {
 
	mixins: [mixins]
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
}
.sub-page .left-menu li {
  text-align: left;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
.search-more {
  display: inline-block;
}
</style>






