<template>
    <div id="dialog-add" class="dialog b-dialog" >
        <div class="form scrollbar menu-form">
          <el-form :model="dataForm" ref="formData" :rules="validata" :inline="true" label-width="100px">
           <!-- <dl>
                <label></label>
                <span class="m-input">
                  <el-form-item >
                    <el-radio-group v-model="dataForm.type">
                      <el-radio 
                          v-for="(type, index) in menuTypeList" 
                          :label="index" :key="index">{{ type }}
                       </el-radio>
                    </el-radio-group>
                  </el-form-item>
                </span>
            </dl>-->
            <dl>
              <label><i class="require-icon">*</i>{{menuTypeList[dataForm.type] + ''}}</label>
              <span class="m-input">
                <el-form-item  prop="permissionModule.name">
                 <el-input v-model="dataForm.permissionModule.name" :placeholder="menuTypeList[dataForm.type] + ''"></el-input>
                </el-form-item>
              </span>
            </dl>
           <!-- <dl>
              <label><i class="require-icon">*</i></label>
              <span class="m-input">
                <el-form-item  prop="operations">
                  <el-select 
                    popper-class="m-select-con"
                    multiple
                    collapse-tags
                    v-model="dataForm.operations"
                    placeholder="">
                        <el-option
                        v-for="item in constant "
                        :key="item.value"
                        :label="item.text"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
              </span>
            </dl>-->
            <dl>
                <label></label>
               <span class="m-input">
                   <el-popover
                        popper-class="popover"
                        ref="popover"
                        placement="right"
                        width="200"
                        trigger="click"
                        v-model="visible"
                        @show="showData"
                    >
                    <!-- <syn-tree :option="treeOption" @clickNode="clickTreeNode" ></syn-tree> -->
                    <el-tree 
                     ref="tree"
                    :data="treeOption.data" 
                    :props="treeOption.defaultProps" 
                     node-key="id"
                    @node-click="clickTreeNode" >
                     
                    </el-tree>
                    </el-popover>
                    <el-form-item >
                      <el-input  v-popover:popover :readonly='true' v-model="dataForm.permissionModule.parentName" placeholder=""></el-input>
                    </el-form-item>
              </span>
            </dl> 
             <dl v-if="dataForm.type === 1">
                <label></label>
               <span class="m-input">
                 <el-form-item >
                  <el-input-number v-model="dataForm.permissionModule.rank" controls-position="right" :min="0" label=""></el-input-number>
                 </el-form-item>
              </span>
            </dl> 
            <dl>
                <label><i class="require-icon">*</i></label>
               <span class="m-input">
                 <el-form-item prop="permissionModule.identification">
                  <el-input v-model="dataForm.permissionModule.identification" placeholder=": sys:user:add, sys:user:edit, sys:user:delete"></el-input>
                 </el-form-item>            
              </span>
            </dl>
            <dl v-if="dataForm.type === 1" >
                <label></label>
               <span class="m-input">
                 <el-form-item  >
                   <el-input v-model="dataForm.permissionModule.uris" placeholder=""></el-input>
                 </el-form-item>
              </span>
              <el-tooltip placement="top" effect="light">
                <div slot="content">
                  <p>URL</p>
                    <p>1.URLViews /Sys/User,  /sys/user/</p>
                    <p>2.</p>
                </div>
                <i class="el-icon-warning"></i>
              </el-tooltip>
            </dl>
          </el-form>
	    </div>
	    <div class="btn-quit pt0"><a href="javascript:void(0)" @click="submit()"> </a></div>
    </div>
</template>
<script>
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      visible: false,
      dataForm: {
        type: 1,
        // operations:[],
        permissionModule: {
          name:"",
          identification:"",
          rank:"",
          uris:""
        }
      },
      menuTypeList: ["", ""],
      constant: [
        {
          text: "",
          value: "RETRIEVE"
        },
        {
          text: "",
          value: "CREATION"
        },
        {
          text: "",
          value: "UPDATING"
        },
        {
          text: "",
          value: "DELETION"
        }
      ],
      treeOption: {
        data: [],
        defaultProps: {
          children: "children",
          label: "name"
        }
      },
      validata: {
        "permissionModule.name": [{ required: true, message: "" }],
        // operations: [{ required: true, message: "" }],
        "permissionModule.identification": [
          { required: true, message: "" }
        ]
      }
    };
  },
  mounted() {
    this.expendFunction();
    if (this.$util.isEmpty(this.pData.data)) {
      this.getTree();
      return;
    }
    this.getEditDataInit();
  },
  methods: {
    expendFunction() {
      Function.prototype.before = function(beforefn) {
        let _self = this; //
        return function() {
          beforefn.apply(this, arguments); //this
          return _self.apply(this, arguments); //
        };
      };
      Function.prototype.after = function(afterfn) {
        let _self = this;
        return function() {
          let ret = _self.apply(this, arguments); //return
          afterfn.apply(this, arguments);
          return ret;
        };
      };
    },
    getEditDataInit() {
      let _list = [];

      this.dataForm.permissionModule = JSON.parse(
        JSON.stringify(this.pData.data)
      );

      /* this.dataForm.permissionModule.menu
        ? (this.dataForm.type = 1)
        : (this.dataForm.type = 0);*/
      this.getTable().then(res => {
         let meunList = res.filter(e => {
          return e.menu === true;
        });
        let _temp = meunList.filter(e => {
          return e.id === this.dataForm.permissionModule.parentId;
        });

        let prop = {
          code: "id",
          parent: "parentId",
          children: "children"
        };
        this.treeOption.data = this.$util.listToTree(meunList, prop); //   console.log("de",_arr);
        this.dataForm.permissionModule.parentName = _temp[0].name;
      });
    },
    showData() {
      this.$nextTick(() => {
        this.$refs.tree.setCurrentKey(this.dataForm.permissionModule.parentId);
      });
    },
    clickTreeNode(data, node) {
      this.dataForm.permissionModule.parentId = data.id;
      this.dataForm.permissionModule.parentName = data.name;
      this.visible = false;
    },
    getTable(_data) {
      return new Promise(resolve => {
        this.$http.get(`/menu`, _data).then(response => {
          resolve(response.data);
        });
      });
    },
    getTree() {
      let _arr = [];
      this.getTable().then(res => {
        let meunList = res.filter(e => {
          return e.menu === true;
        });
        let prop = {
          code: "id",
          parent: "parentId",
          children: "children"
        };
        this.treeOption.data = this.$util.listToTree(meunList, prop); //   console.log("de",_arr);
      });
    },
    submit() {
      this.ok.before(() => {
        this.dataForm.type != 1
          ? (this.dataForm.permissionModule.menu = false)
          : (this.dataForm.permissionModule.menu = true);
        if (
          this.dataForm.permissionModule.uris &&
          this.dataForm.permissionModule.uris.indexOf(",") !== -1
        ) {
          this.dataForm.permissionModule.uris = this.dataForm.permissionModule.uris.split(
            ","
          );
        } else if (
          Object.prototype.toString.call(
            this.dataForm.permissionModule.uris
          ) !== "[object Array]"
        ) {
          this.dataForm.permissionModule.uris = [
            this.dataForm.permissionModule.uris
          ];
        }
        this.dataForm.permissionModule.parentId
          ? (this.dataForm.permissionModule.root = false)
          : (this.dataForm.permissionModule.root = true);
        this.dataForm = this.dataForm.permissionModule;
      })();
    },
    ok() {
      this.$refs.formData.validate(validate => {
        if (validate) {
          this.$http
            .setTargetLoadType(".b-dialog")
            [this.pData.methods || "post"](this.pData.url, this.dataForm)
            .then(response => {
              this.$emit("result", true, response);
            });
        } else {
          console.log("");
          return false;
        }
      });
    }
  },
  components: {}
};
</script>

<style scoped lang="scss">
@import "../../../../assets/css/style1.css";
.dialog {
  height: 400px;
}
</style>
<style lang="scss">
.input-tree {
  max-height: 200px;
  overflow: auto;
}
</style>
<style lang="scss">
.b-dialog .menu-form {
  height: 320px !important;
  overflow: auto;
}
</style>





