export default {
  data() {
    return {
      childrenRouter: []
    };
  },
  created() {
  //  this.routes().forEach((element, index) => {
  //     this.childrenRouter.push({
  //       name: element.name,
  //       path: element.path,
  //       active: false
  //     });
  //   });

    let permissionResources = this.$store.state.permission.permissionResources;
    this.childrenRouter = permissionResources.getChildrenMenu(this.$route.path);
    if(this.childrenRouter.length){
      this.$router.push(this.childrenRouter[0].path);
    }else{
      this.childrenRouter = permissionResources.getParentChildrenMenu(this.$route.path);
      this.childrenRouter.forEach((e, index) => {
        if (this.$route.path === e.path) {
          this.$set(this.childrenRouter[index], "active", true);
        } else {
          e.active = false;
        }
      });
    }
    

  },
  watch: {
    $route(to, from) {
      this.childrenRouter.forEach((e, index) => {
        if (to.path === e.path) {
          this.$set(this.childrenRouter[index], "active", true);
        } else {
          e.active = false;
        }
      });
    }
  },
  methods: {
    // 
    routes() {
      var routes = {
        children: this.$router.options.routes
      };
      var route = this.$route.matched;

      for (var i = 0; i < route.length - 1; i++) {
        routes = routes.children.find(e => e.name == route[i].name);
      }

      return routes.children;
    },
    getPath(_data) {
      this.childrenRouter.forEach(e => {
        e.active = false;
      });
      this.$set(this.childrenRouter[_data], "active", true);
    }
  }
}


