<template>
  <div class="mapRoot">
    <span class="back" v-if="isBack" @click="back"></span>
    <div class="map" ref="map" v-if="isShow"></div>
  </div>
</template>

<script>
import geoJson from "./mapData/65_full.json";
export default {
  data() {
    return {
      geoJson: "65_full",
      isShow: false,
      mapData: [],
      min: 0,
      max: 0,
      isBack: false
    };
  },

  mounted() {
    this.getData("65");
  },

  methods: {
    createMap() {
      let mychart = this.$echarts.init(this.$refs.map);
      this.$echarts.registerMap("xinjiang", this.geoJson);
      mychart.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{b}:{c}"
        },

        visualMap: {
          min: this.min,
          max: this.max,
          left: 30,
          text: ["", ""],
          realtime: false,
          calculable: true,
          inRange: {
            //color: ["#37a2da", "#67e0e3"]
            color: ["#0238bf", "#03c2ff"]
          },
          textStyle: {
            color: "#fff"
          },
          bottom: "50"
        },

        series: [
          {
            name: "",
            type: "map",
            mapType: "xinjiang", // 
            itemStyle: {
              normal: {
                label: { show: false, color: "#fff" },
                shadowColor: "#fff",
                borderColor: "#2a66ff",
                //shadowColor: "rgba(255, 255, 255, 1)",
                shadowOffsetX: -2,
                shadowOffsetY: 2,
                shadowBlur: 10,
                opacity: 0.85
              },
              emphasis: { label: { show: true } }
            },
            data: this.data
            /* [
              { name: "", value: 100 },
              { name: "", value: 200 },
              { name: "", value: 300 },
              { name: "", value: 5000 },
              { name: "", value: 500 },
              { name: "", value: 158 },
              { name: "", value: 169 },
              { name: "", value: 189 },
              { name: "", value: 220 },
              { name: "", value: 363 },
              { name: "", value: 392 },
              { name: "", value: 420 },
              { name: "", value: 125 },
              { name: "", value: 230 },
              { name: "", value: 169 },
              { name: "", value: 310 },
              { name: "", value: 10 },
              { name: "", value: 139 },
              { name: "", value: 164 },
              { name: "", value: 164 },
              { name: "", value: 164 },
              { name: "", value: 164 },
              { name: "", value: 164 },
              { name: "", value: 34 },
              { name: "", value: 14 }
            ] */
          }
        ]
      });
      mychart.on("click", params => {
        if (this.isBack) {
          return;
        } else {
          this.isBack = true;
          this.getData(params.data.id);
        }
      });
    },
    back() {
      this.isBack = false;
      this.getData("65");
    },
    getData(id) {
      this.isShow = false;
      this.data = [];
      this.$http.get(`/big-screen/area-distribution/${id}`).then(res => {
        this.isShow = true;
        this.geoJson = require(`./mapData/${id}_full.json`);
        //let keys = Object.keys(res.data);
        this.min = 0;
        this.max = 0;
        for (let i = 0; i < res.data.length; i++) {
          if (this.min > res.data[i].value) {
            this.min = res.data[i].value;
          }
          if (this.max < res.data[i].value) {
            this.max = res.data[i].value;
          }
        }
        this.data = res.data;
        this.isShow = true;
        this.$nextTick(() => {
          this.createMap();
        });
      });
    }
  },

  watch: {},

  computed: {},

  components: {}
};
</script>

<style  scoped>
.mapRoot {
  position: relative;
  width: 100%;
  height: 100%;
}
.back {
  position: absolute;
  right: 30px;
  color: #fff;
  cursor: pointer;
  z-index: 10;
}
.map {
  width: 100%;
  height: 100%;
}
</style>




