<template>
   <div class="snapReport">
      <div class="clearfix">
        <div class="base-search">
            <label></label>
            <span class="m-input-date">
               <el-date-picker
               popper-class="m-select-con"
               v-model="areaOption.dataSearch.StartTime"
                 :picker-options="areaOption.startOptions"
               value-format="yyyy-MM-dd"
                type="date"
                placeholder="">
                </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
               <el-date-picker
               popper-class="m-select-con"
                type="date"
                v-model="areaOption.dataSearch.EndTime"
                  :picker-options="areaOption.endOptions"
                value-format="yyyy-MM-dd"
                placeholder="">
                </el-date-picker>
            </span>
            <label></label>
            <span class="m-input">
               <el-cascader
              v-model="areaOption.dataSearch.AreaCode"
              :options="areaOption.treeList"
             @change="handleChange"
              clearable
              popper-class="m-cascader"
              ></el-cascader>
            </span>
            <a href="javascript:void(0)" class="btn-search" @click="search()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
        </div>
        <div class="tjbb-item">
          <div class="sbtj-tjt">
            <div class="tjt-left">
            <div class="t-head">
            <span class="name"></span>
            <span class="time">
                <a v-for="(item,index) in areaOption.searchList" 
                :key="index" href="javascript:void(0)" 
                :class="item.active?'selected':''" @click="search(index,item.value)" v-if="permissionResource.hasPermission('RETRIEVE')">{{item.text}}</a>
            </span>
            <a  ref="down" @click="saveImage()" class="btn-down-pic" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
            </div>
                <div id="tu1" style="height:435px; width:100%">
                  <div style="height:435px; width:100%">
                    <echarts-one :xAxisList="areaOption.xAxisList"  :list="areaOption.list" ref="line" v-if="areaOption.lineShow"></echarts-one>
                  </div>
                </div>
            </div>
            <div class="tjt-right" style="height:435px;">
              <div  style="height:435px; width:100%">
                  <echarts-two :list="areaOption.listPie" v-if="areaOption.pieShow"></echarts-two>
              </div>
              <div class="tu2-title" v-if="areaOption.pieShow"><p></p></div>
              <div class="count" v-if="areaOption.pieShow"><p>{{areaOption.listPieCount}}</p></div>
            </div>
          </div>
        </div>
      </div>
     <div class="clearfix bottom-main">
        <div class="base-search">
            <label></label>
            <span class="m-input-date">
               <el-date-picker
               popper-class="m-select-con"
               v-model="sceneOption.dataSearch.StartTime"
                 :picker-options="sceneOption.startOptions"
               value-format="yyyy-MM-dd"
                type="date"
                placeholder="">
                </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
               <el-date-picker
               popper-class="m-select-con"
                type="date"
                v-model="sceneOption.dataSearch.EndTime"
                  :picker-options="sceneOption.endOptions"
                value-format="yyyy-MM-dd"
                placeholder="">
                </el-date-picker>
            </span>
            <label></label>
            <span class="m-input">
               <el-cascader
                v-model="sceneOption.dataSearch.AreaCode"
                :options="areaOption.treeList"
                @change="handleSceneChange"
                clearable
                popper-class="m-cascader"
              ></el-cascader>
            </span>
            <label></label>
              <span >
                  <el-select 
                  popper-class="m-select-con"
                  v-model="sceneOption.dataSearch.SceneCode"
                  placeholder="">
                      <el-option
                      v-for="item in sceneCodeEnumList"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                      </el-option>
                  </el-select>
              </span>
            <a href="javascript:void(0)" class="btn-search" @click="searchScene()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
        </div>
        <div class="tjbb-item">
          <div class="sbtj-tjt">
            <div class="tjt-left">
            <div class="t-head">
            <span class="name"></span>
            <span class="time">
                <a v-for="(item,index) in sceneOption.searchList" 
                :key="index" href="javascript:void(0)" 
                :class="item.active?'selected':''" @click="searchScene(index,item.value)" v-if="permissionResource.hasPermission('RETRIEVE')">{{item.text}}</a>
            </span>
            <a  ref="downTwo" @click="saveSceneImage()" class="btn-down-pic" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
            </div>
                <div id="tu1" style="height:435px;">
                  <div style="height:435px; width:100%">
                    <echarts-one :xAxisList="sceneOption.xAxisList"  :list="sceneOption.list" ref="lineTwo" v-if="sceneOption.lineShow"></echarts-one>
                  </div>
                </div>
            </div>
            <div class="tjt-right">
              <div  style="height:435px; width:100%">
                  <echarts-two :list="sceneOption.listPie" v-if="sceneOption.pieShow"></echarts-two>
              </div>
              <div class="tu2-title" v-if="sceneOption.pieShow"><p></p></div>
              <div class="count" v-if="sceneOption.pieShow"><p>{{sceneOption.listPieCount}}</p></div>
            </div>
          </div>
        </div>
      </div>
   </div>
</template>
<script>
import echartsOne from "@/views/xinjiang/common/components/lineEchart/line";
import echartsTwo from "@/views/xinjiang/common/components/pieEchart/normalPie";
import mixins from "@/views/xinjiang/reportNew/mixins/mixins";
export default {
  mixins: [mixins],
  /*data() {
    let vm = this;
    return {
      areaId: vm.$areaId,
      sceneCodeEnumList: [],
      areaOption: {
        lineShow: false,
        pieShow: false,
        xAxisList: [],
        list: [],
        listPie: [],
        listPieCount: 0,
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        treeList: [],
        dataSearch: {},
        endOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.areaOption.dataSearch.StartTime))
              return (
                new Date(this.areaOption.dataSearch.StartTime).getTime() >
                date.getTime()
              );
          }
        },
        startOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.areaOption.dataSearch.EndTime))
              return (
                new Date(this.areaOption.dataSearch.EndTime).getTime() <
                date.getTime()
              );
          }
        }
      },
      sceneOption: {
        lineShow: false,
        pieShow: false,
        xAxisList: [],
        list: [],
        listPie: [],
        listPieCount: 0,
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        treeList: [],
        dataSearch: {},
        endOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.sceneOption.dataSearch.StartTime))
              return (
                new Date(this.sceneOption.dataSearch.StartTime).getTime() >
                date.getTime()
              );
          }
        },
        startOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.sceneOption.dataSearch.EndTime))
              return (
                new Date(this.sceneOption.dataSearch.EndTime).getTime() <
                date.getTime()
              );
          }
        }
      }
    };
  },
  created() {
    this.getTree();
    this.getSceneEnum();
  },
  mounted() {
    this.areaOption.dataSearch.AreaCode = this.areaId;
    this.sceneOption.dataSearch.AreaCode = this.areaId;
    this.getSnapData(this.areaOption.dataSearch);
    this.getScene(this.sceneOption.dataSearch);
  },
  methods: {
    saveImage() {
      this.$refs.line.saveImg().then(e => {
        this.$refs.down.href = e;
        this.$refs.down.download = ".png";
      });
      return false;
    },
    saveSceneImage() {
      this.$refs.lineTwo.saveImg().then(e => {
        this.$refs.down.href = e;
        this.$refs.down.download = ".png";
      });
      return false;
    },
    getSceneEnum() {
      this.$http.get("vcpsystem/dict/findByType?type=scene").then(e => {
        this.sceneCodeEnumList = [...e.data.data];
        this.sceneCodeEnumList.unshift({
          label: "",
          value: ""
        });
      });
    },
    getScene(_data) {
      this.getHttp({
        elem: "sceneOption",
        url: `vcpinterface/ConvCompHistory/StatisticsBySceneCode`,
        _data: _data,
        echartName: "scene",
        echartData: "sceneNumList"
      });
    },
    getSnapData(_data) {
      this.getHttp({
        elem: "areaOption",
        url: `vcpinterface/ConvFaceReceive/Statistics`,
        _data: _data
      });
    },*/
   /* getHttp(_option = {}) {
      /**
       * _option:{
       * elem:"xxxx",areaOption,
       * loadElem:"xxx"dom
       * methods:"post",
       * url:"xxx",
       * _data:{}
       * echartName"xxx"echarts
       * echartData"xxx"echarts
       * echartCount"xxx"echarts 
       * }
       */
     /* let option = JSON.parse(JSON.stringify(_option));
      this[option.elem].pieShow = false;
      this[option.elem].lineShow = false;
      this.$http
        .setTargetLoadType(option.loadElem || ".report")
        [option.methods || "post"](option.url, option._data || {})
        .then(response => {
          let _data = response.data.Res || [];
          this[option.elem].xAxisList = _data.dateArray;
          this[option.elem].listPieCount = _data.totalData.total;
          this[option.elem].list = _data.dataList.map((item, index, arr) => {
            let newArr = {
              name: item[option.echartName || "address"],
              data: item[option.echartData || "addressNumList"]
            };
            return newArr;
          });
          this[option.elem].listPie = _data.totalData.array.map(
            (item, index, arr) => {
              let newArr = {
                name: item[option.echartName || "address"],
                value: item[option.echartCount || "count"]
              };
              return newArr;
            }
          );
          this[option.elem].pieShow = true;
          this[option.elem].lineShow = true;
        });
    },
    getTree(_data) {
      this.$http.get(`regisger-object/area-tree`, _data).then(response => {
        let newResponse = [];
        response.data.find(e => {
          if (e.id == this.areaId || e.parentId == this.areaId) {
            newResponse.push(e);
          }
        });
        console.log("ded", newResponse);
        let newArr = newResponse.map((item, index, arr) => {
          let newArr = {
            label: item.name,
            value: item.id,
            id: item.id,
            parentId: item.parentId
          };
          return newArr;
        });

        this.areaOption.treeList = this.$util.listToTree(newArr, {
          code: "id",
          parent: "parentId",
          children: "children"
        });
      });
    },
    search(_index, _data) {
      if (this.$util.isEmpty(_index)) {
        this.areaOption.searchList.forEach(e => {
          e.active = false;
        });
      } else {
        this.areaOption.searchList.forEach(e => {
          e.active = false;
        });
        this.$set(this.areaOption.searchList[_index], "active", true);
        this.areaOption.dataSearch.TimeType = _data;
      }
      this.getSnapData(this.areaOption.dataSearch);
    },
    searchScene(_index, _data) {
      if (this.$util.isEmpty(_index)) {
        this.sceneOption.searchList.forEach(e => {
          e.active = false;
        });
      } else {
        this.sceneOption.searchList.forEach(e => {
          e.active = false;
        });
        this.$set(this.sceneOption.searchList[_index], "active", true);
        this.sceneOption.dataSearch.TimeType = _data;
      }
      this.getScene(this.sceneOption.dataSearch);
    },
    handleChange(value) {
      this.areaOption.dataSearch.AreaCode = value[value.length - 1];
    },
    handleSceneChange(value) {
      this.sceneOption.dataSearch.AreaCode = value[value.length - 1];
    }
  },*/
  mounted(){
    this.getAreaData(this.areaOption.dataSearch);
  },
  methods:{
     getAreaData(_data) {
      this.getHttp({
        elem: "areaOption",
        url: `vcpinterface/ConvFaceReceive/Statistics`,
        _data: _data
      });
    },
  },
  components: {
    "echarts-one": echartsOne,
    "echarts-two": echartsTwo
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.tjt-right {
  width: 66%;
}
.tjt-left {
  width: 34%;
}
.sub-page .t-head,
.base-search {
  text-align: left;
}
.bottom-main{
  margin-top: 20px;
}
</style>



