<template>
  <div ref="myChart" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
export default {
  name: "guTags",
  data() {
    // 
    let echarts = require("echarts/lib/echarts");
    // 
    require("echarts/lib/chart/bar");
    // title
    require("echarts/lib/component/tooltip");
    require("echarts/lib/component/title");
    return {
      data: [],
      arr: [],
      date: []
    };
  },
  mounted() {
    this.getData();
  },
  created() {},
  methods: {
    getData() {
      this.$http.get("/big-screen/assess").then(response => {
        var arr1 = this.$util.resolveObject(response.data.faceAssessBean);
        var arr2 = this.$util.resolveObject(response.data.carAssessBean);
        this.date.push(arr1.keys);
        this.date.push(arr2.keys);
        this.data.push(arr1.values);
        this.data.push(arr2.values);
        console.log(this.date)
        this.drawBar();
      });
    },
    drawBar() {
      // domecharts
      let myChart = this.$echarts.init(this.$refs.myChart, "shine");
      // 
      var option = {
        color: ["#ffdb5c", "#67e0e3"],
        title: {
          text: "",
          textStyle: {
            fontSize: 12,
            padding: "50px",
            color: "#ffffff"
          }
        },
        legend: {
          orient: "horizontal",
          left: "center",
          data: ["A", "B"],
          top: "top",
          itemWidth: 5,
          itemHeight: 4,
          formatter: "{name}",
          textStyle: {
            color: "#FFFFFF"
          }
        },
        grid: {
          top: "15%",
          bottom: "15%"
        },
        tooltip: {},
        xAxis: {
          name: "",
          data: this.date[0],
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff"
            }
          },
          axisLine: {
            lineStyle: {
              color: "#ffffff"
            }
          }
        },
        yAxis: {
          splitArea: { show: false },
          splitLine: {
            //
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#ffffff"
            }
          },
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff"
            }
          }
        },

        series: [
          {
            barGap: "-5%",
            barWidth: 10,
            data: this.data[0],
            name: "faceAssessBean",
            type: "bar",
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#5fb5fe" },
                  { offset: 1, color: "#4c41fe" }
                ])
              }
            }
          },
          {
            barWidth: 10,
            data: this.data[1],
            name: "carAssessBean",
            type: "bar",
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#fefc5f" },
                  { offset: 1, color: "#fb8741" }
                ])
              }
            }
          }
        ]
      };
      myChart.setOption(option, true);
    }
  }
};
</script>
<style>
</style>
<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
  width: 99%;
}
</style>



