<template>
    <div class="sub-page">
		<!-- s -->
		<div class="left-menu" v-if="permissionResource.hasPermission('RETRIEVE')">
			<ul>
				<li v-for="(item,index) in searchList" :key="index">
          <a href="javascript:void(0)" :class="item.active?'selected':''" @click="search(index,item.value)">{{item.text}}</a>
        </li>
			</ul>
			<span class="b-01"></span>
			<span class="b-02"></span>
			<span class="b-03"></span>
			<span class="b-04"></span>
		</div>
		<!-- e -->
		<div class="zzjggl clearfix">
      <div class="panel">
        <div class="base-search">
          <label></label>
          <span class="m-input">
              <el-input v-model="dataSearch.name" placeholder=""></el-input>
          </span>
          <label></label>
          <span >
              <el-select 
              popper-class="m-select-con"
              v-model="dataSearch.type"
              placeholder="">
                  <el-option
                  
                  v-for="item in deviceTypeOptions "
                  :key="item.value"
                  :label="item.text"
                  :value="item.value">
                  </el-option>
              </el-select>
          </span>
          <a href="javascript:void(0)" class="btn-search"  @click="searchData()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
          <a href="javascript:void(0)" class="btn-search-more" @click="moreSearch()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
            <!-- <a href="javascript:void(0)" class="btn-del fr"></a>
            <a href="javascript:void(0)" class="btn-ok fr"></a>-->             
        </div>
        <div class="search-more base-search clearfix warn-base-search" v-if="more">
          <div class="search-more-item">
            <label></label>
            <span class="m-input">
                <el-input v-model="dataSearchMore.name" placeholder=""></el-input>
            </span>
          </div>
          <div class="search-more-item" >
            <label></label>
            <span >
                <el-select 
                  v-model="dataSearchMore.type"
                popper-class="m-select-con"
                placeholder="">
                    <el-option
                    v-for="item in deviceTypeOptions "
                    :key="item.value"
                    :label="item.text"
                    :value="item.value">
                    </el-option>
                </el-select>
            </span>
          </div>
          <div class="search-more-item">
            <label></label>
              <span class="m-input">
                <el-input v-model="dataSearchMore.organizationCode" placeholder=""></el-input>
            </span>
          </div>
          <div class="search-more-item">
            <label></label>
            <span class="m-input">
                <el-input v-model="dataSearchMore.brand" placeholder=""></el-input>
            </span>
          </div>
          <div class="search-more-item search-more-item-time" >
            <label></label>
            <span class="m-input-date">
              <el-date-picker
                v-model="dataSearchMore.startDate"
                popper-class="m-select-con"
                type="date"
                value-format="timestamp"
                :picker-options="startOptions"
                placeholder="">
              </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
              <el-date-picker
                v-model="dataSearchMore.endDate"
                value-format="timestamp"
                popper-class="m-select-con"
                :picker-options="endOptions"
                type="date"
                placeholder="">
              </el-date-picker>		
            </span>
            </div>
            <a href="javascript:void(0)" class="search-more-btn" @click="searchNormal()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>
            <a href="javascript:void(0)" class="search-more-btn btn-hidden" @click="moreSearch()"></a>
        </div>
        <div class="line"></div>
        <div class="panel-content">
            <table-grid :option="option" ref="tableGrid">
              <template slot-scope="row">
                  <el-button type="success" @click="openAddOrEditView(row.rowData)" size="mini" v-if="permissionResource.hasPermission('UPDATING')"></el-button>
                  <el-button type="warning" @click="delEmployee(row.rowData)" size="mini" v-if="permissionResource.hasPermission('DELETION')"></el-button>
              </template>
            </table-grid>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(
      vm.$route.path
    );
    return {
      permissionResource: permissionResource,
      more: false,
      constant: [],
      searchList: [],
      option: {
        isShowTableNav: false,
        hasCustomColum: true,
        tableOption: {
          singleSelect: false,
          isShowSelect: false,
          tableTitle: [
            { prop: "name", label: "" },
            { prop: "alarmType", label: "" },
            { prop: "type", label: "",
            format: (row, column, cellValue, index) => {
                return (
                  <div>
                    {
                     this.deviceTypeOptions.filter(e=>{
                        return e.value===row.type
                      })
                    }
                  </div>
                );
              } },
            { prop: "organizationCode", label: "" },
            { prop: "brand", label: "" },
            { prop: "locationInfo", label: "" },
            {
              prop: "alarmTime",
              label: "",
              format: (row, column, cellValue, index) => {
                return (
                  <div>
                    {this.$util.formatDateByArg(
                      row.alarmTime,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ]
        },
        customLabel: "",
        url: "/operation-log",
        type: "get",
        param: {},
        notRequestSever: false
      },
      deviceTypeOptions: [],
      dataSearch: {},
      dataSearchMore: {},
      searchAlarmType: {},
      endOptions: {
        disabledDate: date => {
          if (!this.$util.isEmpty(this.dataSearchMore.startDate))
            return (
              new Date(this.dataSearchMore.startDate).getTime() > date.getTime()
            );
        }
      },
      startOptions: {
        disabledDate: date => {
          if (!this.$util.isEmpty(this.dataSearchMore.endDate))
            return (
              new Date(this.dataSearchMore.endDate).getTime() < date.getTime()
            );
        }
      }
    };
  },
  created() {
    this.getConstant();
  },
  methods: {
    getConstant() {
      //
      this.$http.get(`/operation-log/constant`).then(response => {
        this.constant = response.data;
        this.getSearch();
        this.search(0, this.searchList[0].value);
      });
    },
    getSearch() {
      let newArr = this.constant.find(res => {
        //
        return res.id == "AlarmType";
      });
      let Type = this.constant.find(res => {
        //
        return res.id == "Type";
      });
      this.deviceTypeOptions = Type.options;
      this.deviceTypeOptions.unshift({
        value: "",
        text: ""
      });
      newArr.options.forEach((element, index) => {
        index == 0 ? (this.dataSearch.alarmType = element.value) : "";
        this.searchList.push(
          Object.assign(
            {
              key: element.value,
              index: index,
              active: index == 0 ? true : false
            },
            element
          )
        );
      });
    },
    moreSearch() {
      this.more = !this.more;
    },
    searchData() {
      this.dataSearchMore = {};
      this.$refs.tableGrid.searchTableData(
        Object.assign(this.dataSearch, this.searchAlarmType)
      );
    },
    searchNormal() {
      this.dataSearch = {};
      this.$refs.tableGrid.searchTableData(
        Object.assign(this.dataSearchMore, this.searchAlarmType)
      );
    },
    search(_data, _label) {
      this.searchList.forEach(e => {
        e.active = false;
      });
      this.$set(this.searchList[_data], "active", true);
      this.searchAlarmType.alarmType = _label;
      this.$refs.tableGrid.searchTableData(
        Object.assign(this.dataSearch, this.searchAlarmType)
      );
    }
  },
  components: {
    "table-grid": tableGrid
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
  background: url("../../../assets/img/login/bg1.jpg") center 0 no-repeat
    #051121;
  .left-menu {
    display: inline-block;
    height: calc(100vh - 120px);
    ul {
      width: 100%;
      height: calc(100% - 12px);
      overflow: auto;
    }
    li {
      text-align: left;
    }
  }
  .zzjggl {
    float: right;
    width: calc(100% - 190px);
    height: calc(100vh - 120px);
    overflow: hidden;
    .panel {
      height: 100%;
      width: calc(100% + 20px);
      padding-right: 21px;
      overflow: auto;
    }
  }
  .t-head,
  .base-search {
    text-align: left;
  }
  .search-more {
    display: inline-block;
    .search-more-item-time {
      width: 50%;
    }
  }
}
</style>
<style lang="scss">
.index-content .warn-base-search {
  .el-select {
    width: 100%;
  }
  .m-input .el-input input {
    width: 100%;
  }
}
</style>







