<template>
    <div  class="dialog b-dialog">
      <div class="form scrollbar tree-form">
        <syn-tree class="synTree" 
        :dataTree="treeOption.data" 
        :dataDefaultTree="treeOption.dataDefaultTree"
        @treeCheckData="treeCheckData"
        ref="trees"
        >
          <template slot="treeHandle" slot-scope="treeData">
              <span class="middle middle-one" v-if="treeData.data.menu===false" ></span>
              <span class="middle middle-two" v-else-if="treeData.data.menu===true" ></span>
              <span class="middle middle-three" v-else></span>
              <span class="right" >{{treeData.data.children?'':treeData.data.parentName}}</span>
              
          </template>
        </syn-tree>
      </div>
      <div class="btn-quit pt0 clearfix">
        <span class="all" ><el-checkbox  @change="setAll"></el-checkbox></span>
        <a href="javascript:void(0)" class="reset" @click="reset()"></a>
        <a href="javascript:void(0)" @click="submit()"></a>
      </div>
    </div>
</template>
<script>
import synTree from "@/views/xinjiang/common/components/tree/asideCheckTree";
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      dataForm: {},
      operations: ["DELETION", "RETRIEVE", "CREATION", "UPDATING"],
      operationsList: [],//
      treeOption: {
        dataDefaultTree: [],
        dataCheck: [],
        defaultData: [],
        data: []
      }
    };
  },
  created() {
    this.getConstant();
  },
  mounted() {
    this.expendFunction();
    this.getTree();
    if (this.$util.isEmpty(this.pData.data)) return;
    this.dataForm = Object.assign(this.dataForm, this.pData.data);
    this.getEditData();
  },
  methods: {
    getConstant() {
      //
      this.$http.get(`/standardOperation`).then(response => {
        this.operationsList = response.data;
      });
    },
    expendFunction() {
      //function
      Function.prototype.before = function(beforefn) {
        let _self = this; //
        return function() {
          beforefn.apply(this, arguments); //this
          return _self.apply(this, arguments); //
        };
      };
      Function.prototype.after = function(afterfn) {
        let _self = this;
        return function() {
          let ret = _self.apply(this, arguments); //return
          afterfn.apply(this, arguments);
          return ret;
        };
      };
    },
    getEditData() {
      if (this.$util.isEmpty(this.dataForm.totalResources)) return;
      this.dataForm.totalResources.forEach(e => {
        e.operations.forEach(child => {
          child.id = `${child.value}${e.permissionModule.id}`;
          child.parentId = `${e.permissionModule.id}`;
          this.treeOption.dataDefaultTree.push(child);
        });
        this.treeOption.dataDefaultTree.push(e.permissionModule);
      });
    },
    getTable(_data) {
      return new Promise(resolve => {
        this.$http
          .setTargetLoadType(".tree-form")
          .get(`/menu`, _data)
          .then(response => {
            resolve(response.data);
          });
      });
    },
    getTree() {
      let _arr = [];
      this.getTable().then(res => {
        let meunList = res.filter(e=>{
             return e.menu===true;
        });
        meunList.forEach(element => {
          _arr.push(element);
          if (element.menu) {
            this.operationsList.forEach(e => {
              let _obj = {};
              _obj.id = `${e.value}${element.id}`;
              _obj.parentId = element.id;
              _obj.parentName = element.name;
              _obj.value = e.value;
              _obj.text = e.text;
              _obj.name = `${e.text}`;
              _arr.push(_obj);
            });
          }
        });
        let prop = {
          code: "id",
          parent: "parentId",
          children: "children"
        };
        this.treeOption.defaultData = JSON.parse(JSON.stringify(_arr));
        this.treeOption.data = this.$util.listToTree(_arr, prop) || [];
        this.setParentName(this.treeOption.data);
      });
    },
    setParentName(_data) {
      _data.forEach(e => {
        if (
          Object.prototype.toString.call(e.children) === "[object Array]" &&
          e.children.length !== 0
        ) {
          e.children.forEach(children => {
            children.parentName = e.name;
          });
          this.setParentName(e.children);
        }
      });
    },
    setAll(_data) {
      if (!_data) {
        this.reset();
      } else {
        this.treeOption.defaultData.forEach(e => {
          this.$refs.trees.updatedData([e.id], true);
          this.treeOption.dataCheck.push(e);
        });
      }
    },
    reset() {
      this.treeOption.dataCheck = [];
      this.treeOption.defaultData.forEach(e => {
        this.$refs.trees.updatedData([e.id], false);
      });
    },
    treeCheckData(_arr, _parent, _checkdata, _node) {
      this.treeOption.dataCheck = _arr;
      this.findChild(_checkdata, _node, _arr);
    },
    findChild(_checkdata, _node, _arr) {
      let list = [];
      let arr = this.treeOption.defaultData;
      if (!_checkdata || !arr) return;
      arr.forEach(e => {
        if (e.parentId === _checkdata.parentId) {
          list.push(e);
        }
      });
      list.forEach(e => {
        if (_checkdata.value !== "RETRIEVE" && e.value === "RETRIEVE") {
          this.$refs.trees.updatedData([e.id], true);
          this.treeOption.dataCheck.push(e);
          return;
        }
      });
      if (_checkdata.value === "RETRIEVE") {
        let list = [];
        let _parentList = _node.halfCheckedNodes.filter(e => {
          return e.id == _checkdata.parentId;
        });
        this.deepChildNodeKey(list, _parentList);
        list.forEach((child, index) => {
          if (child.value !== "RETRIEVE") {
            this.$refs.trees.updatedData([child.id], false);
            this.treeOption.dataCheck.forEach((filter, index) => {
              if (filter.id === child.id) {
                this.treeOption.dataCheck.splice(index, 1);
              }
            });
          } else if ( child.value === "RETRIEVE" && _checkdata.parentId !== child.parentId) {
            this.$refs.trees.updatedData([child.id], false);
            this.treeOption.dataCheck.forEach((filter, index) => {
              if (filter.id === child.id) {
                this.treeOption.dataCheck.splice(index, 1);
              }
            });
          }
        });
      }
    },
    deepChildNodeKey(list = [], _data) {
      if (!Array.isArray(_data) && _data.length > 0) return;
      _data.forEach(e => {
        if (
          !this.$util.isEmpty(e.children) &&
          typeof e.children !== "undefined"
        ) {
          list.push(e);
          this.deepChildNodeKey(list, e.children);
        } else {
          list.push(e);
        }
      });
      return list;
    },
    submit() {
      this.ok.before(() => {
        this.dataForm.totalResources = [];
        let prop = {
          code: "id",
          parent: "parentId",
          children: "children"
        };
        let _arr = JSON.parse(JSON.stringify(this.treeOption.dataCheck));
        let _newArry = this.$util.listToTree(_arr, prop) || [];
        this.formatBtnPermission(_newArry);
        let list = [],
          _newList = [];
        this.treeToList(list, _newArry);
        list.forEach(e => {
          if (!e.text && this.operations.indexOf(e.value) === -1) {
            _newList.push(e);
          }
        });
        _newList.forEach(e => {
          let _obj = {};
          _obj["permissionModule"] = {
            identification: e.identification
          };
          _obj["operations"] = [];
          _obj["operations"] = e.operations;
          this.dataForm.totalResources.push(_obj);
        });
      })();
    },
    formatBtnPermission(_data) {
      let operations = this.operations;
      _data.forEach(e => {
        if (!this.$util.isEmpty(e.children)) {
          e.operations = [];
          e.children.forEach(child => {
            if (child.text && operations.indexOf(child.value) !== -1) {
              e.operations.push(child.value);
            }
          });
          this.formatBtnPermission(e.children);
        }
      });
    },
    treeToList(list = [], tree) {
      if (!Array.isArray(tree) && tree.length > 0) return;
      let _arr = [...tree];
      _arr.forEach(e => {
        list.push(e);
        if (typeof e.children !== "undefined") {
          this.treeToList(list, e.children);
        }
      });
      return list;
    },
    ok() {
      this.$http
        .setTargetLoadType(".b-dialog")
        [this.pData.methods || "post"](this.pData.url, this.dataForm)
        .then(response => {
          this.$emit("result", true, response);
        });
    }
  },
  components: {
    "syn-tree": synTree
  }
};
</script>
<style scoped lang="scss">
@import "../../../../assets/css/style1.css";
.dialog {
  height: 500px;
  .all {
    float: left;
  }
  .btn-quit a {
    width: 80px;
    background-color: #ffa756;
    height: 30px;
    line-height: 30px;
    vertical-align: middle;
  }
  .btn-quit .reset {
    background-color: #329df2;
    margin-right: 20px;
  }
  .btn-quit {
    position: relative;
    text-align: right;
    height: 60px;
    line-height: 60px;
    margin: 0 28px;
    padding: 0;
    box-sizing: border-box;
    border-top: 1px solid #3c85f2;
  }
}
</style>
<style lang="scss">
.b-dialog .tree-form {
  height: calc(100% - 60px) !important;
  padding: 0 !important;
  padding-top: 10px;
  text-align: left;
  margin-top: 10px;
  overflow: auto;
  .synTree {
    height: 100%;
    max-height: none;
  }
}
</style>





