<template>
  <div class="home-content">
    <!-- s -->
    <div class="right">
      <!-- s -->
      <div class="box box-04">
        <box-right-one :list="boxRightOne" @click.native="toCompairson"></box-right-one>
        <div class="fr icon-warning">
          <img src="../../../assets/img/login/icon_warning.png" alt />
        </div>
      </div>
      <!-- e -->
      <!-- s -->
      <div class="box box-05">
        <box-right-two :list="boxRightTwo"></box-right-two>
      </div>
      <!-- e -->
      <!-- s -->
      <div class="box box-06">
        <div class="box-title"></div>
        <div class="tjt" style="height:135px; width:97px">
          <box-right-three :list="pie.list" v-if="pie.show"></box-right-three>
        </div>
        <div class="content">
          {{pie.total}}
          <dl>
            <dt>
              <i class="square bg-green"></i>
              {{pie.data.faceNum}}
              {{isNaN(pie.data.faceNum/pie.total)?0:Math.round((pie.data.faceNum/pie.total)*100)}}%
            </dt>
            <dd></dd>
          </dl>
          <dl>
            <dt>
              <i class="square bg-red"></i>
              {{pie.data.carNum}}
              {{isNaN(pie.data.carNum/pie.total)?0:Math.round((pie.data.carNum/pie.total)*100)}}%
            </dt>
            <dd></dd>
          </dl>
        </div>
      </div>
      <!-- e -->
    </div>
    <!-- e -->
    <!-- s -->
    <div class="left">
      <!-- s -->
      <div class="box box-01">
        <box-left-one :list="boxLeftOne"></box-left-one>
      </div>
      <!-- e -->
      <!-- s -->
      <div class="box box-02">
        <div class="box-title"></div>
        <div class="tjt tjt-01">
          <div style="height:160px; width:165px">
            <box-left-three
              :list="insertDeviceData.deviceType"
              v-if="insertDeviceData.deviceType.length"
              ref="deviceType"
            ></box-left-three>
          </div>
          <div class="content content1" v-if="insertDeviceData.deviceType.length">
            <p>{{insertDeviceData.deviceType[0].value}} ({{insertDeviceData.deviceType[0].percent}})</p>
            <p>{{insertDeviceData.deviceType[1].value}} ({{insertDeviceData.deviceType[1].percent}})</p>
            <p>{{insertDeviceData.deviceType[2].value}} ({{insertDeviceData.deviceType[2].percent}})</p>
          </div>
        </div>
        <div class="tjt tjt-02">
          <div id="tu3" style="height:160px; width:165px">
            <!-- <box-left-three></box-left-three> -->
            <device-status-pie :list="insertDeviceData.deviceStaus" ref="deviceStatus" v-if="insertDeviceData.deviceStaus.length"></device-status-pie>
          </div>
          <div class="content" v-if="insertDeviceData.deviceStaus.length">
             {{insertDeviceData.deviceStaus[0].count}}
            <p>
              <i class="square bg-light-blue"></i>{{insertDeviceData.deviceStaus[0].value}}
            </p>
            <p>
              <i class="square bg-gray"></i>{{insertDeviceData.deviceStaus[1].value}}
            </p>
          </div>
        </div>
        <div class="tjt tjt-03">
          <div id="tu4" style="height:160px; width:165px">
            <box-left-three  :list="insertDeviceData.deviceBrand" :radius="18" v-if="insertDeviceData.deviceBrand.length" ref="deviceBrand"></box-left-three>
          </div>
          <div class="content content3" v-if="insertDeviceData.deviceBrand.length">
            <p v-for="(item , index) in insertDeviceData.deviceBrand" :key="'deviceBrand'+index">
              {{item.name}}{{item.value}}({{item.percent}})
            </p>
          </div>
        </div>
      </div>
      <!-- e -->
      <!-- s -->
      <div class="box box-03">
        <div class="box-title"></div>
        <div class="table">
          <div class="table-head">
            <box-left-two></box-left-two>
          </div>
        </div>
        <div class="fr icon-warning">
          <img src="../../../assets/img/login/icon_warning.png" alt />
        </div>
      </div>
      <!-- e -->
    </div>
    <!-- e -->
    <!-- s -->
    <div class="center">
      <div class="map" style="height:538px;width:527px">
        <fly-line-map @clickMap="clickMap"></fly-line-map>
      </div>
      <!--527px538px-->
    </div>
  </div>
</template>
<script>
import boxLeftOne from "@/views/xinjiang/home/components/boxOne";
import boxLeftTwo from "@/views/xinjiang/home/components/wrap";
import boxLeftThree from "@/views/xinjiang/home/components/pieTwo";
import boxRightOne from "@/views/xinjiang/home/components/boxTwo";
import boxRightTwo from "@/views/xinjiang/home/components/boxThree";
import boxRightThree from "@/views/xinjiang/home/components/pie";
import flyLineMap from "@/views/xinjiang/common/components/flyLineMap";
import deviceStatusPie from "@/views/xinjiang/home/components/deviceStatusPie";
import { imageBaseUrl } from "@/utils/global";
export default {
  name: "home",
  data() {
    let vm = this;
    return {
      pie: {
        total: 0,
        data: {},
        show: false,
        list: [
          {
            value: 0,
            key: "carNum",
            name: ""
          },
          {
            value: 0,
            key: "faceNum",
            name: ""
          }
        ]
      },

      boxLeftOne: {
        title: "",
        lists: [
          {
            label: "",
            key: "preNum",
            value: 0
          },
          {
            label: "",
            value: 0,
            key: "alreadyNum",
            class: "f-green"
          },
          {
            label: "",
            value: 0,
            key: "blockNum",
            class: "f-red"
          }
        ]
      },
      boxRightOne: {
        title: "",
        show: true,
        lists: [
          {
            label: "",
            key: "faceImage",
            image: "" //require("../../../assets/img/login/temp_011.jpg")
          },
          {
            label: "",
            key: "carImage",
            image: require("../../../assets/img/login/temp_021.jpg")
          }
        ]
      },
      boxRightTwo: {
        title: "",
        lists: [
          {
            label: "",
            value: "0",
            image: require("../../../assets/img/login/icon_01.png"),
            class: "f-yellow"
          },
          {
            label: "",
            value: "0",
            key: "contrastNum",
            image: require("../../../assets/img/login/icon_02.png"),
            class: "f-yellow"
          },
          {
            label: "",
            value: "0",
            key: "factoryCallsNum",
            image: require("../../../assets/img/login/icon_03.png"),
            class: "f-yellow"
          }
        ]
      },
      websock: null,
      insertDeviceData: {
        isRequest: false,
        deviceStaus: [],
        deviceBrand: [],
        deviceType: []
      },

      areaId: vm.$areaId
    };
  },
  mounted() {
    this.getRegister(this.areaId);
    this.getDataForward(this.areaId);
    this.getReceiveForward(this.areaId);
    //this.getRandomImage();
    this.getInsertDevice(this.areaId);
    this.getInitCompare();
    this.initWebSocket();
  },
  methods: {
    /**
     * 
     * areaId  id
     */
    clickMap(areaId) {
      this.areaId = areaId;
      this.getRegister(areaId);
      this.getDataForward(areaId);
      this.getReceiveForward(areaId);
      this.getInsertDevice(areaId);
    },
    getRegister(_data) {
      this.$http
        .get(`/big-screen/device-register-info/${_data}`)
        .then(response => {
          this.$dealData.responDataToFormData(
            response.data,
            this.boxLeftOne.lists
          );
        });
    },
    getDataForward(_data) {
      /* this.$http.get(`/big-screen/data-forward/${_data}`).then(response => {
        this.$dealData.responDataToFormData(
          response.data,
          this.boxRightTwo.lists
        );
        this.boxRightTwo.lists[0].value = Math.round(Math.random() * 300);
      }); */
      this.$http
        .post(`/vcpinterface/ConvCompHistory/FindCompCount`, {
          AreaCode: _data
        })
        .then(response => {
          for (let i = 0; i < this.boxRightTwo.lists.length; i++) {
            this.boxRightTwo.lists[i].value = response.data.Res.total;
          }
        });
    },
    getReceiveForward(_data) {
      /* this.$http.get(`/big-screen/date-receive/${_data}`).then(response => {
        this.pie.data = response.data;
        debugger;
        console.log(this.pie);
        this.pie.total = this.getTotalData(this.pie.data);
        this.$dealData.responDataToFormData(response.data, this.pie.list);
        console.log(this.pie.list);
        this.pie.show = true;
      }); */

      this.$http
        .post(`/vcpinterface/ConvFaceReceive/FindCatchCount`, {
          AreaCode: _data
        })
        .then(response => {
          this.pie.data.faceNum = response.data.Res.total;
          this.$http
            .post("/vcpinterface/ConvVehicle/FindCarCount", { AreaCode: _data })
            .then(res => {
              this.pie.data.carNum = res.data.Res.total;
              this.pie.total = this.getTotalData(this.pie.data);
              this.$dealData.responDataToFormData(this.pie.data, this.pie.list);
              this.pie.show = true;
            });
        });
    },
    getTotalData(_data) {
      let count = 0;
      for (let key in _data) {
        count += _data[key];
      }
      return count;
    },
    getRandomImage() {
      if (this.boxRightOne.show) {
        setTimeout(() => {
          let dataRandom = this.getRandomNumber(3, 1);

          let _data = {
            faceImage: require(`../../../assets/img/login/temp_01${dataRandom}.jpg`),
            carImage: require(`../../../assets/img/login/temp_02${dataRandom}.jpg`)
          };
          this.boxRightOne.lists[1].image = _data.carImage;
          /* this.$dealData.responDataToFormData(
            _data,
            this.boxRightOne.lists,
            "image"
          ); */
          this.getRandomImage();
        }, 5000);
      }
    },
    getRandomNumber(max, min) {
      let x;
      for (let i = 0; i < 10; i++) {
        x = Math.floor(Math.random() * (max - min + 1)) + min;
      }
      return x;
    },
    getInitCompare() {
      if (this.$util.isEmpty(this.boxRightOne.lists[0].image)) {
        this.$http
          .post("/vcpinterface/ConvCompHistory/FindPage")
          .then(response => {
            response = response.data.Res;
             if (response.Data.length ==0) {
                 this.boxRightOne.lists[0].image=require("../../../assets/img/login/temp_011.jpg");
             }else{
                this.boxRightOne.lists[0].image=imageBaseUrl+response.Data[0].FaceID + ".jpg";
             }
          });
      }
    },
    getInsertDevice(_data) {
      this.$http.get(`/big-screen/device-access-info/${_data}`).then(res => {
        this.dealDeviceStuasData(res.data.network);
        this.dealDeviceType(res.data.type);
        this.dealDeviceBrand(res.data.brandBeans);
        this.$nextTick(() => {
          this.$refs.deviceBrand.setData(this.insertDeviceData.deviceBrand);
          this.$refs.deviceType.setData(this.insertDeviceData.deviceType);
          this.$refs.deviceStatus.getData();
        });
      });
    },

    dealDeviceStuasData(data) {
      let count = data.onlineNum + data.offlineNum;
      this.insertDeviceData.deviceStaus = [
        {
          name: "",
          value: data.onlineNum,
          count: count
        },
        {
          name: "",
          value: data.offlineNum,
          count: count
        }
      ];
    },

    dealDeviceBrand(data) {
      let count = 0 ; 
      let deviceData = [];
      for(let i = 0 ; i < data.length; i++){
        switch(data[i].name){
          case '' : 
          case '' : 
          case '' : 
          case '' :
            count = count + data[i].value;
            deviceData.push(data[i]);
            break; 
        }
      }
      for(let i = 0 ; i < deviceData.length; i++){
        
        deviceData[i].percent = (deviceData[i].value / count *100).toFixed(2) + "%"
      }
      this.insertDeviceData.deviceBrand =  deviceData;
      // let count = data.dahua + data.haikang + data.huawei + data.yitu;
      // this.insertDeviceData.deviceBrand = [
      //   {
      //     name: "",
      //     value: data.dahua,
      //     percent: (data.dahua / count * 100).toFixed(2) + "%"
      //   },
      //   {
      //     name: "",
      //     value: data.haikang,
      //     percent: (data.haikang / count * 100).toFixed(2) + "%"
      //   },
      //   {
      //     name: "",
      //     value: data.huawei,
      //     percent: (data.huawei / count * 100).toFixed(2) + "%"
      //   },
      //   {
      //     name: "",
      //     value: data.yitu,
      //     percent: (data.yitu / count * 100).toFixed(2) + "%"
      //   }
      // ];
    },

    dealDeviceType(data) {
      let count =
        data.intelligentIintegrationMachine +
        data.smartCamera +
        data.vehicleFrontEndCamera;
      this.insertDeviceData.deviceType = [
        {
          name: "",
          value: data.intelligentIintegrationMachine,
          percent:
            (data.intelligentIintegrationMachine / count * 100).toFixed(2) + "%"
        },
        {
          name: "",
          value: data.smartCamera,
          percent: (data.smartCamera / count * 100).toFixed(2) + "%"
        },
        {
          name: "",
          value: data.vehicleFrontEndCamera,
          percent: (data.vehicleFrontEndCamera / count * 100).toFixed(2) + "%"
        }
      ];
    },
    initWebSocket() {
      //weosocket
      const wsuri = this.$wsUrl;
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //send
      console.log("websocket");
    },
    websocketonerror() {
      //
      this.initWebSocket();
      console.log("websocket");
    },
    websocketonmessage(data) {
      //
      let host = imageBaseUrl;
      try {
        let redata = JSON.parse(data.data);
        if (
          redata.AreaCode.indexOf(this.areaId) != -1 ||
          (this.areaId == "6504" && redata.AreaCode.indexOf("652122") != -1)
        ) {
          if (redata.Type == "ExistentHitFace") {
            this.boxRightOne.lists[0].image = host + redata.HitFace.HitFaceUri;
          }
        }
      } catch (e) {
        console.log("" + data.data);
      }
    },
    websocketsend(Data) {
      //
      //this.websock.send(Data);
    },
    websocketclose(e) {
      //
      console.log("", e);
    },

    toCompairson() {
      this.$router.push({ path: "/newIndex/newComparison/newComparisonHit" });
    }

    // getCount(data){
    //   let count = 0 ;
    //   for(let i = 0 ; i < data.length; i++){
    //     count = count + data[i].value;
    //   }
    //   return count;
    // }
  },
  beforeDestroy() {
    if (this.websock) {
      this.websock.close();
    }
  },
  components: {
    "box-left-one": boxLeftOne,
    "box-left-two": boxLeftTwo,
    "box-left-three": boxLeftThree,
    "box-right-one": boxRightOne,
    "box-right-two": boxRightTwo,
    "box-right-three": boxRightThree,
    "fly-line-map": flyLineMap,
    "device-status-pie": deviceStatusPie
  }
};
</script>
<style lang="scss">
.sub-page-content {
  background: url("../../../assets/img/login/bg.jpg") center 0 no-repeat #051121;
}
</style>
<style scoped>
@import "../../../assets/css/style1.css";
.home-content {
  height: 100%;
}
.home .box .box-title {
  text-align: left;
}
.content1 {
  top: 10px !important;
  left: 0px !important;
}
.content3 {
  top: 0px !important;
  left: 35px !important;
}
.home .box-06 .content {
  text-align: left;
}
.home .center {
  margin-left: 350px;
  margin-right: 350px;
  width: calc(100% - 700px);
  min-height: 690px;
  padding-top: 14px;
  text-align: center;
}
.home .center .map {
  width: 527px;
  margin: 0 auto;
  position: relative;
  left: 0;
}
</style>



