<template>
    <div class='sysAdd'> 
        <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="120px">
            <el-form-item label="" prop="areaName">
                <el-input v-model="formData.areaName" class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="areaCode">
                <el-input v-model="formData.areaCode"  class="j-input" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item label="" prop="address">
                <el-input v-model="formData.address "  class="j-input" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item label=""  prop="serverName"> 
                <el-input v-model="formData.serverName"  class="j-input" placeholder=""></el-input> 
            </el-form-item> 
            <el-form-item label="IP"  prop="ip"> 
                <el-input v-model="formData.ip"  class="j-input" placeholder=""></el-input> 
            </el-form-item> 
            <el-form-item label="" prop="status"> 
                <el-select v-model="formData.status" popper-class="m-select-con" placeholder="">
                    <el-option
                    v-for="item in sysStatus"
                    :key="item.value"
                    :label="item.text"
                    :value="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>  
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="equit"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
        </span>
	    <!-- <div class="btn-quit pt0"><a href="javascript:void(0)"> </a></div> -->
    </div>
</template>
<script>
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data(){
    return {
        formData:{
            areaName: '',
            areaCode: '',
            status: '',
            serverName: '', 
            address : ''
        },
        //
        rule1:{
          areaName: [{ required: true, message: "", trigger: "change" }],
          areaCode: [{ required: true, message: "", trigger: "change" }],
          address: [{ required: true, message: "", trigger: "change" }],
          status: [{ required: true, message: "", trigger: "change" }],
          serverName: [{ required: true, message: "", trigger: "change" }],
          ip: [{ required: true, message: "IP", trigger: "change" }] 
        },
        sysStatus:[{text: '', value:1},{text: '', value:0}]
    }
  },
  watch:{},
  computed:{},
  methods:{ 
    ok(){ 
        this.$refs.formData.validate((valid) => { 
            if(valid){
                let type = this.pData.type; 
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `/vcpalgaccess/server/save`,msg: ''}: {urlType: this.$http.put,url: `/vcpalgaccess/server/update`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime;
                dataOption.urlType(dataOption.url,this.formData).then(response =>{  
                    if(response.data.resultCd == 0){ 
                        this.$notify({
                            title: '',
                            message: dataOption.msg,
                            type: 'success'
                        }); 
                        this.$emit('equit');
                    }
                })
            } 
        })
    },
    equit(){
        this.$emit('equit');
    } 
  },
  created(){ 
    let type = this.pData.type; 
    switch (type) {
        case '':
            this.formData = {};
            break;
        case '':
            this.formData = this.pData.data;
            break;
        default:
            break;
    }  
  }
};
</script>
<style scoped lang="scss">
//@import "../../../../assets/css/style1.css";
/* .dialog{
    height: 366px;
} */
.sysAdd{
    text-align: center;
    .el-form{ 
        margin-top: 15px;
        .j-input{
            .el-input__inner{ 
                border: 1px solid #0e94d2;
            } 
        } 
    } 
    .textButton{ 
        .el-button{
            background: #1b4272;
            color:#fff;
        }
    }  
}

</style>




