<template>
    <div class='userOpt'> 
        <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="100px">
            <el-form-item label="" prop="account">
                <el-input v-model="formData.account" class="j-input" placeholder="" clearable ></el-input>
            </el-form-item>
            <el-form-item label="" v-if="weatherAdd" prop="password">
                <el-input :show-password='true' v-model="formData.password" type="password" class="j-input" placeholder="" clearable  ></el-input>
            </el-form-item> 
            <el-form-item label=""  prop="permissionRoles">
                <el-select popper-class="m-select-con" class='j-select' v-model="formData.permissionRoles"  clearable placeholder="">
                    <el-option v-for="item in constant.permissionRoles" :key="item.name" :label="item.name" :value="item.id">
                    </el-option>
                </el-select>  
            </el-form-item>
            <el-form-item label="" prop="enabled">
                <el-select popper-class="m-select-con" class='j-select' v-model="formData.enabled" clearable placeholder="">
                    <el-option v-for="item in constant.enabled" :key="item.label" :label="item.label" :value="item.value">
                    </el-option>
                </el-select> 
            </el-form-item>
            <el-form-item label=""  prop="email"> 
                <el-input v-model="formData.email"  class="j-input" placeholder="" clearable></el-input> 
            </el-form-item> 
            <el-form-item label=""  prop="phoneNumber"> 
                <el-input v-model="formData.phoneNumber"  class="j-input" placeholder="" clearable></el-input> 
            </el-form-item> 
            <!-- <el-form-item label="" class='is-required org' prop="organization">
              <el-cascader
                v-model="formData.organization"
                :options="orgOptions"  
                popper-class="m-select-con" 
                :props="orgProps"
                class='j-cascader'
                @change="handleChange"
                clearable></el-cascader>  
            </el-form-item> -->
            <el-form-item label="" prop="orgName">
              <el-input v-model="formData.orgName" :data-value='formData.organization' @click.native="clickControl" class="j-input" placeholder="" clearable></el-input>
              <el-tree 
                v-show="weatherTree"
                empty-text=""
                :highlight-current = true
                :default-expand-all = false
                :expand-on-click-node="true"
                :data="orgOptions"
                :props='treeOption'
                :filter-node-method="filterNode"
                @node-click="selectClassfy"
                class="j-tree"
                ref="selectTree">
              </el-tree> 
            </el-form-item>    
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="equit"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
        </span>
	    <!-- <div class="btn-quit pt0"><a href="javascript:void(0)"> </a></div> -->
    </div>
</template>
<script>
//import synTree from "@/views/xinjiang/common/components/tree/synTree";
import { validatePassword } from '@/utils/validate' 

const pwd = (rule, value, callback) => {
      if (!value) {
        callback(new Error(""));
      } else {    
        if(validatePassword(value, 3)){
            callback(new Error(validatePassword(value, 3)));
        }; 
        callback();  
      }
    };

export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  }, 
  components: {},
  data(){
    return {
      weatherTree: false,
      weatherRole: false,
      weatherAdd: true, 
      orgLinshi:{},
      permissionRoles: [],
      constant: {
          permissionRoles: [],
          enabled:[{label: '', value: true},{label: '', value: false}]
      },
      orgOptions: [],
      orgProps:{
        expandTrigger: 'click', 
        value: 'id',
        label: 'name' 
      }, 
      treeOption:{ 
        children: "children",
        label: "name" 
      }, 
      formData:{
        account: '',
        password: '',
        algVersion: '',
        email: '', 
        permissionRoles: [],
        organization : {},
        orgName: '',
        roleName: {},
      },
      //
      rule1:{
        account: [{ required: true, message: "", trigger: "change" }],
        password: [{ required: true, validator: pwd, trigger: "change" }],
        enabled: [{ required: true, message: "", trigger: "change" }],
        roleName: [{ required: true, message: "", trigger: "blur" }],
        orgName: [{ required: true, message: "", trigger: "change" }],
        algVersion: [{ required: true, message: "", trigger: "change" }],
        email: [{ required: true, message: "", trigger: "change" }],
        phoneNumber: [{ required: true, message: "", trigger: "change" }] 
      },  
      /* treeData: [{
          label: ' 1',
          children: [{
            label: ' 1-1',
            children: [{
              label: ' 1-1-1',
              children:[]
            }]
          }]
        }, {
          label: ' 2',
          children: [{
            label: ' 2-1',
            children: [{
              label: ' 2-1-1',
              children:[]
            }]
          }, {
            label: ' 2-2',
            children: [{
              label: ' 2-2-1',
              children:[]
            }]
          }]
        }, {
          label: ' 3',
          children: [{
            label: ' 3-1',
            children: [{
              label: ' 3-1-1',
              children:[]
            }]
          }, {
            label: ' 3-2',
            children: [{
              label: ' 3-2-1',
              children:[]
            }]
          }]
        }] */
    }
  },
  watch:{},
  computed:{},
  methods:{ 
    clickControl(){
      this.weatherTree = true;
    }, 
    selectClassfy(data) {  
        this.formData.orgName = data.name;
        this.formData.organization = {id: data.id}; 
        this.weatherTree = false;
    },  
    filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
    }, 
    handleChange(){},
    dataInit(){
        /* this.$nextTick(() =>{
            this.$refs.selectTree.getCheck(); 
        })  */   
        this.$http.get('/role/all').then(response =>{
            //this.constant.permissionRoles = response.data[0].options.map(item => (({name, id}) => ({name, id}))(item));
            this.constant.permissionRoles = response.data.map(item => (({name, id}) => ({name, id}))(item));
        })
        this.$http.get('/organization/only-org-tree').then(response=>{
            let list = response.data || [];
            let prop = {
                code: 'id',
                parent: 'parentId',
                children: 'children'
            }
            this.orgOptions = this.$util.listToTree(list,prop);// 
        });
    },
    clickTreeNode(data, node) {
      let param = {
        areaCode: data.id
      };
      this.treeNode = data;
      this.$util.clearObjectValue(this.searchForm);
      this.$refs.deviceList.searchTableData(param);
    },
    ok(){     
        this.$refs.formData.validate((valid) => { 
            if(valid){     
                this.formData.permissionRoles = [{id: this.formData.permissionRoles}];  
                let type = this.pData.type;  
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `/account`,msg: ''} : {urlType: this.$http.patch,url: `/account/${this.pData.data.id}`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime;
                dataOption.urlType(dataOption.url,this.formData).then(response =>{  
                    this.$notify({
                        title: '',
                        message: dataOption.msg,
                        type: 'success'
                    }); 
                    this.$emit('equit'); 
                })   
            } 
        })
    },
    equit(){
        this.$emit('equit');
    }, 
    /* selectClassfy(data) {
        this.formData.organization = data.label;
        this.weatherTree = false;
    },  */ 
    /* filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
    }  */
  },
  created(){ 
    let type = this.pData.type; 
    switch (type) {
        case '':
            this.formData = {};  
            this.weatherAdd = true; 
            break;
        case '':  
            this.formData = this.pData.data; 
            this.orgLinshi = this.formData.organization;
            this.roleLinshi = this.formData.permissionRoles; 
            this.formData.orgName = this.orgLinshi.name;
            this.formData.organization = {id: this.orgLinshi.id};  
            //this.$set(this.formData,'roleName', this.roleLinshi[0].identification);  
            if(this.roleLinshi.length !== 0){ 
                this.formData.permissionRoles = this.roleLinshi[0].id;
            } 
            this.weatherAdd = false; 
            break;
        default:
            break;
    }  
  },
  mounted(){
    this.dataInit(); 
  }
};
</script>
<style scoped lang="scss">
//@import "../../../../assets/css/style1.css";
/* .dialog{
    height: 366px;
} */
.userOpt{
    text-align: center;
    .el-form{ 
        margin-top: 15px;  
        .j-input{
            .el-input__inner{ 
                border: 1px solid #0e94d2;
            } 
            .input:-internal-autofill-selected {
                background-color: #0d254a!important; 
            }
        } 
        .j-tree{
            width: 212px;
            height: auto; 
            padding: 5px 0;
        }
        .j-select{
            width: 212px;
        } 
    } 
    .textButton{ 
        .el-button{
            background: #1b4272;
            color:#fff;
        }
    }  
} 
</style>




