<template>
  <div ref="tu" style="height:100%; width:100%"></div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array,
      default: () => {
        return [];
      }
    },
    radius : {
      type : Number,
      default : 38
    }
  },
  data() {
    return {};
  },
  mounted() {
    this.setData(this.list);
  },
  methods: {
    setData(data) {
      this.createPie(data);
    },

    createPie(data) {
      let mychart = this.$echarts.init(this.$refs.tu);
      let option = this.getOption(data);
      mychart.setOption(option);
    },

    getOption(data) {
      let count = 0;
      let legendData = [];
      let radius = this.radius;
      let series = [];
      let option = {
        color: ["#4ED0F4", "#676566","transparent","#4ED0F4","#4ED0F4"],
        tooltip: {
          trigger: "item", 
          formatter: "{b} : {c}"
        },
        legend:{
          show:false,
          icon:"none",
          left:0,
          textStyle:{
            color:"#fff",
            fontSize:9,
          },
          data: [],
        },
        series: []
      };
      for (let i = 0; i < data.length; i++) {
        count = data[i].value + count;
      }
      count = count + Number((count * 0.25).toFixed(0));

      for (let i = 0; i < data.length; i++) {
        legendData.push(data[i].name);
        let serieData = [];
        serieData.push({
          value: data[i].value,
          name : data[i].name,
        });
        serieData.push({
          value: Number((count*0.75).toFixed(0))-data[i].value,
        });
        serieData.push({ value: Number((count*0.25).toFixed(0))});
        let serie = {
          type: "pie",
          radius: [`${radius}%`, `${radius+10}%`],
          center: ["65%", "50%"],
          label: {
            show: false,
          },
          data:serieData
        };
        series.push(serie);
        radius = radius+12;
      }
      option.legend.data = legendData;
      option.series = series;
      //debugger;
      return option;
    }
  }
};
</script>



