<template>
  <div class="faceRetrieve">
    <div class='faceRetrieve-body'>
      <div class='faceRetrieve-body-header is-required'>
        <el-upload
          :on-change="changeUpload"
          action="" 
          :show-file-list="false"
          :auto-upload="false" 
          accept=".jpg,.jpeg,.png,.gif,.bmp,.pdf,.JPG,.JPEG,.PBG,.GIF,.BMP,.PDF"> 
          <img :src="searchForm.imgBase64" class="avatar"> 
        </el-upload>
        <div slot="tip" class="el-upload__tip"></div>
      </div> 
      <div class='faceRetrieve-body-center'>
        <el-form :model='searchForm' ref="formData" :rules="rule1" label-width="103px">
          <el-form-item label="" prop="repoId">
            <el-select v-model="searchForm.repoId" popper-class="m-select-con" placeholder="" clearable>
                <el-option
                  v-for="(item, index) in constant.lib"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
          </el-form-item>
          <!-- <el-form-item label="">
            <el-select v-model="searchForm.ger" popper-class="m-select-con" placeholder="">
                <el-option
                  v-for="(item, index) in constant.ger"
                  :key="index"
                  :label="item.text"
                  :value="item.value"
                ></el-option>
              </el-select>
          </el-form-item> -->
          <el-form-item label="" prop="score">
            <el-input v-model="searchForm.score" popper-class="m-input" clearable></el-input>
          </el-form-item>
        </el-form>
        <div class='faceRetrieve-body-bottom'>
          <el-button type="primary" icon="el-icon-search" @click="sumbit"></el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {imageBaseUrl}  from "@/utils/global"
import {baseUrl} from "@/utils/global"

export default {
  components:{},
  props:{},
  data(){
    return {
      //imageUrl: require('../../../assets/images/default_user_photo.png'),
      constant:{
        lib:[],
        ger:[
            {text: '',value: 0},{text: '',value: 1}
        ]
      },
      searchForm:{
        imgBase64: require('../../../assets/images/default_user_photo.png')
      },
      rule1:{
          score: [{ required: true, message: "", trigger: "change" }],
          repoId: [{ required: true, message: "", trigger: "change" }] 
      },
      fileReader: new FileReader(),//
    }
  },
  watch:{},
  computed:{},
  methods:{
    init(){
      this.$route.query.data ? (this.searchForm.imgBase64 = this.$route.query.data.FaceID,this.searchForm.score = 80) : this.searchForm = {}; 
      console.log(this.searchForm); 
    },
    dataInit(){
        this.$http.post(`/vcpface/facerepository/findByPage`).then(response =>{
            this.constant.lib = response.data.res.data.map(item => (({name, id}) => ({name, id}))(item)); 
            this.constant.lib = JSON.parse(JSON.stringify(this.constant.lib));
            console.log(this.constant.lib);
        })
    },
    changeUpload(file, fileList) {  
      if(file.size / 1024 / 1024 < 1){
        this.fileReader.readAsDataURL(file.raw);
        let base64Str = "";
        this.fileReader.onload = () => {
            //this.searchForm.imgBase64  = this.fileReader.result;
            this.$set( this.searchForm, 'imgBase64', this.fileReader.result);
        } 
      } else {
        this.$message({
            message: '1M!!',
            type: 'warning'
        });
      }
    },
    sumbit(){
      this.$refs.formData.validate((valid) => {  
        if(valid) {
          this.searchForm.imgBase64 = (this.searchForm.imgBase64.split(","))[1];
          this.$router.push({ path: '/newIndex/newFace/mainLib', query: {data: this.searchForm}});
            /* this.$http.post(`/vcpface/face/searchface`, this.searchForm).then(response =>{  
              this.$router.push({ path: '/newIndex/newFace/mainLib', query: {data: response.data.res.data}}); // this.searchForm
            }) */
          } 
      })
    } 
  },
  created(){
    //this.init();
    this.dataInit();
  },
  mounted(){}
}
</script>
<style lang="scss" scoped>
@import "../../../assets/css/style1.css"; 
.faceRetrieve{
  .faceRetrieve-body{ 
    width: 360px;
    height: 430px;
    margin: 50px auto; 
    border: 1px solid #0e94d2;
    border-radius: 12px;
    padding: 34px; 
    background-color: rgba(2,70,123,0.45); 
    box-shadow: 0 0 0 7px rgba(4,31,75,0.35) inset;
    .faceRetrieve-body-header{
      background: url(../../../assets/images/bg_photo.png) no-repeat;
      width: 157px;
      height: 187px; 
      margin: 0 auto;
      padding: 6px;
      cursor: pointer;
      position: relative;
      img{
        margin-left: 0px!important;
      }
      .el-upload--text{
        height: 147px;
        position: relative;
      }
      .el-upload__tip{
        font-size: 15px;
        color: #849bef;
        text-align: center;
        padding-top: 10px;
      } 
    } 
    .faceRetrieve-body-center{
      .el-form{
        margin-top: 22px;
        .el-form-item {
            margin-bottom: 18px;
            margin-left: -20px; 
            .el-select {
                height: 36px;
                line-height: 36px;
                padding-left: 0px;
                width: 157px; 
            }
            .el-input{
              width: 159px;
            } 
        }
      }
    } 
    .faceRetrieve-body-bottom{
      text-align: center;
      .el-button{
        width: 108px; 
        height: 40px;
        border-radius: 4px;
        font-size: 16px; 
      }
    } 
  }
} 
</style>


