import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/views/Error/404'
import dapin from '@/views/Dapin/index'
import newIndex from "@/views/xinjiang/index"
import newLogin from "@/views/xinjiang/login"
import newHome from '@/views/xinjiang/home/home';
import newAssetDistribution from '@/views/xinjiang/assetDistribution/assetDistribution';
import newWarn from '@/views/xinjiang/warn/warn';
import newReport from '@/views/xinjiang/reportNew/report';
import newSnapReport from '@/views/xinjiang/reportNew/snapReport';
import newDeviceReport from '@/views/xinjiang/reportNew/deviceReport';
import newCompareReport from '@/views/xinjiang/reportNew/compareReport';
import newCarReport from '@/views/xinjiang/reportNew/carReport';
import newComparison from '@/views/xinjiang/comparison/comparison';
import newComparisonHit from '@/views/xinjiang/comparison/comparisonHit';
import newDevice from '@/views/xinjiang/device/device';
import newSystem from '@/views/xinjiang/system/system';
import newSystemDictionary from '@/views/xinjiang/system/dictionaryManager';
import newSystemLog from '@/views/xinjiang/system/logManager';
import newSystemMenu from '@/views/xinjiang/system/menuManager';
import newSystemOrg from '@/views/xinjiang/system/orgManager';
import newSystemRole from '@/views/xinjiang/system/roleManager';
import newSystemUser from '@/views/xinjiang/system/userManager';
import faceCapture from '@/views/xinjiang/comparison/faceCapture';
import newAlarmRealTimeData from '@/views/xinjiang/alarmRealTimeData/alarmRealTimeData';

import newAnalysis from '@/views/xinjiang/analysis/analysis';
import newCompanyManager from '@/views/xinjiang/analysis/companyManager';
import newAnalysisManager from '@/views/xinjiang/analysis/analysisManager';
import newAreaManager from '@/views/xinjiang/analysis/areaManager';
import newIvuManager from '@/views/xinjiang/analysis/ivuManager';   
import newSystemManager from '@/views/xinjiang/analysis/systemManager';
import newFace from '@/views/xinjiang/faceLibrary/facelibMenu'
import newFacelibManager from '@/views/xinjiang/faceLibrary/faceLibManager'
import newFaceManager from '@/views/xinjiang/faceLibrary/faceManager'
import newFaceRetrieve from '@/views/xinjiang/faceLibrary/faceRetrieve'
import newFaceSynthesis from '@/views/xinjiang/comparison/faceSynthesis' 
import newFaceRetrieveMain from '@/views/xinjiang/faceLibrary/faceRetrieveMain'
import newTrajectory from '@/views/xinjiang/trajectory/trajectory'
import service from '@/views/xinjiang/accessService/service'
//import newFaceMap from '@/views/xinjiang/faceLibrary/map'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect: '/newIndex/home',
    },
    {
      path: '/login',
      name: '',
      component: newLogin
      
    },
    {
      path: '/404',
      name: 'notFound',
      component: NotFound
    }, {
      path: '/dapin',
      name: '',
      component: dapin,
      meta: {
        requireAuth: true,  // 
      }, 
    },{
      path: '/newIndex',
      name: '',
      component:newIndex,
      redirect: '/newIndex/home',
      children:[{
        path : '/newIndex/home',
        name : '',
        meta: {
          requireAuth: true,  // 
        }, 
        component: newHome
      },{
        path : '/newIndex/assetDistribution',
        name : '',
        meta: {
          requireAuth: true,  // 
        }, 
        component: newAssetDistribution
      },{
        path : '/newIndex/newWarn',
        name : '',
        meta: {
          requireAuth: true,  // 
        }, 
        component: newWarn
      },
      {
        path : '/newIndex/newReport',
        name : '',
        component: newReport,
       /*  redirect: '/newIndex/newReport/newSnapReport', */
        children:[
          {
            path:"/newIndex/newReport/newSnapReport",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component: newSnapReport
          },{
            path:"/newIndex/newReport/newDeviceReport",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newDeviceReport
          },{
            path:"/newIndex/newReport/newCompareReport",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newCompareReport
          },
          {
            path:"/newIndex/newReport/newCarReport",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newCarReport
          }
        ]
        },
        {
          path: '/newIndex/newFace',
          name: '',
          component: newFace,
        /*   redirect: '/newIndex/newFace/newFaceLibManager', */
          children: [
            {
              path: "/newIndex/newFace/newFaceLibManager",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newFacelibManager
            }, {
              path: "/newIndex/newFace/newFaceManager",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newFaceManager
            }, {
              path: "/newIndex/newFace/newFaceRetrieve",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newFaceRetrieve
            }, {
                path: '/newIndex/newFace/mainLib',
                name: '1',
                meta: {
                  requireAuth: true,  // 
                }, 
                component: newFaceRetrieveMain
            }/* , {
              path: "/newIndex/newFace/newFaceMap",
              name: '',
              component: newFaceMap
            } */
          ]
        },
        {
          path: '/newIndex/newAnalysis',
          name: '',
          component: newAnalysis,
        /*   redirect: '/newIndex/newAnalysis/newCompanyManager', */
          children: [
            {
              path: "/newIndex/newAnalysis/newCompanyManager",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newCompanyManager
            },
            {
              path: "/newIndex/newAnalysis/newAnalysisManager",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newAnalysisManager
            },
            {
              path: "/newIndex/newAnalysis/newAreaManager",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newAreaManager
            }, {
              path: "/newIndex/newAnalysis/newIvuManager",
              name: 'IVU',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newIvuManager
            }, {
              path: "/newIndex/newAnalysis/newSystemManager",
              name: '',
              meta: {
                requireAuth: true,  // 
              }, 
              component: newSystemManager
            }
          ]
        },
      {
        path : '/newIndex/newSystem',
        name : '',
        component: newSystem,
       /*  redirect: '/newIndex/newSystem/newSystemUser', */
        children:[
          {
            path:"/newIndex/newSystem/newSystemUser",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newSystemUser
          },{
            path: "/newIndex/newSystem/newSystemOrg",
            name: '',
            meta: {
              requireAuth: true,  // 
            }, 
            component: newSystemOrg
          },{
            path:"/newIndex/newSystem/newSystemRole",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newSystemRole
          },
          {
            path:"/newIndex/newSystem/newSystemMenu",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newSystemMenu
          },{
            path: "/newIndex/newSystem/newSystemDictionary",
            name: '',
            meta: {
              requireAuth: true,  // 
            }, 
            component: newSystemDictionary
          },{
            path:"/newIndex/newSystem/newSystemLog",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newSystemLog
          }
        ]
      },
      {
        path : '/newIndex/newComparison',
        name : '',
        component: newComparison,
       /*  redirect: '/newIndex/newComparison/newComparisonHit', */
        children:[
          {
            path:"/newIndex/newComparison/newComparisonHit",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component: newComparisonHit
          },{
            path: "/newIndex/newComparison/faceCapture",
            name: '',
            meta: {
              requireAuth: true,  // 
            }, 
            component: faceCapture
          },{
            path:"/newIndex/newComparison/newAlarmRealTimeData",
            name : '',
            meta: {
              requireAuth: true,  // 
            }, 
            component:newAlarmRealTimeData
          },{
            path: "/newIndex/newComparison/newFaceSynthesis",
            name: '',
            meta: {
              requireAuth: true,  // 
            }, 
            component: newFaceSynthesis
          },{
            path:"/newIndex/toNewTrajectory",
            name : '',
            redirect: '/newIndex/newTrajectory',
          }
        ]
      },{
        path: '/newIndex/newDevice',
        name : '',
        meta: {
          requireAuth: true,  // 
        }, 
        component : newDevice,
      },{
        path : '/newIndex/newTrajectory',
        name : '',
        meta: {
          requireAuth: true,  // 
        }, 
        component : newTrajectory
      },{
        path: '/newIndex/newService',
        name : '',
        component : service,
      }]
    }
  ]
})

export default router



