        </div> -->
        <div class="base-search">
            <label></label>
            <span class="m-input-date">
                <el-date-picker 
                        type="date"
                        popper-class="m-select-con"
                        placeholder=""
                        v-model="formData.BeginCreateTime"
                        :picker-options="dateTime.pickerOptionsStart"
                        :editable="false" 
                        @change="(value)=>changeTime(value,'startTime')" 
                    >
                    </el-date-picker>
            </span>
            <span class="mid-text"></span>
            <span class="m-input-date">
                <el-date-picker 
                        type="date"
                        popper-class="m-select-con"
                        placeholder="" 
                        :editable="false" 
                        v-model="formData.EndCreateTime"
                        :picker-options="dateTime.pickerOptionsEnd" 
                        @change="(value)=>changeTime(value,'endTime')"
                    >
                    </el-date-picker>
            </span>
            <label></label>
            <el-select popper-class="m-select-con" v-model="formData.AreaCode" clearable>
                <el-option 
                    :key="index" 
                    v-for="(item,index) in constant.areaCode" 
                    :label="item.name" 
                    :value="item.id"> 
                </el-option> 
            </el-select> 
            <label></label>
            <el-select popper-class="m-select-con" v-model="formData.DeviceID" clearable>
                <el-option 
                    :key="index" 
                    v-for="(item,index) in constant.DeviceID" 
                    :label="item.name" 
                    :value="item.deviceID"> 
                </el-option> 
            </el-select> 
            <a href="javascript:void(0)" class="btn-search" @click="search()"></a>
        </div>
         <div >
            <div class="face-list" v-loading="loading"> 
                <ul class="clearfix">
                    <li v-for="(item, key) in messeageList" :key="key" class='zp-face-img'>
                        <div class='msgInfo' @click="showpop(item)"> 
                            <img v-if="imgShow" :src="item.FaceImageUrl" />  
                            <div class="zp-title">
                                <el-tooltip :content="item.DeviceID" placement="top" popper-class="atooltip">
                                    <div>{{item.DeviceID}}</div>
                                </el-tooltip> 
                                <el-tooltip :content="item.AreaCode" placement="top" popper-class="atooltip">
                                    <div>{{item.AreaCode}}</div>
                                </el-tooltip>
                                <el-tooltip :content="item.LocationMarkTime" placement="top" popper-class="atooltip">
                                    <div>{{item.LocationMarkTime}}</div>
                                </el-tooltip>
                            </div>
                        </div>
                    </li> 
                </ul> 
            </div>
        </div> 
        <pagination :pData='pageOption' @init='init'/>
        <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
            <captureMsg :pData='captureInfo'/>
        </msgDialog>
   </div>
</template>
<script>
import pagination from '@/views/xinjiang/common/components/pagination'
import msgDialog from '@/views/xinjiang/common/components/dialog/msgDialog'
import captureMsg from '@/views/xinjiang/comparison/captureMsg'
import {imageBaseUrl}  from "@/utils/global"
import {dateArraySort} from '@/utils/datetime'
export default {
  data() {
    return { 
        loading: false,
        imgShow: false,
        imageBaseUrl:imageBaseUrl, 
        constant:{
            areaCode:[],
            DeviceID:[]
        },
        formData:{
            BeginCreateTime: '',
            EndCreateTime: '',
            areaCode: ''
        },
        dateTime: {
          pickerOptionsStart: {
            disabledDate: time => {
              if (this.formData.EndCreateTime) { 
                return time.getTime() > this.formData.EndCreateTime;
              }  
            }
          },
          pickerOptionsEnd: {
            disabledDate: time => {
              if (this.formData.BeginCreateTime) {  
                return time.getTime() < this.formData.BeginCreateTime ;
              }  
            }
          }
        }, 
        captureInfo:{
            capture: '',
            library: '',
            percent: '75%',
            data:{
                name: ''
            }
        },
        dialogVisible:{
            msgDialog: false
        },
        msgDialog:{
            title: '',
            dialogVisible: true,
            width: '600px' 
        }, 
        pageOption:{
            currentPageNum: 1,  
            pageSize: 10, 
            firstPage: 1,
            lastPage: 7, 
            total: 70, 
            url: 'vcpinterface/ConvFaceReceive/FindPage' 
        },
        messeageList:[
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
            {
                FaceImageUrl: '',
                ImageUrl: '',
                LocationMarkTime: '2019-08-31  12:45:46',
                deviceInfo: '',
                
            },
        ]
    };
  },
  components: { pagination, msgDialog, captureMsg },
  methods:{
      init(formData) { 
          this.loading = true; 
          this.imgShow = false;
          this.$http.post(`/vcpinterface/ConvFaceReceive/FindPage`, Object.assign( 
          {
            PageSize: 21
          },
          formData
        ),{headers:{"Content-Type": "application/json"}}).then(response =>{
              this.pageOption = {
                    currentPageNum: 1,  
                    pageSize: 21, 
                    firstPage: 1,
                    lastPage: (response.data.Res.totalRow % 21 == 0) ? response.data.Res.totalRow / 21 : parseInt(response.data.Res.totalRow / 0) + 1 , 
                    total: response.data.Res.totalRow, 
                    url: '/vcpinterface/ConvCompHistory/FindPage' 
              }   
              let Host = imageBaseUrl;
              let data = response.data.Res.data;
              let newArry = [];
              for(let i = 0; i < data.length; i++) {
                  data[i].FaceImageUrl = Host + data[i].FaceImageUrl;
                  data[i].ImageUrl = Host + data[i].ImageUrl;  
                  data[i].LocationMarkTime = ': ' + data[i].LocationMarkTime;
                  data[i].getMap = {AreaCode: data[i].AreaCode, DeviceID: data[i].DeviceID, imgBase64: data[i].FaceImageUrl};
                  this.$http.get(`/regisger-object/get-area-name/${data[i].AreaCode}`).then(response =>{
                      data[i].AreaCode = '' + response.data;
                  }); 
                  this.$http.get(`/regisger-object/device-id/${data[i].DeviceID}`).then(response =>{
                      data[i].DeviceID = '' + response.data
                  }); 
                  newArry.push(data[i]);
              }
              this.messeageList = dateArraySort(newArry, 'LocationMarkTime'); 
              console.log('tag', this.messeageList)
              this.loading = false;
              this.imgShow = true;
          })
      },
      search(){
          let data = JSON.parse(JSON.stringify(this.formData));  
          this.init(data);
      },
      dataInit(){ 
        this.$http.get(`/regisger-object/area-tree`).then(response =>{
            this.constant.areaCode = response.data.map(item => (({name, id}) => ({name, id}))(item)); 
        })
        this.$http.get('/regisger-object/device-name').then(response =>{
            this.constant.DeviceID = response.data.map(item => (({name, deviceID}) => ({name, deviceID}))(item)); 
        })
      },
      showpop(data){
          this.captureInfo = data; 
          this.dialogVisible.msgDialog = true;
      },
      equit(){
          this.dialogVisible.msgDialog = false;
      },
      //
      changeTime(value, status) {  
        let time = new Date(value).getTime();  
        switch (status) {
            case 'startTime':
                this.formData.BeginCreateTime = time; 
                break;
            case 'endTime':
                this.formData.EndCreateTime = time; 
                break;        
            default:
                break;
            }
      },
  },
  created(){ 
    this.init(); 
    this.dataInit();
  },
  mounted(){  
  }
};
</script>

<style scoped lang="scss">
@import "../../../assets/css/style1.css";
@import 'comparisonHit.scss';
.faceCapture{ 
    .face-list{
        // padding: 20px 20px 0 20px;
        // margin: 0 10px 15px 0px;
        .clearfix{
            height: auto; 
            min-height: 402px;
            max-height: 1050px;
            overflow: auto;
            .zp-face-img{  
                width: 207px; 
                border: 0px;
                .msgInfo{
                    cursor: pointer; 
                    margin-left: 3%; 
                    img{
                        width: 160px; 
                        height: 160px;
                        margin-top: -10px;
                    }
                    .zp-title{
                        height: 53px;
                        line-height: 13px;
                        color: #67cafe; 
                        font-size: 10px;
                        .el-tooltip{
                            margin-top: 3px;
                            margin-bottom: 5px; 
                        } 
                        div{
                                text-align: left;
                                width:177px; 
                                text-overflow:ellipsis;
                                overflow:hidden;
                                white-space:nowrap;  
                            }
                        /* div:hover { 
                            white-space:normal;
                            height:300px;  
                            transition-duration:0.5s;
                            transition-timing-function:ease-in;  
                        } */
                    }
                } 
            } 
        }  
              
    } 
    /* .pageItem{
        width: 19%!important;
        padding: 37px 20px 0 20px!important;
        margin: 0 5px 15px 0px!important;
        background: url(../../../assets/img/login/face-bg.png) no-repeat top center!important;
        .hit-img-real{
            width: 172px!important;
            height: 172px!important;
            img{
                width: 160px!important;
                height: 160px!important;
            }
        }
    } */
}
</style>


