
const menu = ({
    state : {
        menu :JSON.parse(sessionStorage.getItem("menu")) || {},
        defaultActive:sessionStorage.getItem("defaultActive") || ""
    },
    mutations:{
        menu_save(state,menu){
            state.menu = menu;
        },

        menu_clear(state){
            state.menu = {};
        },

        menu_setDefaultActive(state,defaultActive){
            state.defaultActive = defaultActive;
        }
    },
    modules:{

    },
    actions:{

    }
});


export default menu;


