
import PermissionResources from '@/views/xinjiang/model/permission/PermissionResources'
const permission = ({
    state : {
        permissionResources : getPermissionResources()
    },
    mutations:{
        permission_save(state,permissionResources){
            state.permissionResources = permissionResources;
        },

        permission_clear(state){
            state.permissionResources = [];
        }
    },
    modules:{

    },
    actions:{

    }
});

function getPermissionResources(){
    let resourcesData = JSON.parse(sessionStorage.getItem("permissionResources")) || [];
    let permissionResources = new PermissionResources();
    if(resourcesData.resources){
        permissionResources.setData(resourcesData.resources);
    }
    return permissionResources;
}

export default permission;


