import util from '@/utils/util'
class Base {
    constructor(){
        this.id = "";
    }

    deepClone(){
        return util.copyObject(this);
    }

    setData(data){
        util.setObjectValue(this,data);
    }

    listToTree(data,prop){
        return util.listToTree(data,prop);
    }

    deepCopyObject(obj){
        return util.copyObject(obj);
    }
}
export default Base;


