<template>
  <div class="login">
    <div class="w-380">
      <div class="login-area">
        <div class="login-logo">{{title}}</div>
        <div class="login-form">
          <ul>
            <h1></h1>
            <form id onSubmit="return false;" :rules="rules" ref="form" method="post">
              <li>
                <label class="login-label-user" prop="account"></label>
                <input type="text" v-model="loginForm.account" tabindex="1" placeholder="" />
              </li>
              <li class="lastli">
                <label class="login-label-pwd" prop="password"></label>
                <input
                  type="password"
                  v-model="loginForm.password"
                  autocomplete="off"
                  tabindex="2"
                  placeholder=""
                />
              </li>
              <!-- <li class="code">
                <label class="login-label-code" prop="captcha"></label>
                <input name type="text" tabindex="3" placeholder="" />
                
                <img
                  :src="loginForm.src"
                  class="pointer"
                  @click="refreshCaptcha"
                  style="width: 40%;float: right;"
                />
              </li>-->
              <!--
          <div class="remember-pwd">
            <div class="check-btn"><span>
              <input name="checked" v-model="checked" type="checkbox" value="">
              </span></div>
          </div>
              -->
              <div class="login-btn">
                <a class="btn-entry" tabindex="4" @click="login()" :loading="loading"> </a>
              </div>
            </form>
          </ul>
        </div>
        <div class="login-form-bottom"></div>
        <!--footer s-->
        <!--<div class="login-footer">Copyright 2014 Fujian Infolion Software Co.,Ltd <br/>
          </div>-->
        <!--footer e-->
      </div>
    </div>
    <!--e-->
  </div>
</template>


<script>
import Cookies from "js-cookie";
import PermissionUser from "@/views/xinjiang/model/permission/PermissionUser";
import PermissionResources from "@/views/xinjiang/model/permission/PermissionResources"
//import GeneralEnum from "@/views/xinjiang/model/enum/GeneralEnum"
export default {
  data() {
    return {
      title: "",
      loading: false,
      loginForm: {
        account: "",
        password: "",
        captcha: "",
        src: ""
      },
      rules: {
        account: [{ required: true, message: "", trigger: "blur" }],
        password: [{ required: true, message: "", trigger: "blur" }],
        captcha: [{ required: true, message: "", trigger: "blur" }]
      },
      checked: true
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      // Object.assign(this.user, JSON.parse(localStorage.getItem("acountInfo")));
      this.$http.get("/platform/platform-name").then(res=>{
        this.title = res.data;
        document.title = this.title;
        this.$store.commit("saveTitle",this.title);
      })
    },

    login() {
      let vm = this;
      if(!vm.loginForm.account || !vm.loginForm.password){
        vm.$message({
          message: "",
          type: "warning"
        });
        return ;
      }
      vm.$http.post("/authentication/login", vm.loginForm).then(res => {
        let resources = res.data.resources || [];
        let menu = [];//
        let permissionResources = new PermissionResources();//
        let user = new PermissionUser();//
        user.setData(res.data.user);
        permissionResources.setData(resources);
        let defaultActive = permissionResources.activeMenu;
        menu = permissionResources.menu;
        let param = { user, menu, permissionResources, defaultActive };
        vm.$store.dispatch("login", param);
        if (defaultActive) {
          vm.$router.push({ path: defaultActive });
        } else {
          vm.$message({
            message: ",",
            type: "warning"
          });
        }
      });
    },
    refreshCaptcha: function() {
      this.loginForm.src =
        this.global.baseUrl +
        "/vcpsystem/captcha.jpg?t=" +
        new Date().getTime();
    }
  },
  computed: {},
  watch: {}
};
</script>

<style scoped lang="scss">
@import "../../assets/css/style1.css";
</style>





