<template>
    <div class="sub-page">
		<!-- s -->
		<div class="left-menu">
		    <ul>
          <li 
				 v-for="(item,index) in childrenRouter" :key="index">
				 <router-link :to="{path:item.path}" :class="item.active?'selected':''" >{{item.name}}</router-link></li>
        </ul>
			<span class="b-01"></span>
			<span class="b-02"></span>
			<span class="b-03"></span>
			<span class="b-04"></span>
      <span class="b-05"></span>

		</div>
		<!-- e -->
	   <router-view></router-view>
  </div>
</template>
<script>
import mixins from "@/views/xinjiang/common/mixins/mixins";
export default {
  mixins: [mixins]
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
  .left-menu {
    display: inline-block;
    height: calc(100vh - 120px);
    ul {
      width: 100%;
      height: calc(100% - 12px);
      overflow: auto;
    }
    li {
      text-align: left;
    }
  }
  .t-head,
  .base-search {
    text-align: left;
  }
  .search-more {
    display: inline-block;
  }
  .zzjggl {
    float: right;
    width: calc(100% - 190px);
    height: calc(100vh - 120px);
    overflow: hidden;
    .panel {
      height: 100%;
      width: calc(100% + 20px);
      padding-right: 21px;
      overflow: auto;
    }
  }
}
</style>





