<template>
    <div class='analyAdd'> 
        <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="100px">
            <el-form-item label="" prop="algName">
                <el-input v-model="formData.algName" class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="algCode">
                <el-input v-model="formData.algCode"  class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label=""  prop="status"> 
                <el-input v-model="formData.status"  class="j-input" placeholder=""></el-input> 
            </el-form-item> 
            <el-form-item label="" prop="algType">
                <el-input v-model="formData.algType"  class="j-input" placeholder=""></el-input>
            </el-form-item>  
            <el-form-item label="" prop="algDesc">
                <el-input v-model="formData.algDesc"  class="j-input" placeholder=""></el-input>
            </el-form-item>  
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="equit"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
        </span>
	    <!-- <div class="btn-quit pt0"><a href="javascript:void(0)"> </a></div> -->
    </div>
</template>
<script>
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data(){
    return {
        formData:{
            algName: '',
            algCode: '',
            algType: '',
            status: '', 
            algDesc: ''
        },
        //
        rule1:{
          algName: [{ required: true, message: "", trigger: "change" }],
          algCode: [{ required: true, message: "", trigger: "change" }],
          algType: [{ required: true, message: "", trigger: "change" }],
          status: [{ required: true, message: "", trigger: "change" }] 
        },
    }
  },
  watch:{ 
  },
  computed:{},
  methods:{ 
    ok(){
        this.$refs.formData.validate((valid) => { 
            if(valid){ 
                let type = this.pData.type; 
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `/vcpalgaccess/alg/save`,msg: ''}: {urlType: this.$http.put,url: `/vcpalgaccess/alg/update`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime;
                dataOption.urlType(dataOption.url,this.formData).then(response =>{ 
                    if(response.data.resultCd == 0){ 
                        this.$notify({
                            title: '',
                            message: dataOption.msg,
                            type: 'success'
                        });
                        this.$emit('equit');
                    }
                })
            } 
        })
    },
    equit(){
        this.$emit('equit');
    } 
  },
  created(){ 
    let type = this.pData.type; 
    switch (type) {
        case '':
            this.formData = {};
            break;
        case '':
            this.formData = this.pData.data;
            break;
        default:
            break;
    }  
  }
};
</script>
<style scoped lang="scss">
//@import "../../../../assets/css/style1.css";
/* .dialog{
    height: 366px;
} */
.analyAdd{
    text-align: center;
    .el-form{ 
        margin-top: 15px;
        .j-input{
            .el-input__inner{ 
                border: 1px solid #0e94d2;
            } 
        } 
    } 
    .textButton{ 
        .el-button{
            background: #1b4272;
            color:#fff;
        }
    }  
}

</style>




