import Base from '../Base'
import PermissionModule from './PermissionModule'
class PermissiomResource extends Base{
    constructor(){
        super();
        Object.assign(this,{
            enabled : undefined,
            id : "",
            rank : "",
            permissionModule: new PermissionModule(), 
            operations : []
        })
    }


    hasPermission(opt){
        for(let i = 0 ; i < this.operations.length; i++){
            if(this.operations[i].value == opt){
                return true;
            }
        }
        return false;
    }

    
}

export default PermissiomResource;


