<template>
    <div style="height:100%">
        <div class="box-title">{{data.title}}</div>
        <div class="container">
            <p v-for="(item,index) in data.lists" >
                <img :src="item.image" :alt="item.image">
                {{item.label}}<em :class="item.class">{{item.value}}</em>
                <a  class="btn-details" @click="getData(item)"></a>
                <slot name="handle"></slot>
            </p>
        </div>
    </div>
</template>
<script>
export default {
    name:"box-three",
    props:{
        list:{
            type:Object,
            default:()=>{
                return {
                    title:"",
                    lists:[]
                }
            }
        }
    },
    data(){
        return{
            data:{}
        }
    },
    created(){
        this.data = this.list;
    },
    methods:{
        getData(_data){
            this.$emit("getData",_data);
        },
        updateData(){

        }
    }
}
</script>
<style scoped>
@import "../../../../assets/css/style1.css";
.box-title{
    text-align: left;
}
.container p {
  text-align: left;
}
</style>



