import Vue from 'vue'
import Vuex from 'vuex'
import menu from './modules/menu'
import user from './modules/user'
import permission from './modules/permission'
Vue.use(Vuex)

function getTitle(){
    let  title = sessionStorage.getItem("title");
    document.title = title || "";
    return title;
}

export default new Vuex.Store({
    state : {
        title : getTitle() 
    },
    mutations:{
        saveTitle(state , title){
            state.title = title;
            sessionStorage.setItem("title",title);
        }
    },
    modules:{
        menu,
        user,
        permission
    },
    actions:{
        //
        login(context,{user,menu,permissionResources,defaultActive}){
            context.commit("user_save",user);
            context.commit("menu_save",menu);
            context.commit("permission_save",permissionResources);
            context.commit("menu_setDefaultActive",defaultActive);
            sessionStorage.setItem("user",JSON.stringify(user));
            sessionStorage.setItem("menu",JSON.stringify(menu));
            sessionStorage.setItem("permissionResources",JSON.stringify(permissionResources));
            sessionStorage.setItem("defaultActive",defaultActive);
        },
        //
        logout(context){
            context.commit("user_clear");
            context.commit("menu_clear");
            context.commit("permission_clear");
            sessionStorage.clear();//
        }
    }
});




