<template>
    <div style="height:100%">
        <div class="box-title">{{data.title}}</div>
        <ul class="clearfix">
            <li 
                v-for="(item,index) in data.lists" 
                >
                {{item.label}}
                <p :class="item.class">{{item.value}}</p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"box-one",
    props:{
        list:{
            type:Object,
            default:()=>{
                return {
                    title:"",
                    lists:[]
                }
            }
        }
    },
    data(){
        return{
            data:{}
        }
    },
    created(){
        this.data = this.list;
    },
    methods:{
        updateData(){

        }
    }
}
</script>
<style scoped>
@import "../../../../assets/css/style1.css";
.box-title{
    text-align: left;
}
</style>





