<template>
     <div>
        <div v-for="(item,index) in list.lists" :key="index" class="pic-box">
            <img :src="item.image"/>
            <p class="box-white">{{item.label}}</p>
            <p class="box-yellow">{{item.value}}</p>
        </div>
    </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Object,
      default: () => {
        return {
          title: "",
          lists: []
        };
      }
    }
  }
};
</script>
<style scoped lang="scss">
@import "../../../../assets/css/style1.css";
.box-title {
  text-align: left;
}
.pic-box {
  width: 50%;
  float: left;
  background: url("../../../../assets/img/login/line_box_07.png") right 3px
    no-repeat;
  .box-white {
    color: #fff;
    word-break: break-all;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 15px;
    margin-top: 3px;
  }
  .box-yellow {
   // margin-top: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 20px;
  }
  img {
    width: 32px;
    height: 36px;
  }
}
</style>



