import axios from '../axios'

/* 
 * 
 */

// 
export const save = (data) => {
    return axios({
        url: '/vcpalgaccess/area/save',
        method: 'post',
        data
    })
}
// 
export const update = (data) => {
    return axios({
        url: '/vcpalgaccess/area/update',
        method: 'put',
        data
    })
}
// 
export const batchDelete = (params) => {
    return axios({
        url: '/vcpalgaccess/area/delete',
        method: 'delete',
		params
    })
}
// 
export const findTree = (params) => {
    return axios({
        url: '/vcpalgaccess/area/findTree',
        method: 'get',
        params
    })
}





