<template>
    <div class="orgOpt"> 
      <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="100px">
        <!-- <el-form-item label="" prop="name">
            <el-input v-model="formData.name" class="j-input" placeholder="" clearable></el-input>
        </el-form-item> 
        <el-form-item label="" prop="organization">
          <el-input v-model="formData.organization" @click.native="clickControl" class="j-input" placeholder="" clearable></el-input>
          <el-tree v-show="weatherShow"
            empty-text=""
            show-checkbox 
            :highlight-current = true
            :default-expand-all = false
            :expand-on-click-node="true"
            :data="treeData"
            :filter-node-method="filterNode"
            @node-click="selectClassfy"
            class="j-tree"
            ref="selectTree">
          </el-tree>
        </el-form-item>  
        <el-form-item label=""  prop="orderNum"> 
          <el-input-number class="m-input" v-model="formData.orderNum" controls-position="right" @change="handleChange" :min="1" ></el-input-number> 
        </el-form-item> --> 
        <el-form-item label="">
          <el-input v-model="formData.creator" readonly clearable></el-input>
        </el-form-item>    
        <el-form-item label="" prop="name">
          <el-input v-model="formData.name" class="j-input" placeholder="" clearable></el-input>
        </el-form-item> 
        <el-form-item label="" prop="code">
          <el-input v-model="formData.code" class="j-input" placeholder="" clearable></el-input>
        </el-form-item> 
        <el-form-item label="id"  prop="parentName"> 
          <el-input v-model="formData.parentName" :data-value='formData.parentId' @click.native="clickControl" class="j-input" placeholder="" clearable></el-input>
          <el-tree 
            v-show="weatherTree"
            empty-text=""
            :highlight-current = true
            :default-expand-all = false
            :expand-on-click-node="true"
            :data="orgOptions"
            :props='treeOption'
            :filter-node-method="filterNode"
            @node-click="selectClassfy"
            class="j-tree"
            ref="selectTree">
          </el-tree> 
        </el-form-item> 
      </el-form>
      <span class='textButton'>
          <el-button type="primary" @click="equit"> </el-button>
          <el-button type="primary" @click="ok"> </el-button>
      </span>
    </div>
</template>
<script>
import synTree from "@/views/xinjiang/common/components/tree/synTree";
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  }, 
  data() {
    return {
      weatherShow: false,
      weatherTree: false,
      constant:{
        areaCode:[],
        areaName: [],
        areaC:[]
      }, 
      parentArry:[],
      accountName: 'string',
      treeOption:{
        children: "children",
        label: "name"
      }, 
      orgOptions:[],
      formData:{
          name: '', 
          organization : '',
          orderNum: '',
          creator: '',
          area:'' 
      },
      //
      rule1:{
        name: [{ required: true, message: "", trigger: "blur" }], 
        /* organization: [{ required: true, message: "", trigger: "change" }], */
        parentName: [{ required: true, message: "id", trigger: "change" }],
        parentId: [{ required: true, message: "id", trigger: "change" }],
        code: [{ required: true, message: "", trigger: "change" }],
        /* orderNum: [{ required: true, message: "", trigger: "change" }]  */
      },
      treeData:[]
      /* treeData: [{
          label: ' 1',
          children: [{
            label: ' 1-1',
            children: [{
              label: ' 1-1-1',
              children:[]
            }]
          }]
        }, {
          label: ' 2',
          children: [{
            label: ' 2-1',
            children: [{
              label: ' 2-1-1',
              children:[]
            }]
          }, {
            label: ' 2-2',
            children: [{
              label: ' 2-2-1',
              children:[]
            }]
          }]
        }, {
          label: ' 3',
          children: [{
            label: ' 3-1',
            children: [{
              label: ' 3-1-1',
              children:[]
            }]
          }, {
            label: ' 3-2',
            children: [{
              label: ' 3-2-1',
              children:[]
            }]
          }]
        }]  */
    };
  },
  watch: {},
  methods: {
    clickControl(){
      this.weatherTree = true;
    }, 
    selectClassfy(data) {  
        this.formData.parentName = data.name;
        this.formData.parentId = data.id; 
        this.weatherTree = false;
    },  
    filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
    }, 
    dataInit(){  
      this.$http.get('/organization/only-org-tree').then(response=>{
          let list = response.data || [];
          let prop = {
              code: 'id',
              parent: 'parentId',
              children: 'children'
          }
          this.parentArry = response.data;
          this.orgOptions = this.$util.listToTree(list,prop);//   
          let type = this.pData.type; 
          switch (type) {
              case '':
                  this.formData = {}; 
                  //this.formData.creator = sessionStorage.getItem('accountName'); 
                  this.weatherAdd = true; 
                  break;
              case '':  
                  this.formData = this.pData.data; 
                  //this.formData.creator = sessionStorage.getItem('accountName');
                  this.parentArry.filter(item => {
                    if(item.id = this.formData.parentId){
                      this.formData.parentName = item.name;
                    }
                  }); 
                  this.weatherAdd = false; 
                  break;
              default:
                  break;
          }  
          this.formData.creator = this.$store.state.user.user.name; 
      });
    },
    handleChange(value) {
      console.log(value);
    },
    clickTreeNode(data, node) {
      let param = {
        areaCode: data.id
      };
      this.treeNode = data;
      this.$util.clearObjectValue(this.searchForm);
      this.$refs.deviceList.searchTableData(param);
    },
    ok(){ 
        this.$refs.formData.validate((valid) => { 
            if(valid){
                let type = this.pData.type; 
                //this.formData.area = {code: this.formData.area};
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `/organization`,msg: ''}: {urlType: this.$http.put,url: `/organization/${this.pData.data.id}`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime;
                delete this.formData.area; 
                delete this.formData.enabled;
                delete this.formData.root;
                delete this.formData.text;
                dataOption.urlType(dataOption.url,this.formData).then(response =>{  
                  this.$notify({
                      title: '',
                      message: dataOption.msg,
                      type: 'success'
                  }); 
                  this.$emit('equit'); 
                })
            } 
        })
    },
    equit(){
        this.$emit('equit');
    } 
  },
  mounted(){
    this.dataInit();
  },
  components: {
    "syn-tree": synTree
  }
};
</script>
 
<style lang="scss">
.orgOpt{
  text-align: center;
    .el-form{ 
      margin-top: 15px;
      .j-input{
          .el-input__inner{ 
              border: 1px solid #0e94d2;
          } 
          .input:-internal-autofill-selected {
              background-color: #0d254a!important; 
          }
      }
      .j-select{
          width: 212px;
      }  
    } 
    .textButton{ 
      .el-button{
          background: #1b4272;
          color:#fff;
      }
    } 
}
</style>




