<template>
    <div ref="tu" style="height:100%; width:100%"></div>
</template>
<script>
export default {
  /** 
   * xAxisList:[
   * "2019-08-27",
    "2019-08-28",
    "2019-08-29",
    "2019-08-30",
    "2019-08-31",
    "2019-09-01",
    "2019-09-02"]
   * list:[{ y
      name: '',
      data: [31, 42, 32, 41, 32, 42, 31, 42, 31, 43, 31, 40]
    }]
  */
  props: {
    list: {
      type: Array,
      default: () => {
        return [];
      }
    },
    xAxisList: {
      type: Array,
      default: () => {
        return [];
      }
    },
    colorList: {
      type: Array,
      default: () => {
        return ["#f9f139", "#50cb74", "#45e6ea","#24a1e3", "#d1451a","#e690d1", "#e650ab","#f48421","#8c73fe"];
      }
    }
  },
  data() {
    return {
      chart: null,
      data: [],
      option: {
        color: [],
        dataZoom: [
          {
            show: true,
            start: 0,
            type: "inside"
          },
          {
            type: "inside"
          }
        ],
        legend: {
          x: "center",
          y: "bottom",
          bottom: "40%",
          data: [],
          textStyle: {
            color: "#708dec",
            fontSize: "13"
          }
        },
        toolbox: {
          feature: {
            saveAsImage: { show: false }
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow"
          }
        },
        grid: {
          left: "0",
          right: "0",
          bottom: "50",
          top: "20",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            splitLine: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: "#27467a"
              }
            },
            axisLabel: {
              show: true,
              interval: 0,
              textStyle: {
                color: "#7fb8f8",
                fontSize: 13,
                fontWeight: 0
              }
            },
            data: []
          }
        ],
        yAxis: [
          {
            name: "",
            nameTextStyle: {
              color: "#0e5eaf",
              padding: [0, 15, 6, 0],
              fontSize: 13
            },
            type: "value",
            splitLine: {
              lineStyle: {
                color: "#27467a"
              }
            },
            axisLine: {
              show: false
            },
            axisLabel: {
              show: true,
              formatter: "{value}",
              color: "#7fb8f8",
              fontSize: 13
            }
          }
        ],
        series: [
          /*{
            name: "",
            type: "line",
            smooth: true,
            symbolSize: 8,
            symbol: "circle",
            data: [31, 42, 32, 41, 32, 42, 31, 42, 31, 43, 31, 40]
          },
          {
            name: "",
            type: "line",
            smooth: true,
            symbolSize: 8,
            symbol: "circle",
            data: [21, 30, 22, 28, 21, 30, 21, 29, 21, 30, 21, 30]
          },
          {
            name: "",
            type: "line",
            smooth: true,
            symbolSize: 8,
            symbol: "circle",
            data: [20, 15, 18, 15, 10, 20, 15, 20, 10, 21, 15, 20]
          },
          {
            name: "",
            type: "line",
            smooth: true,
            symbolSize: 8,
            symbol: "circle",
            data: [15, 10, 13, 8, 5, 15, 12, 15, 7, 15, 12, 16]
          }*/
        ]
      }
    };
  },
  mounted() {
    this.setData();
  },
  methods: {
    setData() {
      this.chart = this.$echarts.init(this.$refs.tu);
      this.getOption();
      this.chart.setOption(this.option);
    },
    getOption() {
      this.option.color = this.colorList;
      this.option.xAxis[0].data = this.xAxisList;
      if (this.xAxisList.length > 8) {
        this.option.dataZoom[0].end = 8 / this.xAxisList.length * 100;
      } else {
        this.option.dataZoom[0].end = 100;
      }
      this.list.forEach(element => {
        this.option.legend.data.push(element.name);
        this.option.series.push({
          name: element.name,
          data: element.data,
          type: "line",
          smooth: true,
          symbolSize: 8,
          symbol: "circle"
        });
      });
    },
    saveImg() {
      return new Promise(resolve => {
        let dataURL = this.chart.getConnectedDataURL({
          pixelRatio: 2,
          type: "png",
          backgroundColor: "#081121"
        });
        resolve(dataURL);
      });
    }
  }
};
</script>



