import Base from '../../model/Base';
import Chain from '@/views/xinjiang/common/pattern/Chain'
import axios from '@/utils/axios';
class DeviceInfo extends Base{
    constructor(){
        super();
        Object.assign(this,{
            province : "",
            city : "",
            county : "",
            unit : "00",
            industrCategory : "",
            equipmentType : "",
            networkIdentification : "",
            equipmentSerialNumber : "",
            provinceData : [],
            cityData : [],
            countyData : [],
            industrCategoryData:[],
            equipmentTypeData :[],
            networkIdentificationData : [],

        });
        
        this.init();
    }

    init(){
        this.monitor();
        this.getProvinceData();
        //this.getEquipmentSerialNumber();
    }

    getEquipmentSerialNumber(){
        axios.get("/regisger-object/unique-num").then(res => {
            console.log(this);
            this.equipmentSerialNumber = res.data;
        });
    }


    getProvinceData(){
        axios.get("/regisger-object/city-data/100000000000").then(res=>{
            this.provinceData = res.data;
        })
    }

    getCityData(){
        if(this.province){
            axios.get(`/regisger-object/city-data/${this.province}`).then(res =>{
                this.cityData = res.data;
            })
        }
    }

    getCountyData(){
        if(this.city){
            axios.get(`/regisger-object/city-data/${this.city}`).then(res =>{
                this.countyData = res.data;
            })
        }
    }

    monitor() {
        let province = "";
        let city = "";
        let vm = this;
        Object.defineProperty(this,"province",{
            get: function () {
                return province;//returnname
            },
            set:function(value){
                province = value;
                vm.city = "";
                vm.getCityData();
            }
        });

        Object.defineProperty(this,"city",{
            get: function () {
                return city;//returnname
            },
            set:function(value){
                city = value;
                vm.county = "";
                if(vm.city){
                    vm.getCountyData();
                }
            }
        });
    }

    // getDviceId(){
    //     let deviceId = "";
    //     deviceId = getProvinCode() + getCityCode() + getCountyCode() + this.unit + this.
    // }

    getProvinCode(){
        let province = this.province+"";
        return province.substring(0,2) || "00";
    }

    getCityCode(){
        let city = this.city + "";
        return city.substring(2,4) || "00";
    }

    getCountyCode(){
        let county = this.county + "";
        return county.substring(4,6) || "00";
    }

    setAreaCode(deviceId){
        let provinceCode = deviceId.substring(0,2);
        let cityCode = deviceId.substring(2,4);
        let countyCode = deviceId.substring(4,6);
        // for(let i = 0 ; i < this.provinceData.length ; i++){
        //     if(this.provinceData[i].substring(0,2) == provinceCode){
        //         this.province = this.provinceData[i];
        //         break;
        //     }
        // }
        this.province = provinceCode + "0000000000";
        this.city = provinceCode + cityCode + "00000000";
        this.county = provinceCode + cityCode + countyCode + "000000";
    }

    setData(deviceId){
        this.setAreaCode(deviceId);
        this.industrCategory = deviceId.substring(8,10);
        this.equipmentType = deviceId.substring(10,13);
        this.networkIdentification = deviceId.substring(13,14);
        this.equipmentSerialNumber = deviceId.substring(14,20);
    }


}
export default DeviceInfo;


