<template>
    <div class="zzjggl areaOpt">
      <el-row>
        <el-col :span="4">
          <div class="left-tree-box">
            <h1 class="title">
              <i class="icon icon-tree"></i>
            </h1>
            <ul id="browser" class="filetree">
              <synTree :option="treeOption" @clickNode='clickNode'></synTree>
            </ul>
            <span class="b-01"></span>
            <span class="b-02"></span>
            <span class="b-03"></span>
            <span class="b-04"></span>
          </div>
        </el-col> 
        <el-col :span="19" class='areaManager'>
          <div class="panel">
            <div class="base-search">
              <label></label>
              <span class="m-input">
                  <el-input class="w-200"  v-model="searchForm.name" placeholder=""></el-input>
              </span>
              <label></label>
              <span class="m-input">
                  <el-input class="w-200"  v-model="searchForm.AreaCode" placeholder=""></el-input>
              </span>
              <a href="javascript:void(0)" class="btn-search" @click="search"></a>
              <a href="javascript:void(0)" class="btn-add" @click="add" v-if="permissionResource.hasPermission('CREATION')"></a>
              <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')"></a>
            </div>
            <div class="line"></div>           
            <div>
                <table-grid :option="option" ref="areaList">
                    <template slot-scope="row" >
                       <a href="javascript:void(0)" title=""  @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')">
                          <i class="btn-icon icon-16"></i>
                        </a>
                        <a href="javascript:void(0)" title=""  @click="deleteTab(row)" v-if="permissionResource.hasPermission('DELETION')">
                          <i class="btn-icon icon-17"></i>
                        </a>
                    </template>
                </table-grid>
            </div>
        </div>
        </el-col>
      </el-row>
      <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog1' @equit="equit">
        <dialog-edit :pData='msgDialogInfo' @equit='equit'></dialog-edit>
      </msgDialog> 
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import synTree from "@/views/xinjiang/common/components/tree/synTree"; 
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogEdit from "@/views/xinjiang/analysis/opt/analyAdd"; 

export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      treeOption: {
        url: "/regisger-object/area-tree",
        param: {},
        prop: {
          children: "children",
          label: "name"
        },
        type: "get"
      },
      searchForm:{},
      dialogVisible:{
        msgDialog: false
      },
      msgDialogInfo:{

      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "620px",
        data: ""
      },
      option: {
        isShowTableNav: false,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          hasCustomColum: true,
          tableTitle: [
            { prop: "code", label: "" },
            { prop: "name", label: "" }, 
            { prop: "address", label: "" }, 
            { prop: "serverName", label: "" },
            {
              prop: "createTime",
              label: "",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.modifiedDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          opeColumnWidth: 220,
          customRes: true
        },
        customLabel: "",
        url: "/vcpalgaccess/area/findByPage",
        type: "post",
        notRequestSever: false,
        param: {
          /* pageSize: 10,
          pageNum: 1,
          para: {
            code: "11"
          } */
        }
      }
    };
  },
  methods: {
    search(data) {
      //let param = {}; 
      if (this.searchForm) { 
        this.option.param = Object.assign({pageSize: 10, pageNum: 1,},{para: this.searchForm});
      }  
      this.$refs.areaList.searchTableData(Object.assign(this.option.param,{para: data}));
    },
    clickNode(data,node){
      this.search({parentCode: data.id});  
    },
    deleteTab(row){ 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/vcpalgaccess/alg/delete`,{id: row.rowData.id}).then(response =>{
            if(response.data.resultCd == 0){
              this.$notify({
                title: '',
                message: '!!',
                type: 'success'
              });
              this.equit();
            } 
          })
        });
    }, 
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.$refs.analysisList.flushTableData();
      this.dialogVisible.msgDialog = false;
    }
  },
  components: {
    "table-grid": tableGrid,
    synTree,
    msgDialog,
    dialogEdit
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page .t-head,.base-search{
  text-align:left; 
} 
.areaOpt{  
  .areaManager{
    margin-left: 2%!important;
  } 
  .left-tree-box {  
    height: calc(100vh - 100px);
  } 
}
</style>




