import axios from '../axios'

/* 
 * 
 */

// 
export const save = (data) => {
    return axios({
        url: '/vcpdev/api/devrsa/operateRsa',
        method: 'post',
        data
    })
}

// 
export const batchDelete = (params) => {
    return axios({
        url: '/vcpdev/devkey/delete',
        method: 'delete',
		params
    })
}
// 
export const findPage = (data) => {
    return axios({
        url: '/vcpdev/devkey/findByPage',
        method: 'post',
        data
    })
}

// 
export const findOne = (params) => {
    return axios({
        url: '/vcpdev/devkey/findOne',
        method: 'get',
		params
    })
}



