<template>
    <div class='pagePosition'>
        <div class="paginationPage"> 
            <div class='pageInfo'> 
                <div><div class='numberColor'>{{total}}</div> </div>
                <div>{{` ${pageSize} `}}</div> 
            </div>
            <div class='pageOption'>  
                <el-pagination
                background
                @current-change="handleCurrentChange"
                :current-page="currentPageNum"
                :page-size="pageSize"
                layout="slot"   
                >
                <el-button icon="el-icon-d-arrow-left first-pager" :disabled="this.currentPageNum === this.firstPage" @click="toFirstPage"></el-button> 
                </el-pagination>
                <el-pagination
                background
                @current-change="handleCurrentChange"
                :current-page="currentPageNum"
                :page-size="pageSize"
                layout="prev,pager,next,slot,jumper" 
                :total="total"
                :firstPage='firstPage'
                :lastPage='lastPage'
                > 
                <el-button icon="el-icon-d-arrow-right last-pager" :disabled="this.currentPageNum === this.lastPage" @click="toLastPage"></el-button> 
                </el-pagination>
                <div class='jump'>
                    <el-button type="small" size="small" @click="jump">GO</el-button>
                </div>
            </div> 
        </div>
    </div>
</template>

<script>
export default {
  components:{},
  props:{ 
      pData:{
          type: Object,
          defalut: () =>{}
      }
  },
  data(){
    return { 
        currentPageNum: 1,
        pageSize: 10,
        currentPage: 1,
        firstPage: 1,
        lastPage:5,
        total: 50 
    }
  },
  watch:{
    pData(val){
        this.pageSize = this.pData.pageSize; 
        this.lastPage = this.pData.lastPage;
        this.total = this.pData.total; 
    } 
  },
  computed:{},
  methods:{ 
    handleCurrentChange(val) { 
        let data = {PageNum: `${val}`};
        this.$emit('init', data);  
    },
    toFirstPage(val) {
        this.currentPageNum = this.firstPage;
        this.handleCurrentChange(this.firstPage);
    },
    toLastPage(val) {  
        this.currentPageNum = this.lastPage; 
        this.handleCurrentChange(this.lastPage); 
    }, 
    jump(){ 
    },
    init(){ 
    }
  },
  created(){  
  },
  mounted(){   
  } 
}
</script>
<style lang="scss" >
@import 'pagination.scss'; 
</style>


