import axios from '../axios'

/* 
 * 
 */

// 
export const save = (data) => {
    return axios({
        url: '/vcpdev/dev/save',
        method: 'post',
        data
    })
}
// 
export const update = (data) => {
    return axios({
        url: '/vcpdev/dev/update',
        method: 'put',
        data
    })
}
// 
export const batchDelete = (params) => {
    return axios({
        url: '/vcpdev/dev/delete',
        method: 'delete',
		params
    })
}
// 
export const findPage = (data) => {
    return axios({
        url: '/vcpdev/dev/findByPage',
        method: 'post',
        data
    })
}

// 
export const findOne = (params) => {
    return axios({
        url: '/vcpdev/dev/findOne',
        method: 'get',
		params
    })
}



