<template>
    <div class='companyManagerAdd'> 
        <el-form :model="formData" ref="formData" :inline="true" :rules="rule1" label-width="100px">
            <el-form-item label="" prop="mfName">
                <el-input v-model="formData.mfName" class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="mfCode">
                <el-input v-model="formData.mfCode"  class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="mfDesc">
                <el-input v-model="formData.mfDesc"  class="j-input" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item label="" prop="linker">
                <el-input v-model="formData.linker"  class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
                <el-input v-model="formData.phone"  class="j-input" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model="formData.email"  class="j-input" placeholder=""></el-input>
            </el-form-item> 
        </el-form>
        <span class='textButton'>
            <el-button type="primary" @click="equit"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
        </span>
	    <!-- <div class="btn-quit pt0"><a href="javascript:void(0)"> </a></div> -->
    </div>
</template>
<script>
export default {
  props: {
    pData: {
      type: Object,
      default: () => {}
    }
  },
  data(){
    return {
        formData:{
            mfName: '',
            mfCode: '',
            linker: '',
            phone: '',
            email: '',
            mfDesc: ''
        },
        //
        rule1:{
          mfName: [{ required: true, message: "", trigger: "change" }],
          mfCode: [{ required: true, message: "", trigger: "change" }],
          linker: [{ required: true, message: "", trigger: "change" }],
          phone: [{ required: true, message: "", trigger: "change" }],
          email: [{ required: true, message: "", trigger: "change" }] 
        },
    }
  },
  watch:{},
  computed:{},
  methods:{ 
    ok(){ 
        this.$refs.formData.validate((valid) => { 
            if(valid){
                let type = this.pData.type; 
                let dataOption = (type == '') ? {urlType: this.$http.post,url: `/vcpalgaccess/manuf/save`,msg: ''}: {urlType: this.$http.put,url: `/vcpalgaccess/manuf/update`,msg: ''};
                this.formData.status == '' ? this.formData.status = 1 :  this.formData.status = 0;
                delete this.formData.createTime;
                dataOption.urlType(dataOption.url,this.formData).then(response =>{ 
                    if(response.data.resultCd == 0){
                        this.$notify({
                            title: '',
                            message: dataOption.msg,
                            type: 'success'
                        }); 
                        this.$emit('equit');
                    }
                })
            } 
        })
    },
    equit(){
        this.$emit('equit');
    } 
  },
  created(){ 
    let type = this.pData.type; 
    switch (type) {
        case '':
            this.formData = {};
            break;
        case '':
            this.formData = this.pData.data;
            break;
        default:
            break;
    }  
  }

};
</script>
<style scoped lang="scss">
//@import "../../../../assets/css/style1.css";
/* .dialog{
    height: 366px;
} */
.companyManagerAdd{
    text-align: center;
    .el-form{ 
        margin-top: 15px;
        .j-input{
            .el-input__inner{ 
                border: 1px solid #0e94d2;
            } 
        } 
    } 
    .textButton{ 
        .el-button{
            background: #1b4272;
            color:#fff;
        }
    }  
}

</style>




