<template>
    <div ref="tu" style="height:100%; width:100%"></div>
</template>
<script>
export default {
  /** 
   * list:[{
          value: 300,
          name: ""
        },
        {
          value: 900,
          name: ""
        },
        {
          value: 900,
          name: ""
        },
        {
          value: 900,
          name: ""
        }]
  */
  props: {
    list: {
      type: Array,
      default: () => {
        return [];
      }
    },
    colorList: {
      type: Array,
      default: () => {
        return ["#f9f139", "#50cb74", "#45e6ea","#24a1e3", "#d1451a","#e690d1", "#e650ab","#f48421","#8c73fe"];
      }
    }
  },
  data() {
    return {
      chart: null,
      option: {
        legend: {
          x: "72%",
          y: "top",
          orient: "vertical",
          padding: [32, 0, 0, 10],
          data: [],
          textStyle: {
            color: "#708dec",
            fontSize: "12"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: function(param) {
            return (
              param.marker +
              "" +
              param.name +
              "<br>" +
              "&nbsp;&nbsp;&nbsp;&nbsp;" +
              param.value +
              ""
            );
          }
        },
        series: [
          {
            name: "",
            type: "pie",
            center: ["37%", "50%"],
            radius: ["40%", "60%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: []
          }
        ]
      }
    };
  },
  mounted() {
    this.setData();
  },
  methods: {
    setData() {
      this.chart = this.$echarts.init(this.$refs.tu);
      this.getOption();
      this.chart.setOption(this.option);
    },
    getOption() {
      this.option.color = this.colorList;
      this.option.series[0].data = this.list;
      this.list.forEach(element => {
        this.option.legend.data.push(element.name);
      });
    }
  }
};
</script>



