<template>
  <div class="root">
    <div class="title">
      <p></p>
      <div class="opt">
        <span @click="toHome"></span>
      </div>
    </div>
    <div class="lenged">
      <div class="lenged-left">
        <img class="lenged-img" :src="require('./img/ico_image.png')" />
        <span></span>
        <span class="lenged-number">{{facePhotoNumber}}</span>
        <span></span>
      </div>
      <div class="lenged-right">
        <img class="lenged-img" :src="require('./img/ico_car.png')" />
        <span></span>
        <span class="lenged-number">{{carPhotoNumber}}</span>
        <span></span>
      </div>
    </div>
    <div class="main">
      <div class="left-container">
        <div class="box-title">
          <p></p>
        </div>
        <div class="insertCapabilityRoot box-bg">
          <myBar @click.native="toDevice"></myBar>
        </div>
        <div class="box-title">
          <p></p>
        </div>
        <div class="brandRoot box-bg">
          <myPie @click.native="toDevice"></myPie>
        </div>
        <div class="box-title">
          <p></p>
        </div>
        <div class="assetsInsertRoot box-bg">
          <myLine @click.native="toDevice"></myLine>
        </div>
      </div>
      <div class="center-container">
        <div class="box-title areaTitle">
          <p></p>
        </div>
        <myMap></myMap>
      </div>
      <div class="right-container">
        <div class="box-title">
          <p></p>
        </div>
        <div class="assetsCountRoot box-bg">
          <myCircle @click.native="toDevice"></myCircle>
        </div>
        <div class="box-title">
          <p></p>
        </div>
        <div class="alarmRoot box-bg">
          <myLineGroup @click.native="toDevice"></myLineGroup>
        </div>
        <div class="box-title">
          <p></p>
        </div>
        <div class="logRoot box-bg">
          <myWrap @click.native="toLog"></myWrap>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import myCircle from "./component/circle";
import myMap from "./component/map";
import myBar from "./component/echartsBar";
import myLine from "./component/echartsLine";
import myLineGroup from "./component/echartsLineGroup";
import myWrap from "./component/wrap";
import myPie from "./component/echartsPie";
export default {
  data() {
    return {
      facePhotoNumber: 0,
      carPhotoNumber: 0
    };
  },

  mounted() {
    this.getCount();
  },

  methods: {
    toHome() {
      this.$router.push("/"); // 
    },
    toDevice() {
      this.$router.push("/device/index"); //
    },
    toLog() {
      this.$router.push("/log/index"); //
    },
    getCount() {
      this.$http.get("/big-screen/camture").then(res => {
        this.facePhotoNumber = res.data.faceObjectNum;
        this.carPhotoNumber = res.data.motorVehicleObjectNum;
      });
    }
  },

  watch: {},

  computed: {},

  components: {
    myMap,
    myCircle,
    myBar,
    myLine,
    myWrap,
    myLineGroup,
    myPie
  }
};
</script>

<style  scoped>
.root {
  background-image: url("./img/data_bg.png");
  background-size: 100% 100%;
}
.title {
  height: 30px;
  position: relative;
}

.lenged {
  height: 85px;
  background-image: url("./img/title_bg.png");
  background-size: 100% 100%;
  padding: 0px;
  margin: 0px;
  display: flex;
}
.lenged-left {
  flex: 1;
  color: #fff;
  align-items: center;
  justify-content: left;
  margin-left: 80px;
  display: flex;
}
.lenged-img {
  height: 20px;
  margin-right: 10px;
}
.lenged-number {
  margin-right: 20px;
  font-size: 34px;
  color: yellow;
  margin-left: 10px;
  margin-right: 20px;
}
.lenged-right {
  flex: 1;
  color: #fff;
  align-items: center;
  justify-content: left;
  display: flex;
}
.title p {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  padding: 10px;
  color: #fff;
}
.box-bg {
  background-image: url("./img/box_bg.png");
  background-size: 100% 100%;
  flex: 1;
}
.box-title {
  background-image: url("./img/title.png");
  background-size: 100% 100%;
  text-align: left;
  color: #fff;
  padding-bottom: 3px;
  margin-top: 5px;
}
.box-title p {
  margin-left: 15px;
}

.opt {
  position: absolute;
  top: 10px;
  right: 30px;
}
.opt span {
  margin-left: 10px;
  cursor: pointer;
  color: #fff;
}
.main {
  display: flex;
  width: 100vw;
  height: calc(100vh - 116px);
}
.left-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.center-container {
  position: relative;
  flex: 2;
}
.right-container {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.areaDistribution {
  flex: 1;
}
.areaTitle {
  position: absolute;
  left: 20px;
  width: 200px;
}
p {
  margin-block-start: 0px;
  margin-block-end: 0px;
}
</style>




