<template>
  <div class="circle" ref="circle" v-if="isShow"></div>
</template>

<script>
export default {
  data() {
    return {
      offlineNum: 0,
      onlineNum: 0,
      preNum: 0,
      total: 0,
      isShow: false
    };
  },

  mounted() {
    this.getData();
  },

  methods: {
    createCircle() {
      let option = {
        color: [
          "#e062ae",
          "#67e0e3",
          "#ff4777",
          "#70f3ff",
          "#4b5cc4",
          "#f47983",
          "#8d4bbb",
          "#6635EF",
          "#FFAFDA"
        ],
        title: {
          text: ` \n\n ${this.total}`,
          x: "center",
          y: "center",

          textStyle: {
            color: "#fff"
          }
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },
        legend: {
          orient: "horizontal",
          bottom: "bottom",
          data: ["", "", ""],
          textStyle: {
            color: "#fff"
          }
        },
        series: [
          {
            type: "pie",
            radius: ["50%", "70%"],

            data: [
              { value: this.onlineNum, name: "" },
              { value: this.offlineNum, name: "" },
              { value: this.preNum, name: "" }
            ],
            label: {
              show: false
            },
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            }
          }
        ]
      };
      let mychart = this.$echarts.init(this.$refs.circle);
      mychart.setOption(option);
    },
    getData() {
      this.isShow = false;
      this.$http.get("/big-screen/assert").then(res => {
        this.offlineNum = res.data.offline;
        this.onlineNum = res.data.onlineNum;
        this.preNum = res.data.preNum;
        this.total = res.data.total;
        this.isShow = true;
        this.$nextTick(() => {
          this.createCircle();
        });
      });
    }
  },

  watch: {},

  computed: {},

  components: {}
};
</script>

<style  scoped>
.circle {
  width: 100%;
  height: 100%;
}
</style>




