<template>
    <div ref="tu" style="height:100%; width:100%"></div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      chart: null,
      data: [],
      option: {
        color: ["#d3634b", "#78ceef"],
        tooltip: {
          trigger: "item",
          formatter: function(param) {
            return (
              param.marker +
              "" +
              param.name +
              "<br>" +
              "&nbsp;&nbsp;&nbsp;&nbsp;" +
              param.value +
              "" +
              param.percent +
              "%"
            );
          }
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: []
          }
        ]
      }
    };
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs.tu);
    this.data = this.list;
    this.getData();
  },
  methods: {
    getData() {
      this.option.series[0].data=this.data;
      this.chart.setOption(this.option);
    }
  }
};
</script>




