/**
 * axios
 * 
 */

import axios from 'axios';
import qs from 'qs';
import { Loading, Message } from "element-ui";
import 'element-ui/lib/theme-chalk/index.css';
import util from './util.js'
import { baseUrl } from '@/utils/global'

let loading, isShow = false;
let needLoadingRequestCount = 0;//
let targetType
let defaultConfig = {
  headers: {
    "Content-Type": "application/json"
  },
  //baseURL:baseUrl
};
let HTTP = axios.create(defaultConfig);
let load = {
  startLoading() {
    if (targetType != null) {
      loading = Loading.service({
        text: '...',
        target: targetType,
        background:"transparent"
      });
    }
  },
  endLoading() {
    if (loading != undefined) {
      loading.close();
    }
  },
  showFullScreenLoading() {
    if (needLoadingRequestCount === 0) {
      this.startLoading();
    }
    needLoadingRequestCount++;
  },
  tryHideFullScreenLoading() {
    if (needLoadingRequestCount <= 0) return;
    needLoadingRequestCount--;
    if (needLoadingRequestCount === 0) {
      this.endLoading();
    }
  }
};
//
HTTP.interceptors.request.use((config) => {
  //test();

  load.showFullScreenLoading();
  /* let token = mUtils.getSessionStore("sfqxToken");
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  } */
  return config;
}, (error) => { //404
  load.tryHideFullScreenLoading();
  return Promise.reject(error);
});
HTTP.interceptors.response.use((response) => { //
  if (response.status == 200) {
    load.tryHideFullScreenLoading();
    if(response.data.resultCd){
      Message.error(response.data.msg);
      return Promise.reject(response);
    }
  }
  return response;
}, (error) => { //404
  load.tryHideFullScreenLoading();
  let tempArr = [];
  // error.response.data.message != undefined ? tempArr.push(error.response.data.message) : '';
  if (error.response.data.details != undefined) {
    Array.from(error.response.data.details, (result) => {
      if (result.field) {
        tempArr.push(result.field + " : " + result.message);
      } else {
        tempArr.push(result.message);
      }
    })
  }
  Message.error(error.response.data.message);
  return Promise.reject(error);
})
export default {
  setTargetLoadType(content = '.el-main') {
    targetType = document.querySelector(content);
    return this;
  },
  setConfig(data = {}) {
    if (typeof data !== 'object') return data
    defaultConfig = data;
    HTTP = axios.create(defaultConfig);
    return this;
  },
  post(url, params = {}, config = {}) {
    params = util.deleteEmptyProperty(params);
    params = util.deleteEmptyProperty(params);
    params = util.changeObjectVale(params);
    return new Promise((resolve, reject) => {
      HTTP.post(url, params, config)
        .then(response => {
          resolve(response);
        }, err => {
          reject(err);
        })
        .catch((error) => {
          reject(error)
        })
    })
  },
  get(url, params = {}, config = {}) {
    params = util.deleteEmptyProperty(params);
    params = util.deleteEmptyProperty(params);
    params = util.changeObjectVale(params);
    return new Promise((resolve, reject) => {
      url = (url.indexOf("?") == '-1') ? `${url}?t=${+new Date().getTime().toString()}`
        : `${url}&t=${new Date().getTime().toString()}`
      HTTP.get(url, {
        params: params,
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'indices', allowDots: true })
        }
      }, config).then(response => {
        resolve(response);
      })
        .catch((error) => {
          reject(error)
        })
    })
  },
  delete(url, params = {}, config = {}) {
    params = util.deleteEmptyProperty(params);
    params = util.deleteEmptyProperty(params);
    params = util.changeObjectVale(params);
    return new Promise((resolve, reject) => {
      url = (url.indexOf("?") == '-1') ? `${url}?t=${+new Date().getTime().toString()}`
        : `${url}&t=${new Date().getTime().toString()}`
      HTTP.delete(url, {
        params: params,
        paramsSerializer: params => {
          return qs.stringify(params, { arrayFormat: 'indices', allowDots: true })
        }
      }, config).then(response => {
        resolve(response);
      })
        .catch((error) => {
          reject(error)
        })
    })
  },
  put(url, params = {}, config = {}) {
    params = util.deleteEmptyProperty(params);
    params = util.deleteEmptyProperty(params);
    params = util.changeObjectVale(params);
    return new Promise((resolve, reject) => {
      HTTP.put(url, params, config).then(response => {
        resolve(response);
      }, err => {
        reject(err);
      })
        .catch((error) => {
          reject(error)
        })
    })
  },
  patch(url, params = {}, config = {}) {
    params = util.deleteEmptyProperty(params);
    return new Promise((resolve, reject) => {
      HTTP.patch(url, params, config)
        .then(response => {
          resolve(response);
        }, err => {
          reject(err);
        })
        .catch((error) => {
          reject(error)
        })
    })
  },
  downLoad(url) {//
    axios({
      method: 'get',
      url: url,
      responseType: 'blob'
    }).then(response => {

    }).catch((error) => {

    })
  },
  getAxios(option) {
    return axios;
  },
  isHide(show) {
    isShow = show;
    return this;
  }

}


