import axios from '../axios'

/* 
 * IVU
 */

// 
export const save = (data) => {
    return axios({
        url: '/vcpalgaccess/ivu/save',
        method: 'post',
        data
    })
}
// 
export const update = (data) => {
    return axios({
        url: '/vcpalgaccess/ivu/update',
        method: 'put',
        data
    })
}
// 
export const batchDelete = (params) => {
    return axios({
        url: '/vcpalgaccess/ivu/delete',
        method: 'delete',
		params
    })
}
// 
export const findPage = (data) => {
    return axios({
        url: '/vcpalgaccess/ivu/findByPage',
        method: 'post',
        data
    })
}

// 
export const findOne = (params) => {
    return axios({
        url: '/vcpalgaccess/ivu/findOne',
        method: 'get',
		params
    })
}




