<template>
  <div class="captureMsg"> 
      <div class='captureMsg-body'> 
          <el-row>
            <el-col :span="12" class='captureMsg-body-left'>
                <div class="hit-img-real">
                    <img :src="formData.FaceImageUrl" />
                </div>
            </el-col>
            <el-col :span="12" class='captureMsg-body-right'> 
                <div>{{`${formData.DeviceID}`}}</div>
                <div>{{`${formData.AreaCode}`}}</div>
                <div>{{`${formData.LocationMarkTime}`}}</div>
                <div class='optButton' v-if="weatherLib"> 
                    <img :src="img.loc" @click="getMap(formData.getMap)" class='firstImg'/>
                    <img :src="img.comparise" @click="getComarise(formData)"/>
                    <img :src="img.user" />
                    <img :src="img.film" />
                    <img :src='img.library' @click="getAddLibrary(formData.getMap)">
                </div> 
            </el-col> 
          </el-row>
          <div class='captureMsg-bottomImg'> 
            <img :src="formData.ImageUrl" />
          </div> 
      </div>
      <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
        <faceOpt  ref='formFace' :pData='msgDialogInfo'  @equit='equit'/>
      </msgDialog>
  </div>
</template>

<script>
import msgDialog from '@/views/xinjiang/common/components/dialog/msgDialog' 
import faceOpt from '@/views/xinjiang/faceLibrary/opt/faceOpt'

export default {
  components:{msgDialog, faceOpt},
  props:{
      pData:{
          type: Object,
          default: () =>{}
      }
  },
  data(){
    return {
        msgInfo: '',
        weatherLib: true,
        img:{
            loc: require('../../../assets/images/loc.png'), 
            comparise: require('../../../assets/images/pic.png'),
            user: require('../../../assets/images/user.png'),
            film: require('../../../assets/images/video.png'), 
            library: require('../../../assets/images/lib.png')
        }, 
        formData:{},
        imgBase64: '',
        dialogVisible:{
            msgDialog: false 
        },
        msgDialogInfo:{},
        msgDialog:{
            title: '',
            dialogVisible: true,
            width: '600px' 
        }, 
        percentage:{
            range: 75, 
            paddingWidth: 20,
            width: 150,
            colorPrecent: '#4dd0f3'
        },  
    }
  },
  watch:{},
  computed:{},
  methods:{
    getAddLibrary(obj){  
        this.$util.imagePathToBase64(obj.imgBase64).then(res=>{
            this.imgBase64 = res;    
            this.msgDialogInfo = {type: '', data: {imageUrl: this.imgBase64}};
            this.dialogVisible.msgDialog = true;
        });
    },
    equit(){
        this.dialogVisible.msgDialog = false;
    },
    getMap(obj){  
        this.$util.imagePathToBase64(obj.imgBase64).then(res=>{
            this.imgBase64 = res;  
            let mapObj = {imgBase64: this.imgBase64,AreaCode: obj.AreaCode, DeviceID: obj.DeviceID}; 
            this.$router.push({ path: '/newIndex/newTrajectory', query: {data: mapObj}});//
        });
        
    },
    getComarise(obj){   
        this.$router.push({ path: '/newIndex/newFace/mainLib', query: {data: {imgBase64: obj.FaceImageUrl}}}); 
    },
  },
  created(){
      if(this.pData){ 
        this.formData = this.pData;
        /* this.$http.get(`/regisger-object/get-area-name/${this.formData.AreaCode}`).then(response =>{
            this.formData.AreaCode = response.data;
        });  */ 
        this.pData.type == '' ? this.weatherLib = false : this.weatherLib = true;
        console.log(this.formData)
      }
  },
  mounted(){}
}
</script>
<style lang="scss" scoped>
.captureMsg{
    .captureMsg-body{
      margin-top: 10px;
      .el-row{
        margin-bottom: 5px;
        .captureMsg-body-left{
            width: 32%;   
            img{
            width: 97%;
            height: auto;
            }
        }
        .captureMsg-body-right{ 
            text-align: left;
            margin-left: 3%;
            width: 65%; 
            color: #fff;
            div{
                height: 40px;
            }
            /* img{
                width: 97%;
                height: auto;
            }  */
            .optButton{
                img{
                    width: 33px;
                    height: 33px;
                    padding: 8px 12px;
                    margin-left: 5px;
                    cursor: pointer;
                    border:1px solid #0e94d2;
                    border-radius: 5px;
                    background: #201335;
                }
                .firstImg{
                   margin-left: 0px;
                }
            } 
        } 
      } 
      .captureMsg-bottomImg{
          img{
              width: 100%;
              height: 250px;
          }
      }
    }  
} 
</style>


