<template>
  <div>
    <div style="width:100%">
    <ul>
      <li>
        <span style="width: 20%;display: inline-block;text-align:left"></span>
        <span style="width: 38%;display: inline-block;text-align:center"></span>
        <span style="width: 38%;display: inline-block;text-align:center"></span>
      </li>
    </ul>
    </div>
    <div class="echartsRight">
      <vueSeamless :data="APIIpList" :class-option="option">
        <ul >
          <li v-for="(item, index) in APIIpList" :key="index">
            <span style="width:20%;display: inline-block;text-align:left" v-text="item.alarmType.text"></span>
            <span style="width: 38%;display: inline-block;text-align:center" v-text="item.name"></span>
            <span style="width:38%;display: inline-block;text-align:center" >{{$util.formatDateByArg(item.createdDate, "yyyy-MM-dd")}}</span>
          </li>
        </ul>
      </vueSeamless>
    </div>
  </div>
</template>

<script>
import vueSeamless from "vue-seamless-scroll";
export default {
  components: {
    vueSeamless
  },
  name: "wrap",
  data() {
    return {
      datas: [],
      option: {
        step: 0.5,
        limitMoveNum: "",
        openTouch: false,
        waitTime: 0,
        direction: 1,
        singleHeight: 0
      },
      APIIpList: [
      ]
    };
  },
  computed: {},
  created() {
    this.getData()
  },
  methods: {
    getData(){
      this.$http.get("/operation-log" )
        .then(response => {
          this.APIIpList = response.data.data;
          this.option.limitMoveNum = this.APIIpList.length;
        });
    }
  }
};
</script>
<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
  width: 99%;
}
li {
  list-style-type: none;
  color:white;
}
p {
  margin: 0;
  height: 30px;
  width: 100%;
}
.content p {
  position: absolute;
}
.echartsRight {
  overflow: hidden;
  height: 90px;
  width: 100%;
  color: #666666;
  line-height: 20px;
}
.echartsRight span {
  font-size: 10px;
}
.echartsRight .rowup {
  -webkit-animation: 10s rowup linear infinite normal;
  animation: 10s rowup linear infinite normal;
  position: relative;
}
</style>



