<template>
  <el-aside class="aside-check-tree">
        <div class="tree-nav" v-if="dataTreeConfig.isNav"><template><slot name="tree-name"></slot></template></div>
        <div class="tree-body">
           <el-tree
                class="filter-tree"
                :data="dataTree"
                @node-click="handleNodeClick"
                @check="handleNodeCheck"
                ref="tree"
                :props="defaultProps"
                default-expand-all
                show-checkbox
                :node-key="defaultOption.id"
               :expand-on-click-node="false"
               :check-strictly="isCheck"
            >
            <span class="custom-tree-node" slot-scope="{node,data}">
               <span>
                   <!-- <i v-if="data.type==1" :class="node.icon==''?'fa icon-home':node.icon"></i>
                    <i v-else-if="data.type==5" :class="node.icon==''?'fa icon-u2142':node.icon"></i>-->
                    <span class="tree-name"> {{data[defaultOption.name]}}

                      <slot name="treeHandle" :data="data"></slot>
                    </span>
                    
                </span>  
                 <!---->
                <span>
                  <el-button
                    v-if ="dataTreeConfig.add && dataTreeConfig.currentIndex==data[defaultOption.id]"
                    type="text"
                    size="mini"
                    @click="() => opt(node,data,'add')">
                  
                  </el-button>
                 
                  <el-button
                    v-if ="dataTreeConfig.edit && dataTreeConfig.currentIndex==data[defaultOption.id]"
                    type="text"
                    size="mini"
                    @click="() => opt(node,data,'edit')"
                  >
                    
                  </el-button>
                  <el-button
                   v-if ="dataTreeConfig.del && dataTreeConfig.currentIndex==data[defaultOption.id]"
                    type="text"
                    size="mini"
                    @click="() => opt(node,data,'del')"
                  >
                    
                  </el-button>
                </span>        
            </span>
            </el-tree>
        </div>
    </el-aside>
</template>
<script>
/**
 * @module asideCheckTree
 * @description element ui checktree 
 * @author huangpx
 */
/** 
 * 
 * @param //tree-name ->
 * @param //treeHandle ->
 *  <aside-tree :dataTree="opt.editDatas.dataTree" :dataTreeConfig =" opt.editDatas.dataTreeConfig" >
    <template slot="tree-name" ></template>
    <template slot="treeHandle" slot-scope="treeData">
      <i v-if="treeData.data.type==1" :class="treeData.data.icon==''||treeData.data.icon==undefined?'fa icon-home':treeData.data.icon"></i>
      <i v-else-if="treeData.data.type==5" :class="treeData.data.icon==''||treeData.data.icon==undefined?'fa icon-no_out':treeData.data.icon"></i>
      <i v-else-if="treeData.data.level==1|treeData.data.level==2" :class="treeData.data.icon==''||treeData.data.icon==undefined?'fa icon-home':treeData.data.icon"></i>
      <i v-else-if="treeData.data.level==3" :class="treeData.data.icon==''||treeData.data.icon==undefined?'fa icon-bar_ico_storeys':treeData.data.icon"></i>
      <i v-else :class="treeData.data.icon==''||treeData.data.icon==undefined?'fa icon-u344':treeData.data.icon"></i>
      {{treeData.data.orgName}}
    </template> 
</aside-tree>
*/
export default {
  name: "asideCheckTree",
  props: {
    /**
     * @prop {Array} dataTree 
     * @prop {Array} dataDefaultTree id[x,x,x,x]
     * @prop {Object}dataTreeConfig{
     * @prop {Boolean} add 
     * @prop {Boolean} edit 
     * @prop {Boolean} edit 
     * @prop {Number} currentIndex 
     * @prop {Boolean} isNav 
     * @prop {Boolean} isRadio 
     * } 
     * @prop {Object} dataTreeOption id name { children: "children",
        label: "name"}
     */
    dataTree: {
      type: Array,
      default: () => {
        [];
      }
    },
    dataDefaultTree: {
      type: Array,
      default: () => {
        [];
      }
    },
    dataTreeConfig: {
      default: () => {
        return {
          add: false,
          edit: false,
          del: false,
          currentIndex: "",
          isNav: false,
          isRadio: false
        };
      }
    },
    dataTreeOption: {
      default: () => {
        return {};
      }
    }
  },
  data() {
    /**
     * @param {Array} treeCheckDataList - 
     * @param {Boolean}isCheck 
     * @param {} defaultProps 
     * @param {Object} defaultOption  idnameidname ,idorgId,orgNamedataTreeOption
     */
    return {
      treeCheckDataList: [],
      isCheck: false,
      defaultProps: {
        children: "children",
        label: "name"
      },
      defaultOption: {
        id: "id",
        name: "name"
      }
    };
  },
  created() {
    this.defaultOption = Object.assign(this.defaultOption, this.dataTreeOption);
  },
  mounted() {
    this.getCheck();
  },
  updated() {
    //console.log("", this.dataDefaultTree);
    this.getCheck();
  },
  methods: {
    /**
     * @method getCheck
     * @description 
     * @param {}treeCheckData -
     * 
     */
    getCheck: function() {
      this.treeCheckDataList = [];
     
          
      if (
        Object.prototype.toString.call(this.dataDefaultTree) ===
          "[object Array]" &&
        this.dataDefaultTree.length != 0
      ) {
        for (let i = 0, len = this.dataDefaultTree.length; i < len; i++) {
          this.$refs.tree.setChecked(this.dataDefaultTree[i][this.defaultOption.id], true, false);
          if (this.dataDefaultTree[i] != "") {
            //let a = { [this.defaultOption.id]: this.dataDefaultTree[i] };
            this.treeCheckDataList.push(this.dataDefaultTree[i]);
          }
        }
      } else {
        this.$refs.tree.setCheckedKeys([]);
      }
      this.$emit("treeCheckData", this.treeCheckDataList);
    },
    /**
     * @method opt
     * @description 
     */
    opt: function(node, data, type) {
      this.$emit("treeOptData", node, data, type);
    },
    /**
     * @method  handleNodeClick
     * @description 
     */
    handleNodeClick(data) {
      this.$emit("treeClickData", data);
    },
    /**
     * @method  handleNodeCheck
     * @description 
     */
    handleNodeCheck(data, node) {
      if (this.dataTreeConfig.isRadio) {
        //
        let total = this.$refs.tree.getCheckedNodes(false, true);
        for (let i = 0; i < total.length; i++) {
          this.$refs.tree.setChecked(total[i], false, false);
        }
        this.$refs.tree.setChecked(data[this.defaultOption.id], true, false);
        this.$emit(
          "treeCheckData",
          this.$refs.tree.getCheckedNodes(false, true),
          this.$refs.tree.getHalfCheckedKeys(),
          data,
          node
        );
      } else {
        this.$emit(
          "treeCheckData",
          this.$refs.tree.getCheckedNodes(false, true),
          this.$refs.tree.getHalfCheckedKeys(),
          data,
          node
        );
      }
    },
    updatedData(data, checked = false, deep = false) {
      for (let i = 0; i < data.length; i++) {
        this.$refs.tree.setChecked(data[i], checked, deep);
      }
    }
  }
};
</script>
<style>
.aside-check-tree {
  width: calc(100% - 20px) !important;
  height: calc(100% - 20px);
  max-height: 400px;
  overflow: auto;
  margin: 0 auto;
}
</style>
<style scoped>
.tree-nav {
  background-color: #f7f7f7;
  height: 50px;
  line-height: 50px;
  box-sizing: border-box;
  border: 1px solid #dddddd;
}
.aside-tree {
  width: 20%;
  box-sizing: border-box;
  border: 1px solid #dddddd;
  margin: 10px;
  height: calc(100% - 20px);
}
.aside-tree .tree-body {
  width: 100%;
  height: calc(100% - 50px);
  overflow: auto;
}
.aside-tree .tree-body .nav-top-grey {
  text-align: center;
}
.aside-tree .tree-body .el-tree {
  width: 100%;
  height: 100%;
}
</style>




