class Chain {
    constructor(obj){
        this.current = obj;
        this.next = null;
    }

    run(scope,arg){
        let result = this.current.apply(scope,[arg]);
        if(result){
            console.log(`:${arg}  ${result}`);
            return result;
        }else{
            console.log(":"+result);
            result = this.next.run(scope,arg);
            return result;
        }
    }

    setNext(obj){
        this.next = obj;
    }
}

export default Chain;


