<template>
    	<div class="zzjggl">
      <div class="panel">
        <div class="base-search">
            <label></label>
            <span class="m-input">
                <el-input class="w-200" v-model="selectData.roleName" placeholder=""></el-input>
            </span>
            
            <a href="javascript:void(0)" class="btn-search"  @click="search()" v-if="permissionResource.hasPermission('RETRIEVE')"></a>    
            <a href="javascript:void(0)" class="btn-add" @click="add()" v-if="permissionResource.hasPermission('CREATION')"></a>       
            <a href="javascript:void(0)" class="btn-del fr" @click="batDel()" v-if="permissionResource.hasPermission('DELETION')"></a>
        </div>
        <div class="line"></div>
        <div>
            <table-grid :option="option" ref="tableGrid">
                <template slot-scope="row">
                    <a href="javascript:void(0)" @click="config(row.rowData)" v-if="permissionResource.hasPermission('UPDATING')" title="">
                      <i class="btn-icon icon-19"></i>
                    </a>
                    <a href="javascript:void(0)" @click="edit(row.rowData)" v-if="permissionResource.hasPermission('UPDATING')" title="">
                      <i class="btn-icon icon-16"></i>
                    </a>
                    <a href="javascript:void(0)" @click="del(row.rowData)" v-if="permissionResource.hasPermission('DELETION')" title="">
                      <i class="btn-icon icon-17"></i>
                    </a>
                </template>
            </table-grid>
        </div>
      </div>
      <msg-dialog v-if="configData.dialogVisible.msgDialog" :pDialog='configData.msgDialog' @equit="equit">
        <role-menu-opt :pData='configData.msgDialog.data' @result="dialogOpt"></role-menu-opt>
      </msg-dialog>
      <msg-dialog v-if="opt.dialogVisible.msgDialog" :pDialog='opt.msgDialog' @equit="equit">
        <role-opt :pData='opt.msgDialog.data'  @result="dialogOpt"></role-opt>
      </msg-dialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import roleOrMenuOpt from "@/views/xinjiang/system/opt/roleOrMenuOpt";
import roleOpt from "@/views/xinjiang/system/opt/roleOpt";
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(
      vm.$route.path
    );
    return {
      permissionResource: permissionResource,
      configData: {
        dialogVisible: {
          msgDialog: false
        },
        msgDialog: {
          title: "",
          dialogVisible: true,
          width: "520px",
          data: {}
        }
      },
      opt: {
        dialogVisible: {
          msgDialog: false
        },
        msgDialog: {
          title: "",
          dialogVisible: true,
          width: "520px",
          data: {}
        }
      },
      option: {
        isShowTableNav: false,
        tableOption: {
          singleSelect: true,
          isShowSelect: true,
          tableTitle: [
            { prop: "id", label: "ID" },
            { prop: "name", label: "" },
            { prop: "remark", label: "" },
            { prop: "createdBy", label: "" },
            {
              prop: "createdDate",
              label: "",
              format: function(row, column, cellValue, index) {
                return (
                  <div>
                    {vm.$util.formatDateByArg(
                      row.createdDate,
                      "yyyy-MM-dd hh:mm:ss"
                    )}
                  </div>
                );
              }
            }
          ],
          tableBodyOverFlow: false,
          fixedHeight: 360,
          hasCustomColum:
            permissionResource.hasPermission("UPDATING") ||
            permissionResource.hasPermission("DELETION")
              ? true
              : false,
          opeColumnWidth: 220
        },
        customLabel: "",
        url: "/role",
        type: "get",
        notRequestSever: false,
        param: {}
      },
      selectData: {}
    };
  },
  mounted() {},
  methods: {
    config(row) {
      this.configData.msgDialog.data.methods = "put";
      this.configData.msgDialog.data.url = `/role/${row.id}`;
      this.configData.msgDialog.data.data = row;
      this.configData.dialogVisible.msgDialog = true;
    },
    equit() {
      this.configData.dialogVisible.msgDialog = false;
      this.opt.dialogVisible.msgDialog = false;
    },
    add() {
      this.$util.clearObjectValue(this.opt.msgDialog.data.data);
      this.opt.msgDialog.title = "";
      this.opt.msgDialog.data.methods = "post";
      this.opt.msgDialog.data.url = "/role";
      this.opt.dialogVisible.msgDialog = true;
    },
    edit(row) {
      this.opt.msgDialog.title = "";
      this.opt.msgDialog.data.methods = "put";
      this.opt.msgDialog.data.url = `/role/${row.id}`;
      this.opt.msgDialog.data.data = row;
      this.opt.dialogVisible.msgDialog = true;
    },
    del(row) {
      let id = row.id;
      this.delData(id);
    },

    batDel() {
      let allData = [];
      this.$refs.tableGrid.getSelectedRow().forEach(e => {
        allData.push(e.id);
      });
      if (
        Object.prototype.toString.call(allData) === "[object Array]" &&
        allData.length === 0
      ) {
        this.$message({
          message: "",
          type: "warning"
        });
        return;
      }
      this.delData(allData.join(","));
    },
    delData(id) {
      let vm = this;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http.delete(`/role/${id}`).then(response => {
          this.$message({
            message: response.data.msg || "",
            type: "success"
          });
          vm.$refs.tableGrid.flushTableData();
        });
      });
    },
    search() {
      this.$refs.tableGrid.searchTableData(Object.assign(this.selectData));
    },
    dialogOpt(_data) {
      if (_data) {
        this.$refs.tableGrid.flushTableData();
        this.equit();
      }
    }
  },
  components: {
    "table-grid": tableGrid,
    "msg-dialog": msgDialog,
    "role-menu-opt": roleOrMenuOpt,
    "role-opt": roleOpt
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
  .zzjggl {
    float: right;
    width: calc(100% - 190px);
    height: calc(100vh - 120px);
    overflow: hidden;
    .panel {
      height: 100%;
      width: calc(100% + 20px);
      padding-right: 21px;
      overflow: auto;
    }
  }
  .t-head,
  .base-search {
    text-align: left;
  }
}
</style>



