<template>
  <div ref="line" :style="{width: '100%', height: '100%'}"></div>
</template>

<script>
export default {
  name: "guTags",
  data() {
    // 
    let echarts = require("echarts/lib/echarts");
    // 
    require("echarts/lib/chart/bar");
    // title
    require("echarts/lib/component/tooltip");
    require("echarts/lib/component/title");
    return {
      data: []
    };
  },
  mounted() {
    this.getData();
  },
  created() {},
  methods: {
    getData() {
      this.$http.get("/big-screen/brand").then(response => {
        var d1 = Object.keys(response.data);
        var arr1 = [];
        var obj = {};
        for (var i = 0; i < d1.length; i++) {
          obj.name = d1[i];
          obj.value = response.data[d1[i]];
          this.data.push(obj);
          obj = {};
        }
        console.log(this.data, "pie");
        this.drawLine();
      });
    },
    drawLine() {
      // domecharts
      let line = this.$echarts.init(this.$refs.line, "shine");
      // 
      var option = {
        color: ["#ffdb5c", "#67e0e3", "#e062ae", "#ff9f7f", ],
        title: {},

        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)"
        },
        legend: {
          orient: "horizontal",
          left: "center",
          data: ["", "", "", ""],
          bottom: "bottom",
          itemWidth: 5,
          itemHeight: 4,
          formatter: "{name}",
          textStyle: {
            color: "#FFFFFF"
          }
        },
        xAxis: {
          show: false
        },
        yAxis: {
          show: false
        },

        series: [
          {
            barWidth: 20,
            data: this.data,
            roseType: "radius",
            name: "",
            type: "pie",
            radius: "90%",
            areaStyle: {},
            label: {
              normal: { show: true, position: "inner", formatter: "{d}%" }
            }
          }
        ]
      };
      line.setOption(option, true);
    }
  }
};
</script>
<style>
</style>
<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 10px;
  width: 99%;
}
</style>



