<template>
    <div class="zzjggl">
      <div class="panel">
            <div class="base-search">
              <label>IVU</label>
              <span class="m-input">
                  <el-input class="w-200" v-model="searchForm.ivuName" placeholder=""></el-input>
              </span>
              <label></label> 
              <el-select v-model="searchForm.algCode" popper-class="m-select-con" placeholder="">
                <el-option
                  v-for="item in deviceTypeData"
                  :key="item.value"
                  :label="item.text"
                  :value="item.value"
                ></el-option>
              </el-select>
              <label></label> 
              <el-select v-model="searchForm.mfCode" popper-class="m-select-con" placeholder="">
                <el-option
                  v-for="item in devicefactory"
                  :key="item.value"
                  :label="item.text"
                  :value="item.value"
                ></el-option>
              </el-select>
              <a href="javascript:void(0)" class="btn-search"  @click="search"></a>
              <a href="javascript:void(0)" class="btn-add" @click="add" v-if="permissionResource.hasPermission('CREATION')"></a>
              <a href="javascript:void(0)" class="btn-del fr" v-if="permissionResource.hasPermission('DELETION')"></a>
            </div>
            <div class="line"></div>           
            <div>
                <table-grid :option="option" ref="ivuList">
                    <template slot-scope="row">
                      <!-- <a href="javascript:void(0)"  title="">
                        <i class="btn-icon icon-12"></i>
                      </a>
                      <a href="javascript:void(0)" title="">
                        <i class="btn-icon icon-13"></i>
                      </a> -->
                      <a href="javascript:void(0)" title="" @click="edit(row)" v-if="permissionResource.hasPermission('UPDATING')">
                        <i class="btn-icon icon-16"></i>
                      </a>
                      <a href="javascript:void(0)" title="" @click="deleteTab(row)" v-if="permissionResource.hasPermission('DELETION')">
                        <i class="btn-icon icon-17"></i>
                      </a>
                      <!-- <a href="javascript:void(0)" title="">
                        <i class="btn-icon icon-14"></i>
                      </a>  -->
                    </template>
                </table-grid>
            </div>
        </div>
        <msgDialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
            <dialogEdit :pData='msgDialogInfo' @equit='equit'></dialogEdit>
        </msgDialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogEdit from "@/views/xinjiang/analysis/opt/ivuAdd"; 

export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      searchForm:{
        ivuName: '',
        algCode: '',
        mfCode: ''
      },
      dialogVisible:{
        msgDialog: false
      },
      msgDialogInfo:{

      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "520px",
        data: ""
      },
      deviceTypeData:[{
        text: '',
        value: 'FACE_RECOGNITION' 
      },{
        text: '',
        value: 'VEHICLE_RECOGNITION'
      }],
      devicefactory:[],
      option: {
        isShowTableNav: false,
        tableOption: {
          singleSelect: true,
          isShowSelect: true, 
          hasCustomColum: true,
          tableTitle: [
            { prop: "ivuName", label: "IVU" }, 
            { prop: "algCode", label: "",
              format: function(row, column, cellValue, index) {
                  return (
                    <div>
                      {row.algCode == 'FACE_RECOGNITION' ? '' : ''}
                    </div>
                  );
                }
            },
            { prop: "mfCode", label: "" },
            { prop: "serverId", label: "" },
            { prop: "algVersion", label: "" },
            /* { prop: "videoNum", label: "" }, */
            { prop: "validDate", label: "" }
          ],
          opeColumnWidth: 220,
          customRes: true
        },
        customLabel: "",
        url: "/vcpalgaccess/ivu/findByPage",
        type: "post",
        notRequestSever: false,
        param: {
          /*pageSize: 10,
          pageNum: 1,
          para: {
            algCode: "FACE_RECOGNITION",
            mfCode: "HUAWEI"
          }*/
        }
      }
    };
  },
  methods: {
    deleteTab(row){ 
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`/vcpalgaccess/ivu/delete`,{id: row.rowData.id}).then(response =>{
            if(response.data.resultCd == 0){
              this.$notify({
                title: '',
                message: '!!',
                type: 'success'
              });
              this.equit();
            } 
          })
        });
    }, 
    add(){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: ''};
      this.dialogVisible.msgDialog = true;
    },
    edit(row){
      this.msgDialog = {
        title: "",
        dialogVisible: true,
        width: "520px" 
      },
      this.msgDialogInfo = {type: '', data: row.rowData};
      this.dialogVisible.msgDialog = true;
    },
    equit() {
      this.$refs.ivuList.flushTableData();
      this.dialogVisible.msgDialog = false;
    },
    search() {
      //let param = {}; 
      if (this.searchForm) { 
        this.option.param = Object.assign({pageSize: 10, pageNum: 1,},{para: this.searchForm});
      }  
      this.$refs.ivuList.searchTableData(this.option.param);
    },
    dataInit(){
      this.$http.get(`/regisger-object/constant`).then(response =>{
          this.devicefactory = (response.data.filter(item => item.id == 'Brand'))[0].options.map(item => (({text, value}) => ({text, value}))(item));  
      })
    }
  },
  mounted(){
    this.dataInit();
  },
  components: {
    "table-grid": tableGrid,
    msgDialog,
    dialogEdit
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page .t-head,.base-search{
  text-align:left;
}
</style>




