<template>
  <div class="mapRoot">
    <!-- <span class="back" v-if="isBack" @click="back"></span> -->
    <span class="back" v-if="isClickArea" @click="areaBack"></span>
    <div class="fly-line-map" ref="flyLineMap"></div>
  </div>
</template>

<script>
export default {
  props: {
    hasline: {
      type: Boolean,
      default: true
    },
    deviceUrl:{
      type : String,
      default : "/big-screen/device-distribution/"
    }
  },
  data() {
    let vm = this;
    return {
      //areaUrl: "/big-screen/map-distribution/6530",
      //deviceUrl: "/big-screen/device-distribution/",
      id: vm.$areaId,
      isBack: true,
      isClickArea : false,
    };
  },

  mounted() {
    this.requstData(this.deviceUrl+this.id, this.id);
  },

  methods: {
    requstData(url, id) {
      this.$http.get(url).then(res => {
        let data = res.data ? res.data : {};
        this.createMap(data, id);
      });
    },

    createMap(resData, id) {
      let mapData = require(`./mapData/${id}_full.json`);
      let moveLine = this.getMoveLine(resData);
      let geoCoordMap = this.getGeoCoordMap(moveLine);
      let data = this.getData(resData);
      let deviceData = this.getDeviceData(moveLine);
      let option = {};
      if (this.isBack) {
        option = this.getOption(geoCoordMap, deviceData, moveLine, data);
      } else {
        option = this.getOption(geoCoordMap, data, moveLine, data);
      }
      let mychart = this.$echarts.init(this.$refs.flyLineMap);
      this.$echarts.registerMap("xinjiang", mapData);
      mychart.setOption(option);
      mychart.on("click", params => {
        if (this.isBack) {
          if (params.componentSubType == "effectScatter") {
            if (!params.data.deviceId) {
              return;
            }
            this.$emit("clickDevice", params.data.deviceId);
          }else{
            if (params.componentSubType == "map") {
              this.isClickArea = true;
              this.$emit("clickMap", params.data.id);
            }
          }
          return;
        } else {
          if (params.componentSubType == "map") {
            this.isBack = true;
            this.requstData(this.deviceUrl + params.data.id, params.data.id);
            this.$emit("clickMap", params.data.id);
          }
        }
      });
    },

    getGeoCoordMap(moveLine) {
      var geoCoordMap = {};
      for (let i = 0; i < moveLine.length; i++) {
        if (i == 0) {
          geoCoordMap[moveLine[i].toName] = moveLine[i].coords[1];
        }
        geoCoordMap[moveLine[i].fromName] = moveLine[i].coords[0];
      }
      return geoCoordMap;
    },

    getMoveLine(resData) {
      var moveLine = [];
      var data = resData.fromToDatas ? resData.fromToDatas : [];
      if (!this.isBack) {
        for (let i = 0; i < data.length; i++) {
          if (data[i].value) {
            moveLine.push(data[i]);
          }
        }
      } else {
        moveLine = data;
      }

      return moveLine;
    },

    getData(resData) {
      var data = resData.areaDistributions ? resData.areaDistributions : [];
      return data;
    },

    getDeviceData(moveLine) {
      let deviceData = [];
      for (let i = 0; i < moveLine.length; i++) {
        let device = {};
        if (i == 0) {
          deviceData.push({ name: moveLine[i].toName, value: 1 });
        }
        device["name"] = moveLine[i].fromName;
        device["value"] = 1;
        device["deviceId"] = moveLine[i].deviceId;
        deviceData.push(device);
      }
      return deviceData;
    },

    getOption(geoCoordMap, lightData, moveLine, mapData) {
      let option = {
        title: {
          top: 20,
          text: "",
          subtext: "",
          x: "center",
          textStyle: {
            color: "#ccc"
          }
        },

        tooltip: {
          trigger: "item",
          formatter: function(params) {
            if (!params|| !params.data || typeof params.data != "object") {
              return "";
            }
            if (typeof params.value[2] == "undefined") {
              return params.name + " : " + params.value;
            } else {
              return params.name + " : " + params.value[2];
            }
          }
        },

        geo: {
          show: true,
          map: "xinjiang",
          zoom: 1.25,
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: false
            }
          },

          itemStyle: {
            normal: {
              areaColor: "#001A3F",
              // borderColor: "#60b5d6",
              borderColor: "#0297CE",
              shadowColor: "rgba(5,148,205, 0.5)",
              borderWidth: 1.5,
              opacity: 0.8
            },
            emphasis: {
              areaColor: "#2B91B7",
             
            }
          }
        },
        series: [
          {
            name: "light",
            type: "effectScatter",
            coordinateSystem: "geo",
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke"
            },
            data: this.convertData(geoCoordMap, lightData),
            symbolSize: function(val) {
              let size = val[2] / 12;
              if (size < 12) {
                size = 12;
              } else if (size > 17) {
                size = 17;
              }
              return size;
            },

            label: {
              normal: {
                formatter: "{b}",
                position: "right",
                show: false
              },
              emphasis: {
                show: true
              }
            },
            itemStyle: {
              normal: {
                color: "#5eab48",
                opacity: 1
              }
            }
          },

          {
            type: "map",
            map: "xinjiang",
            geoIndex: 0,
            label: {
              normal: {
                show: false
              },
              emphasis: {
                show: false,
                textStyle: {
                  color: "#fff"
                }
              }
            },
            roam: true,
            itemStyle: {
              normal: {
                areaColor: "#031525",
                borderColor: "#FFFFFF"
              },
              emphasis: {
                areaColor: "#2B91B7",
              }
            },
            animation: false,
            data: mapData
          }
        ]
      };
      if (this.hasline || this.isBack) {
        option.series.push({
          name: "",
          type: "lines",
          coordinateSystem: "geo",
          zlevel: 2,
          large: true,
          effect: {
            //
            show: true,
            period: 6,
            trailLength: 0.1,

            symbolSize: 4,
            color: "#00f5fe",
            constantSpeed: 0,
            loop: true
          },

          lineStyle: {
            //
            normal: {
              color: "#00fff0",
              width: 2,
              opacity: 0.1,
              curveness: 0.15
            }
          },
          data: moveLine
        });
      }
      return option;
    },

    convertData(geoCoordMap, data) {
      var res = [];
      for (var i = 0; i < data.length; i++) {
        var geoCoord = geoCoordMap[data[i].name];
        if (geoCoord) {
          res.push({
            deviceId: data[i].deviceId,
            name: data[i].name,
            value: geoCoord.concat(data[i].value)
          });
        }
      }
      return res;
    },

    back() {
      this.isBack = false;
      this.requstData(this.areaUrl, this.id);
      this.$emit("clickMap", this.id);
    },

    areaBack(){
      this.isClickArea = false;
      this.$emit("clickMap", this.id);
    }
  },

  watch: {},

  computed: {},

  components: {}
};
</script>

<style  scoped>
.mapRoot {
  position: relative;
  width: 100%;
  height: 100%;
}
.back {
  position: absolute;
  width: 77px;
  height: 33px;
  line-height: 33px;
  right: 30px;
  color: #fff;
  cursor: pointer;
  background: url("../../../../assets/img/login/btn_box_09.png") 0 0 no-repeat;
  z-index: 10;
}
.fly-line-map {
  width: 100%;
  height: 100%;
}
</style>




