<template>
    <div class="dropDownTreeRoot">
        <el-input :value="showName" placeholder="" :style="{width : width}"  @focus="changeTreeDom(true)"></el-input>
        <synTree :option="treeOption" @clickNode="selectNode" v-show="showTree" class="dropDownTreeDom" :style="{width : width}" @finishLoadTree="finishLoadTree"></synTree>
    </div>
</template>

<script>
import synTree from '../tree/synTree'
export default {
    props:{
        value : {
            required : true
        },
        treeOption :{
            type : Object,
            required : true
        },
        prop:{
            type : Object,
            default : ()=>{
                return {
                    name : 'name',
                    value : 'value'
                }
            }
        },
        width:{
            type : String,
            default : "216px"
        }
    },
    data() {
        return {
            showName : "",
            showTree : false,
            treeData : null,
        }
    },

    mounted() {

    },

    methods: {
        init(){
            
        },

        finishLoadTree(list){
            this.treeData = list;
            if(this.value){
                for(let i = 0; i < list.length; i++){
                    let node = list[i];
                    if(node[this.prop.value] == this.value){
                        this.showName  = node[this.prop.name];
                        break;
                    }
                }
            }
        },

        selectNode(data,node){
            this.showName = data[this.prop.name];
            this.$emit("input",data[this.prop.value]);
            this.changeTreeDom(false); 
        },

        changeTreeDom(flag){
            this.showTree = flag; 
        },

        getTreeData(){
            return this.treeData;
        },

        clear(){
            this.showName = "";
            this.$emit("input","");
        }

    },

    watch :{
        value : function(newVal,oldVal){
            this.finishLoadTree(this.treeData);
        }
    },

    computed :{
        
    },
    
    components :{
        synTree
    }

}
</script>

<style scoped>
.dropDownTreeRoot{
    position: relative;
}
.dropDownTreeDom{
    position:absolute;
    z-index: 1;
    background: #060e3d;
    min-height: 60px;
}
</style>




