<template>
  <div class="msgDialog">
    <el-dialog 
    append-to-body
    :close-on-click-modal='false'
    center
    :title="dialogOption.title"
    :visible.sync="dialogOption.dialogVisible"
    :width="dialogOption.width" 
    :before-close="handleClose">
    <slot></slot>  
  </el-dialog>   
  </div>
</template>

<script>
export default {
  components:{},
  props:{
    pDialog:{
      type: Object,
      default: () =>{}
    }
  },
  data(){
    return {
     dialogOption:{
       title: '',
       dialogVisible: true,
       width: '50%'
     } 
    }
  },
  watch:{},
  computed:{},
  methods:{
    handleClose(done){
      this.$emit('equit');
    }
  },
  created(){
    if(this.pDialog){
      this.dialogOption = this.pDialog;
    }
  },
  mounted(){}
}
</script>
<style lang="scss" >
@import 'dialog.scss';
</style>


