<template>
  <div class="sub-page" style="height:100%">
    <div class="alarmLeft">
      <div class="videoPlayer">
        <synTree
          :option="deviceMenu"
          class="deviceMenu"
          v-show="showDeviceMenu"
          ref="deviceTree"
          @clickNode="selectDevice"
        >
          <template slot-scope="data">
            <i
              :class="data.data.treeType.value=='AREA'? 'el-icon-place':data.data.networkState == 'ONLINE' ? 'el-icon-video-camera online' : 'el-icon-video-camera offline' "
            ></i>
          </template>
        </synTree>
        <div class="vlc-player">
          <div class="table-body" v-if="scenesUri && !showVideo">
            <div class="table-child">
              <img
                width="100%"
                :src="host+scenesUri"
                
              />
            </div>
          </div>
         
          <videoPlayer v-if="showVideo" style="height:100%;width:100%" ref="videoPlayerDom"></videoPlayer>
        </div>
        <!-- <object
          type="application/x-vlc-plugin"
          ref="vlc"
          events="True"
          class="vlc-player"
          width="100%"
          height="100%"
          classid="clsid:9BE31822-FDAD-461B-AD51-BE1D1C159921"
          codebase="http://downloads.videolan.org/pub/videolan/vlc/latest/win32/axvlc.cab"
          v-show="!showDeviceMenu"
        >
          <param name="autoplay" value="true" />
          <param name="loop" value="false" />
          <param name="fullscreen" value="false" />
        </object>-->
        <div class="menu">
          <p class="menuText" @click="openOrCloseDeviceMenu"></p>
          <div class="changeShow">
            <span :class="showVideo ? '' : 'chanegActive'" @click="changeVideoOrPhoto(false)"></span>
            <span :class="showVideo ? 'chanegActive' : ''" @click="changeVideoOrPhoto(true)"></span>
          </div>

          <div class="menuDeviceInfo" :title="':'+deviceInfo.name+'  '+deviceInfo.name">
            <span>:{{deviceInfo.parentName}}</span>
            <span>:{{deviceInfo.name}}</span> 
          </div>
          <!-- <img src="../alarmRealTimeData/img/image_tree.png" @click="openOrCloseDeviceMenu" /> -->
          <!--v-if="videoType == 'image'"-->
        </div>
      </div>
      <div class="snapPhoto">
        <div class="alarmTitle">
          <p></p>
          <div @click="jump('/newIndex/newComparison/faceCapture')" class="ponit">
            <span></span>
            <span>>></span>
          </div>
        </div>
        <div class="snapContent" ref="snaPhoto">
          <div class="snapContentItem" v-for="(item,index) in snapPhotoDatas" :key="index">
            <img :src="host+item.CatchUri" />
            <p>{{$util.formatDateByArg(item.LocationMarkTime,'hh:mm:ss')}}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="alarmRight">
      <div class="alarmTitle">
        <p></p>
        <div @click="jump('/newIndex/newComparison/newComparisonHit')" class="ponit">
          <span></span>
          <span>>></span>
        </div>
      </div>
      <div class="comparisonTitle">
        <p class="cloum1"></p>
        <p class="cloum2"></p>
      </div>
      <div class="comparisonList">
        <ul>
          <li v-for="(item,index) in comparisonPhotoDatas" :key="index">
            <div class="comparisonItem">
              <div class="snapImage">
                <img :src="host+item.CatchUri" />
              </div>
              <div class="degree">
                <el-progress
                  type="circle"
                  class="percentageCircle"
                  :width="65"
                  :color="customColors"
                  :percentage="item.HitFace.Similarity"
                ></el-progress>

                <div class="comparisonInfo">
                  <p :tilte="item.HitFace.Name">:{{item.HitFace.Name}}</p>
                  <p
                    :tilte='item.HitFace.Gender == 0 ? "" : item.HitFace.Gender == 1 ? "" : ""'
                  >:{{item.HitFace.Gender == 0 ? "" : item.HitFace.Gender == 1 ? "" : ""}}</p>
                  <p :title="item.HitFace.CredentialNumber">:{{item.HitFace.CredentialNumber}}</p>
                  <!-- <p :title="item.HitFace.CredentialNumber">:350125199808053325</p> -->
                  <p
                    :title="$util.formatDateByArg(item.LocationMarkTime,'hh:mm:ss')"
                  >:{{$util.formatDateByArg(item.LocationMarkTime,'hh:mm:ss')}}</p>
                </div>
              </div>
              <div class="baseIamge">
                <img :src="host+item.HitFace.HitFaceUri" />
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import synTree from "@/views/xinjiang/common/components/tree/synTree";
import { imageBaseUrl } from "@/utils/global";
import { dateArraySort } from "@/utils/datetime";
import videoPlayer from "@/views/xinjiang/common/components/video/videoPlayer";
export default {
  data() {
    return {
      host: imageBaseUrl,
      comparisonPhotoDatas: [],
      snapPhotoDatas: [],
      showVideo: false,
      showDeviceMenu: false,
      scenesUri: "",
      deviceMenu: {
        url: "/regisger-object/device-tree",
        param: {},
        prop: {
          children: "children",
          label: "name"
        },
        type: "get"
      },

      deviceInfo: {}
    };
  },

  mounted() {
    this.initWebSocket();
    this.getInitData();
  },

  methods: {
    customColors: percentage => {
      if (percentage <= 20) {
        return "#f9f139";
      } else if (percentage > 20 && percentage <= 40) {
        return "#50cb74";
      } else if (percentage > 40 && percentage <= 60) {
        return "#8c73fe";
      } else if (percentage > 60 && percentage <= 80) {
        return "#f48421";
      } else if (percentage > 80 && percentage <= 100) {
        return "#d1451a";
      }
    },
    getInitData(DeviceID) {
      this.$http
        .post("/vcpinterface/ConvCompHistory/FindPage", {
          DeviceID: this.deviceInfo.deviceID
        })
        .then(response => {
          response = response.data.Res;
          for (let i = 0; i < 5; i++) {
            let data = {};
            data.HitFace = {};
            if (response.Data.length < i || !response.Data[i]) {
              console.log(
                "" + response.Data.length + ""
              );
              break;
            }
            data.CatchUri = response.Data[i].FaceID + ".jpg";
            data.LocationMarkTime = response.Data[i].CreateTime;
            data.HitFace.HitFaceUri = response.Data[i].FaceHitID + ".jpg";
            data.HitFace.Name = response.Data[i].Name;
            data.HitFace.Gender = response.Data[i].Gender;
            data.HitFace.CredentialNumber = response.Data[i].CredentialNumber;
            data.HitFace.Similarity = Number(response.Data[i].Similarity);
            this.queue(this.comparisonPhotoDatas, data, 2);
            this.queue(this.snapPhotoDatas, data, 19);
            dateArraySort(this.comparisonPhotoDatas, "LocationMarkTime");
            dateArraySort(this.snapPhotoDatas, "LocationMarkTime");
          }
          this.$http
            .post("/vcpinterface/ConvFaceReceive/FindPage", {
              DeviceID: this.deviceInfo.deviceID
            })
            .then(res => {
              res = res.data.Res;
              for (let i = 0; i < 5; i++) {
                let data = {};

                if (res.data.length < i && !res.data[i]) {
                  console.log(
                    "" + response.Data.length + ""
                  );
                  break;
                }
                //this.scenesUri = res.data[i].ImageUrl;
                data.ImageUrl = res.data[i].ImageUrl;
                data.LocationMarkTime = res.data[i].LocationMarkTime;
                data.CatchUri = res.data[i].FaceImageUrl;
                this.queue(this.snapPhotoDatas, data, 19);
                dateArraySort(this.snapPhotoDatas, "LocationMarkTime");
              }
              if(this.snapPhotoDatas[0]){
                this.scenesUri = this.snapPhotoDatas[0].ImageUrl;
              }
            });
        });
    },

    openOrCloseDeviceMenu() {
      this.showDeviceMenu = !this.showDeviceMenu;
    },

    selectDevice(data, node) {
      if (data.treeType.value == "DEVICE") {
        this.deviceInfo = data;
        this.clearData();
        this.getInitData(data.deviceID);
        if (this.showVideo) {
          this.playerVideo(this.deviceInfo.deviceID);
        }
        this.showDeviceMenu = false;
        //this.$refs.videoPlayerDom.changeUrl("");
      } else {
        if (this.deviceInfo.id) {
          this.$refs.deviceTree.setCurrentNode(this.deviceInfo);
        } else {
          this.$refs.deviceTree.cancelNodeSelect();
        }
      }
    },
    clearData() {
      this.comparisonPhotoDatas = [];
      this.snapPhotoDatas = [];
      this.scenesUri = "";
    },

    playerVideo(deviceID) {
      if (this.deviceInfo.deviceID) {
        this.$http.get(`/regisger-object/rtmp/${deviceID}`).then(res => {
          this.deviceInfo.rtmpUrl = res.data;
          this.$nextTick(() => {
            if (this.$refs.videoPlayerDom) {
              this.$refs.videoPlayerDom.changeUrl(res.data);
            }
          });
        });
      }
    },

    changeVideoOrPhoto(flag) {
      this.showVideo = flag;
      if (flag) {
        this.playerVideo(this.deviceInfo.deviceID);
      }
    },

    jump(url) {
      this.$router.push({ path: url });
    },

    //
    queue(arr, data, length) {
      if (arr.length > length) {
        arr.pop();
        arr.unshift(data);
      } else {
        arr.unshift(data);
      }
    },

    initWebSocket() {
      //weosocket
      const wsuri = this.$wsUrl;
      this.websock = new WebSocket(wsuri);
      this.websock.onmessage = this.websocketonmessage;
      this.websock.onopen = this.websocketonopen;
      this.websock.onerror = this.websocketonerror;
      this.websock.onclose = this.websocketclose;
    },
    websocketonopen() {
      //send
      console.log("websocket");
    },
    websocketonerror() {
      //
      this.initWebSocket();
      console.log("websocket");
    },
    websocketonmessage(data) {
      //

      try {
        let redata = JSON.parse(data.data);
        if (this.deviceInfo.deviceID == redata.DeviceID) {
          this.queue(this.snapPhotoDatas, redata, 19);
          this.scenesUri = redata.ScenesUri;
          if (redata.Type == "ExistentHitFace") {
            this.queue(this.comparisonPhotoDatas, redata, 2);
          }
        }
      } catch (e) {
        console.log("" + data.data);
      }
    },
    websocketsend(Data) {
      //
      //this.websock.send(Data);
    },
    websocketclose(e) {
      //
      console.log("", e);
    }
  },

  beforeDestroy() {
    if (this.websock) {
      this.websock.close();
    }
  },

  watch: {},

  computed: {},

  components: {
    synTree,
    videoPlayer
  }
};
</script>

<style lang="scss" scoped>
.sub-page-content {
  background: url("../../../assets/img/login/bg1.jpg") center 0 no-repeat
    #051121;
}
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0px;
  color: #fff;
  display: flex;
}
.alarmLeft {
  flex: 1;
  height: 100%;
  margin-right:20px;
  .videoPlayer {
    background: #000;
    position: relative;
    height: calc(100% - 170px);
    .table-body {
      display: table;
      width: 100%;
      height: 100%;
    }
    .table-child {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
    }
    .deviceMenu {
      width: 250px;
      // height: 350px;
      height: calc(100% - 40px);
      overflow: auto;
      position: absolute;
      z-index: 99;
      background: rgb(0, 0, 0);
      opacity: 0.7;
    }
    .vlc-player {
      // height: 350px;
      vertical-align: middle;
      height: calc(100% - 40px);
      img {
        vertical-align: middle;
        max-height: 100%;
      }
    }
    .menu {
      background: #051121;
      padding: 5px;
      margin-top: -5px;
      height: 40px;
      position: relative;
    }
  }
  .snapPhoto {
    .snapContent {
      width: calc(100vw - 640px);
      overflow: auto;
      height: 150px;
      white-space: nowrap;
      .snapContentItem {
        display: inline-block;
        margin-left: 5px;
        margin-right: 5px;
        img {
          height: 120px;
        }
        p {
          text-align: center;
        }
      }
    }
    .alarmTitle {
      height: 20px;
      p {
        float: left;
      }
      div {
        float: right;
      }
    }
  }
}

.alarmRight {
  
  width: 430px;
  height: 100%;
  .alarmTitle {
    height: 20px;
    p {
      float: left;
    }
    div {
      float: right;
      margin-right: 33px;
    }
  }
  .comparisonList {
    height: calc(100% - 50px);
    margin-top: 10px;
    ul {
      height: 100%;
    }
  }
}

.comparisonItem {
  margin-bottom: 20px;
  .snapImage {
    display: inline;
    img {
      height: 150px;
    }
  }
  .degree {
    width: 20%;
    display: inline-block;

    position: relative;
    .percentageCircle {
      margin-top: 20px;
      margin-left: 15px;
      margin-right: 30px;
      margin-bottom: 10px;
    }
    .comparisonInfo {
      text-align: center;
      font-size: 12px;
    }
    p {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
  }
  .baseIamge {
    display: inline;
    img {
      height: 150px;
    }
  }
}

.ponit {
  cursor: pointer;
}

.comparisonTitle {
  height: 20px;
  .cloum1 {
    float: left;
  }
  .cloum2 {
    float: right;
    margin-right: 40px;
  }
}
.online {
  color: #aafa05;
}

.offline {
  color: #ccc;
}

.menuText {
  height: 30px;
  font-size: 16px;
  color: #097fda;
  cursor: pointer;
  line-height: 35px;
  float: left;
}

.changeShow {
  float: right;
  cursor: pointer;
  line-height: 35px;
}

.chanegActive {
  color: #097fda;
}
.menuDeviceInfo {
  position: absolute;
  top: 9px;
  right: 38%;
  color: #097fda;
  max-width: 300px;
  overflow: hidden;
  white-space: nowrap;
  span {
    margin-right: 10px;
  }
}

// .devcie-icon-area{
//   height: 24px;
//   width: 24px;
//   background-image: url("./img/area.png");
// }
// .device-icon-online{
//   height: 24px;
//   width: 24px;
//   background-image: url("./img/online.png");
// }

// .device-icon-offline{
//   height: 24px;
//   width: 24px;
//   background-image: url("./img/offline.png");
// }
</style>




