export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(vm.$route.path);
    return {
      permissionResource : permissionResource,
      areaId: vm.$areaId,
      sceneCodeEnumList: [],
      areaOption: {
        lineShow: false,
        pieShow: false,
        xAxisList: [],
        list: [],
        listPie: [],
        listPieCount: 0,
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        treeList: [],
        dataSearch: {},
        endOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.areaOption.dataSearch.StartTime))
              return (
                new Date(this.areaOption.dataSearch.StartTime).getTime() >
                date.getTime()
              );
          }
        },
        startOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.areaOption.dataSearch.EndTime))
              return (
                new Date(this.areaOption.dataSearch.EndTime).getTime() <
                date.getTime()
              );
          }
        }
      },
      sceneOption: {
        lineShow: false,
        pieShow: false,
        xAxisList: [],
        list: [],
        listPie: [],
        listPieCount: 0,
        searchList: [
          {
            text: "",
            value: 0,
            index: 0,
            active: true
          },
          {
            text: "",
            value: 1,
            index: 1,
            active: false
          },
          {
            text: "",
            value: 2,
            index: 2,
            active: false
          }
        ],
        treeList: [],
        dataSearch: {},
        endOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.sceneOption.dataSearch.StartTime))
              return (
                new Date(this.sceneOption.dataSearch.StartTime).getTime() >
                date.getTime()
              );
          }
        },
        startOptions: {
          disabledDate: date => {
            if (!this.$util.isEmpty(this.sceneOption.dataSearch.EndTime))
              return (
                new Date(this.sceneOption.dataSearch.EndTime).getTime() <
                date.getTime()
              );
          }
        }
      }
    };
  },
  created() {
    this.getTree();
    this.getSceneEnum();
  },
  mounted() {
    this.areaOption.dataSearch.AreaCode = this.areaId;
    this.sceneOption.dataSearch.AreaCode = this.areaId;
    this.getScene(this.sceneOption.dataSearch);
  },
  methods: {
    saveImage() {
      this.$refs.line.saveImg().then(e => {
        this.$refs.down.href = e;
        this.$refs.down.download = ".png";
      });
      return false;
    },
    saveSceneImage() {
      this.$refs.lineTwo.saveImg().then(e => {
        this.$refs.downTwo.href = e;
        this.$refs.downTwo.download = ".png";
      });
      return false;
    },
    getSceneEnum() {
      this.$http.get("vcpsystem/dict/findByType?type=scene").then(e => {
        this.sceneCodeEnumList = [...e.data.data];
        this.sceneCodeEnumList.unshift({
          label: "",
          value: ""
        });
      });
    },
    getScene(_data) {
      this.getHttp({
        elem: "sceneOption",
        url: `vcpinterface/ConvFaceReceive/StatisticsBySceneCode`,
        _data: _data,
        echartName: "scene",
        echartData: "sceneNumList"
      });
    },
    getHttp(_option = {}) {
      /**
       * _option:{
       * elem:"xxxx",areaOption,
       * loadElem:"xxx"dom
       * methods:"post",
       * url:"xxx",
       * _data:{}
       * echartName"xxx"echarts
       * echartData"xxx"echarts
       * echartCount"xxx"echarts 
       * }
       */
      let option = JSON.parse(JSON.stringify(_option));
      this[option.elem].pieShow = false;
      this[option.elem].lineShow = false;
      this.$http
        .setTargetLoadType(option.loadElem || ".report")
        [option.methods || "post"](option.url, option._data || {})
        .then(response => {
          let _data = response.data.Res || response.data;
          this[option.elem].xAxisList = _data.dateArray;
          this[option.elem].listPieCount = _data.totalData.total;
          this[option.elem].list = _data.dataList.map((item, index, arr) => {
            let newArr = {
              name: item[option.echartName || "address"],
              data: item[option.echartData || "addressNumList"]
            };
            return newArr;
          });
          this[option.elem].listPie = _data.totalData.array.map(
            (item, index, arr) => {
              let newArr = {
                name: item[option.echartName || "address"],
                value: item[option.echartCount || "count"]
              };
              return newArr;
            }
          );
          this[option.elem].pieShow = true;
          this[option.elem].lineShow = true;
        });
    },
    getAxios(_option = {}) {
      /**
       * _option:{
       * elem:"xxxx",areaOption,
       * loadElem:"xxx"dom
       * methods:"post",
       * url:"xxx",
       * _data:{}
       * echartName"xxx"echarts
       * echartData"xxx"echarts
       * echartCount"xxx"echarts 
       * }
       */
      let option = JSON.parse(JSON.stringify(_option));
      this[option.elem].pieShow = false;
      this[option.elem].lineShow = false;
      this.$http
        .setTargetLoadType(option.loadElem || ".report")
        [option.methods || "post"](option.url, option._data || {})
        .then(response => {
          let _data = response.data.Res || response.data;
          this[option.elem].xAxisList = _data.dateArray;
          this[option.elem].listPieCount = _data.totalData.total;
          this[option.elem].list = _data.dataList.map((item, index, arr) => {
            let newArr = {
              name: item[option.echartName || "address"],
              data: item[option.echartData || "addressNumList"]
            };
            return newArr;
          });
          this[option.elem].listPie = _data.totalData.arrays.map(
            (item, index, arr) => {
              let newArr = {
                name: item[option.echartName || "address"],
                value: item[option.echartCount || "count"]
              };
              return newArr;
            }
          );
          this[option.elem].pieShow = true;
          this[option.elem].lineShow = true;
        });
    },
    getTree(_data) {
      this.$http.get(`regisger-object/area-tree`, _data).then(response => {
        let newResponse = [];
        response.data.find(e => {
          if (e.id == this.areaId || e.parentId == this.areaId) {
            newResponse.push(e);
          }
        });
        console.log("ded", newResponse);
        let newArr = newResponse.map((item, index, arr) => {
          let newArr = {
            label: item.name,
            value: item.id,
            id: item.id,
            parentId: item.parentId
          };
          return newArr;
        });

        this.areaOption.treeList = this.$util.listToTree(newArr, {
          code: "id",
          parent: "parentId",
          children: "children"
        });
      });
    },
    search(_index, _data) {
      if (this.$util.isEmpty(_index)) {
        this.areaOption.searchList.forEach(e => {
          e.active = false;
        });
      } else {
        this.areaOption.searchList.forEach(e => {
          e.active = false;
        });
        this.$set(this.areaOption.searchList[_index], "active", true);
        this.areaOption.dataSearch.TimeType = _data;
      }
      this.getAreaData(this.areaOption.dataSearch);
    },
    searchScene(_index, _data) {
      if (this.$util.isEmpty(_index)) {
        this.sceneOption.searchList.forEach(e => {
          e.active = false;
        });
      } else {
        this.sceneOption.searchList.forEach(e => {
          e.active = false;
        });
        this.$set(this.sceneOption.searchList[_index], "active", true);
        this.sceneOption.dataSearch.TimeType = _data;
      }
      this.getScene(this.sceneOption.dataSearch);
    },
    handleChange(value) {
      this.areaOption.dataSearch.AreaCode = value[value.length - 1];
    },
    handleSceneChange(value) {
      this.sceneOption.dataSearch.AreaCode = value[value.length - 1];
    }
  }
}


