import Vue from 'vue'
import App from './App'
import router from './router'
import api from './http'
import i18n from './i18n'
import store from './store'
import global from '@/utils/global'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'font-awesome/css/font-awesome.min.css'
import '@/assets/iconfont/iconfont.css'
import "@/assets/css/customGlobal.css"
import scroll from 'vue-seamless-scroll';
import echarts from 'echarts';
import axios from '@/utils/axios';
import util from '@/utils/util';
import dealData from '@/utils/dealData';
import "@/views/xinjiang/elementScss/reset.scss";
import "babel-polyfill";
import {websocketUrl,areaId} from "@/utils/global"
Vue.prototype.$wsUrl = websocketUrl;
Vue.prototype.$areaId = areaId;
Vue.prototype.$echarts = echarts;
Vue.prototype.$http = axios;
Vue.prototype.$util = util;
Vue.prototype.$dealData = dealData ;
Vue.use(scroll)
Vue.use(ElementUI)
Vue.use(api)

Vue.prototype.global = global

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {  // 
    if (sessionStorage.getItem("user") && JSON.parse(sessionStorage.getItem("user")).id) {  // vuex statetoken
      next();
    } else {
      router.push({ path: "/login" });
    }
  } else {
    next();
  }
});



new Vue({
  el: '#app',
  i18n,
  router,
  store,
  render: h => h(App)
});


