<template>
    	<div class="zzjggl">
      <div class="panel">
        <div div class="panel">
          <div class="base-search">
             <!-- <label></label>
              <span class="m-input">
                  <el-input class="w-200"  v-model="selectFormData.name" placeholder=""></el-input>
              </span>
              <a href="javascript:void(0)" class="btn-search"  @click="search()"></a>  -->  
              <a href="javascript:void(0)" class="btn-add"  @click="add()" v-if="permissionResource.hasPermission('CREATION')"></a>       
          </div>
          <div class="line"></div>
          <div>
              <el-table
                :data="table.tableData"
                style="width: 100%;"
                row-key="id"
                border
                default-expand-all
                class="table-grid"
                @selection-change="handleSelectionChange"
                :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
                <el-table-column
                type="selection"
                width="55">
               </el-table-column>
                <el-table-column
                prop="name"
                label=""
                sortable
                width="180">
                </el-table-column>
               <el-table-column
                label=""
                sortable
                width="180">
                <template slot-scope="scope">
                  <span v-if="scope.row.menu===true" class="middle middle-two"></span>
                  <span v-else class="middle middle-one"> </span>
                </template>
                </el-table-column>
                <el-table-column
                prop="address"
                 sortable
                label="">
                </el-table-column>
                 <el-table-column
                 sortable
                label="URL">
                <template slot-scope="scope">
                  <span v-for="(item,index) in  scope.row.uris" :key="index">
                    <span v-if="scope.row.uris.length-1!=index">{{item}},</span>
                    <span v-else>{{item}}</span>
                  </span>
                </template>
                </el-table-column>
                 <el-table-column
                prop="identification"
                 sortable
                label="">
                </el-table-column>
                 <el-table-column
                 prop="rank"
                 sortable
                 label="">
                </el-table-column>
                <el-table-column label="" v-if="permissionResource.hasPermission('UPDATING')||permissionResource.hasPermission('DELETION')">
                  <template slot-scope="scope">
                    <a href="javascript:void(0)" @click="edit(scope.row)" v-if="permissionResource.hasPermission('UPDATING')" title="">
                      <i class="btn-icon icon-16"></i>
                    </a>
                    <a href="javascript:void(0)" @click="del(scope.row)" v-if="permissionResource.hasPermission('DELETION')" title="">
                      <i class="btn-icon icon-17"></i>
                    </a>
                  </template>
                </el-table-column>
            </el-table>
            <!--<div class="tableGridPage">
              <div class='pageInfo'> 
                  <div><span class='numberColor'>{{pageData.total}}</span> </div>
                  <div>{{` ${pageData.pageSize ? pageData.pageSize : 10} `}}</div> 
              </div>
              <div class='pagePation'>  
                  <el-pagination  layout=" prev, pager, next, jumper" :total="pageData.total ? pageData.total : 0 " 
                   @current-change="changePage"
                  background :page-size="pageData.pageSize ? pageData.pageSize : 10" class="narmalPage"></el-pagination>
              </div>
          </div>-->
          </div>
        </div>
      </div>
       <msg-dialog v-if="dialogVisible.msgDialog" :pDialog='msgDialog' @equit="equit">
        <dialog-opt :pData='msgDialog.data' @result="dialogOpt"></dialog-opt>
      </msg-dialog>
    </div>
</template>
<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import msgDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
import dialogOpt from "@/views/xinjiang/system/opt/menuOpt";
export default {
  data() {
    let vm = this;
    let permissionResources = vm.$store.state.permission.permissionResources;
    let permissionResource = permissionResources.getPermissiomResource(
      vm.$route.path
    );
    return {
      permissionResource: permissionResource,
      selectFormData: {},
      dialogVisible: {
        msgDialog: false
      },
      msgDialog: {
        title: "",
        dialogVisible: true,
        width: "520px",
        data: {}
      },
      pageData: {
        size: 9,
        page: 1,
        total: 20
      },
      table: {
        selectData: [],
        tableData: []
      }
    };
  },
  mounted() {
    this.getTree();
  },
  methods: {
    handleSelectionChange(val) {
      this.table.selectData = val;
    },
    getTable(_data) {
      return new Promise(resolve => {
        this.$http.get(`/menu`, _data).then(response => {
          resolve(response.data);
        });
      });
    },
    getTree() {
      let _arr = [];
      this.getTable().then(res => {
        let meunList = res.filter(e => {
          return e.menu == true;
        });
        let prop = {
          code: "id",
          parent: "parentId",
          children: "children"
        };
        this.table.tableData = this.$util.listToTree(meunList, prop); //   console.log("de",_arr);
      });
    },

    changePage(pageIndex) {
      this.pageData.pageNum = pageIndex;
      this.getTable(this.pageData);
    },
    equit() {
      this.dialogVisible.msgDialog = false;
    },
    add() {
       this.msgDialog.title = "";
      this.$util.clearObjectValue(this.msgDialog.data.data);
      this.msgDialog.data.methods = "post";
      this.msgDialog.data.url = "/menu";
      this.dialogVisible.msgDialog = true;
    },
    edit(row) {
      this.msgDialog.title = "";
      this.msgDialog.data.methods = "put";
      this.msgDialog.data.url = `/menu/${row.id}`;
      this.msgDialog.data.data = row;
      this.dialogVisible.msgDialog = true;
    },
    del(row) {
      let id = row.id;
      this.delData(id);
    },

    /* batDel() {
      let allData = [];
      this.table.selectData.forEach(e => {
        allData.push(e.id);
      });
      if (
        Object.prototype.toString.call(allData) === "[object Array]" &&
        allData.length === 0
      ) {
        this.$message({
          message: "",
          type: "warning"
        });
        return;
      }
      this.delData(allData.join(","));
    },*/
    delData(id) {
      let vm = this;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http.delete(`/menu/${id}`).then(response => {
          this.$message({
            message: response.data.msg || "",
            type: "success"
          });
          vm.getTree();
        });
      });
    },
    search() {
      this.getTable(this.selectFormData);
    },
    dialogOpt(_data) {
      if (_data) {
        this.getTree();
        this.equit();
      }
    }
  },
  components: {
    "table-grid": tableGrid,
    "msg-dialog": msgDialog,
    "dialog-opt": dialogOpt
  }
};
</script>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.sub-page {
  padding: 0;
  height: 100%;
  .zzjggl {
    float: right;
    width: calc(100% - 190px);
    height: calc(100vh - 120px);
    overflow: hidden;
    .panel {
      height: 100%;
      width: calc(100% + 20px);
      padding-right: 21px;
      overflow: auto;
    }
  }
  .t-head,
  .base-search {
    text-align: left;
  }
}
.middle {
  box-sizing: border-box;
  padding: 0 5px;
  margin: 0 20px;
  border-radius: 2px;
}

.middle-one {
  color: #0fbcdb;
  border: 1px solid #0fbcdb;
}

.middle-two {
  color: #1edb0f;
  border: 1px solid #0fbcdb;
}

.middle-three {
  color: #dbad0f;
  border: 1px solid #dbad0f;
}
</style>



