<template>
  <div class="sub-page">
    <div class="zzjggl">
      <div class="panel">
        <div class="base-search">
          <label></label>
          <span class="m-input">
            <input name type="text" v-model="searchForm.serverName" placeholder="" class="w-200" />
          </span>
          <a href="javascript:void(0)" @click="search" class="btn-search"></a>
          <a href="javascript:void(0)" @click="add" class="btn-add"></a>
        </div>
        <div class="line"></div>
        <div>
          <div class="base-table">
            <tableGrid :option="option" ref="deviceList">
              <template slot-scope="row">
                <!-- <a href="javascript:void(0)" @click="reboot(row)" title="">
                  <i class="btn-icon icon-12"></i>
                </a>
                <a href="javascript:void(0)" title="">
                  <i class="btn-icon icon-13"></i>
                </a> -->
                <a href="javascript:void(0)" @click="edit(row)" title="">
                  <i class="btn-icon icon-16"></i>
                </a>
                <a href="javascript:void(0)" @click="deleteDevice(row)" title="">
                  <i class="btn-icon icon-17"></i>
                </a>
                <!-- <a href="javascript:void(0)" title="">
                  <i class="btn-icon icon-14"></i>
                </a> -->
              </template>
            </tableGrid>
          </div>
        </div>
      </div>
    </div>
    <addDialog :pDialog="addOption" @equit="closeDialog">
      <el-form
        :inline="true"
        :model="server"
        label-width="115px"
        :rules="serverValidata"
        class="deviceForm"
        ref="deviceForm"
      >
        <el-form-item label=":" prop="serverName">
          <el-input
            placeholder=""
            v-model="server.serverName"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label=":" prop="serverType.value">
          <el-select
              v-model="server.serverType.value"
              popper-class="m-select-con"
              class="serviceSelect"
              placeholder=""
            >
              <el-option
                v-for="item in serviceTypeData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label=":" prop="ip">
          <el-input
            placeholder=""
            v-model="server.ip"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label=":" prop="port">
          <el-input
            placeholder=""
            v-model="server.port"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label=":" >
          <el-input
            placeholder=""
            v-model="server.serverAddress"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label=":" prop="serverId">
          <el-input
            placeholder=""
            v-model="server.serverId"
            :clearable="true"
          ></el-input>
        </el-form-item>

        <el-form-item label=":" prop="manufacturerName.value">
          <!-- <el-input
            placeholder=""
            v-model="server.manufacturerName.value"
            :clearable="true"
          ></el-input> -->

          <el-select
              v-model="server.manufacturerName.value"
              class="serviceSelect"
              popper-class="m-select-con"
              placeholder=""
            >
              <el-option
                v-for="item in manufacturerNameData"
                :key="item.value"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
        </el-form-item>

        <el-form-item label=":">
          <el-input
            placeholder=""
            v-model="server.deviceMaxNum"
            :clearable="true"
          ></el-input>
        </el-form-item>


        <el-form-item label=":">
          <el-input
            placeholder=""
            v-model="server.baseUrl"
            :clearable="true"
          ></el-input>
        </el-form-item>
        
      </el-form>
      <div class="search-more deviceAddOpt">
        <a href="javascript:void(0)" class="search-more-btn" @click="addOrEdit"></a>
        <a href="javascript:void(0)" class="search-more-btn" @click="cancel"></a>
      </div>
    </addDialog>

    
  </div>
</template>

<script>
import tableGrid from "@/views/xinjiang/common/components/table/src/tableGrid";
import addDialog from "@/views/xinjiang/common/components/dialog/msgDialog";
export default {
  data() {
    let vm = this;
    return {
      requestServer : false,
      advancedSearch: false,
      option: {
        isShowTableNav: false,
        tableOption: {
          isShowSelect: true,
          singleSelect: false,
          tableTitle: [
            { prop: "id", label: "id" },
            { prop: "serverType.text", label: "" },
            { prop: "serverName", label: "" },
            { prop: "ip", label: "" },
            { prop: "port", label: "" },
            { prop: "serverAddress",label: ""},
            { prop: "serverState", label: ""},
            { prop: "serverId", label: ""},
            { prop: "manufacturerName.text", label: ""},
            { prop: "deviceMaxNum", label: ""},
            { prop: "deviceNum", label: ""},
            { prop: "baseUrl", label: ""},
          ],
          tableBodyOverFlow: false,
          fixedHeight: 360,
          hasCustomColum: true,
          opeColumnWidth: 220
        },
        customLabel: "",
        url: "/server",
        type: "get",
        notRequestSever: false,
        param: {}
      },

      addOption: {
        title: "",
        dialogVisible: false,
        width: "70%",
        isAdd: true,
        id: ""
      },

      server : {
        serverName : "",
        ip : "",
        port : "",
        serverAddress : "",
        serverState : "",
        serverId : "",
        manufacturerName : {
          enum: true,
          text : "",
          value : ""
        },
        serverType:{
          enum: true,
          text : "",
          value : ""
        },
        deviceMaxNum : "",
        deviceNum : "",
        baseUrl : ""
      },

      serverValidata:{
        serverName : [{ required: true, message: "" }],
        port : [{ required: true, message: "" }],
        serverId : [{ required: true, message: "" }],
        ip : [{ required: true, message: "" }],
        manufacturerName : [{ required: true, message: "" }],
       
      },

      serviceTypeData:[],
      manufacturerNameData : [],

      searchForm: {
        serverName: "",
      },
    };
  },

  mounted() {
    this.getConstant();
  },

  methods: {
    search() {
      let param = {};
      if (this.searchForm.serverName) {
        param.serverName = this.searchForm.serverName;
      }
      //Object.assign(param,this.searchForm);
      this.$refs.deviceList.searchTableData(param);
    },

    getConstant(){
      this.$http.get("/server/constant").then(res =>{
        this.serviceTypeData = res.data[1].options;
        this.manufacturerNameData = res.data[2].options;
      })
    },




    add() {
      this.addOption.title = "";
      this.addOption.isAdd = true;
      this.$util.clearObjectValue(this.server);
      this.server.manufacturerName.enum =  true;
      this.server.serverType.enum = true;
      this.addOption.dialogVisible = true;
    },

    edit(row) {
      this.$util.clearObjectValue(this.server);
      this.getEditData(row.rowData);
      this.addOption.title = "";
      this.addOption.isAdd = false;
      this.addOption.id = row.rowData.id;
      
      this.addOption.dialogVisible = true;
    },

    getEditData(serverData) {
      this.$util.setObjectValue(this.server, serverData);
    },

    addOrEdit() {
      if(this.requestServer){
        console.log("");
        return ;
      }
      this.$refs.deviceForm.validate(validate => {
        if (validate) {
          if (this.addOption.isAdd) {
            this.$http.post("/server", this.server).then(response => {
              this.addOption.dialogVisible = false;
              this.$message({
                message: "",
                type: "success"
              });
              this.requestServer = false;
              this.$refs.deviceList.flushTableData();
            });
          } else {
            this.$http
              .put(`/server/${this.addOption.id}`, this.server)
              .then(response => {
                this.addOption.dialogVisible = false;
                this.$message({
                  message: "",
                  type: "success"
                });
                this.requestServer = false;
                this.$refs.deviceList.flushTableData();
              });
          }
        } else {
          console.log("");
          return false;
        }
      });
    },

    cancel() {
      this.addOption.dialogVisible = false;
      this.requestServer = false;
    },

    closeDialog(){
      this.addOption.dialogVisible = false;
      this.requestServer = false;
    },
    

    deleteDevice(row) {
      let id = row.rowData.id;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http.delete(`/server/${id}`).then(response => {
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs.deviceList.flushTableData();
        });
      });
    },
  },

  watch: {},

  computed: {},

  components: {
    tableGrid,
    addDialog,
  }
};
</script>
<style lang="scss" scoped>
.sub-page-content {
  background: url("../../../assets/img/login/bg1.jpg") center 0 no-repeat
    #051121;
}
</style>
<style scoped lang="scss">
@import "../../../assets/css/style1.css";
.search-more {
  display: block;
}
.deviceAddOpt {
  border: unset;
  text-align: center;
}

.deviceForm .el-form-item {
  margin-bottom: 20px;
}
.base-search {
  text-align: left;
  padding-bottom: 10px;
}
.serviceSelect{
  width: 216px;
}
</style>




