<template>
  <div class="faceDetail">
      <el-row>
          <el-col :span="8">
              <img :src="formData.imageUrl" style="width:150px;height:150px"/>
          </el-col>
          <el-col :span="16">
              <el-form>
                  <el-form-item label="">{{formData.name}}</el-form-item>
                  <el-form-item label="">{{formData.gender}}</el-form-item>
                  <el-form-item label="">{{formData.repoId}}</el-form-item>
                  <el-form-item label="">{{formData.credentialType}}</el-form-item>
                  <el-form-item label="">{{formData.credentialNumber}}</el-form-item>
                  <el-form-item label="">{{formData.nationality}}</el-form-item>
                  <el-form-item label="">{{formData.country}}</el-form-item>  
              </el-form>
          </el-col>
      </el-row>
  </div>
</template>

<script>
import {baseUrl} from "@/utils/global"
export default {
  components:{},
  props:{
      pData:{
          type: Object,
          default: () =>{}
      }
  },
  data(){
    return {
        formData:{
            name:'',
            ger: '',
            id: 43365662223,
            han: '',
            d: ''

        },
        constant: {lib: []}
    }
  },
  watch:{},
  computed:{},
  methods:{},
  created(){
      if(this.pData){ 
          this.formData = this.pData; 
          this.formData.gender = this.formData.gender == 0 ? '' : '';
          let type = this.formData.credentialType; 
          switch (type) {
              case '0':
                this.formData.credentialType = '' 
                  break;
              case '1':
                  this.formData.credentialType = '' 
                  break;
              case '2':
                this.formData.credentialType = '' 
                  break;
              case '3':
                  this.formData.credentialType = '' 
                  break;
              case '4':
                this.formData.credentialType = '' 
                  break;
              case '5':
                  this.formData.credentialType = '' 
                  break;
              default:
                  break;
          } 
          this.$http.post(baseUrl + `/vcpface/facerepository/findByPage`).then(response =>{
            this.constant.lib = response.data.res.data.map(item => (({name, id}) => ({name, id}))(item));
            this.constant.lib.forEach(item =>{
                if(item.id == this.formData.repoId){
                    this.formData.repoId = item.name;
                }
            })
          })
      }
  },
  mounted(){}
}
</script>
<style lang="scss" scoped>
.faceDetail{
    .el-form-item {
        margin-bottom: 0px;
        color: #fff!important; 
    } 
}
</style>


