import axios from '../axios'

/* 
 * 
 */

// 
export const save = (data) => {
    return axios({
        url: '/vcpsystem/dict/save',
        method: 'post',
        data
    })
}
// 
export const batchDelete = (data) => {
    return axios({
        url: '/vcpsystem/dict/delete',
        method: 'post',
        data
    })
}

// 
export const findPage = (data) => {
    return axios({
        url: '/vcpsystem/dict/findPage',
        method: 'post',
        data
    })
}
// 
export const findByType = (params) => {
    return axios({
        url: '/vcpsystem/dict/findByType',
        method: 'get',
        params
    })
}


