<template>
    <div>
        <!-- <div class="navTitle treeTitle">
            <span class="iconfont icon-tubiaoshu"></span>
            <span>{{option.title}}</span>
        </div> -->
        <div class="treeContent">
            <el-tree
                :data="data"
                :props="option.prop"
                :show-checkbox='checkStatus'
                accordion
                @node-click="handleNodeClick"
                :highlight-current="true"
                :default-expanded-keys="[65]"
                node-key = "id"
                ref="tree">
                <template  slot-scope="{node,data}">
                    <span >
                        <slot :data="data"></slot>
                        {{data.name}}
                    </span> 
                </template>
                
            </el-tree>
        </div>
    </div>
</template>

<script>
/**
 * 
 * 
 * option : {
 *      url : '',//
 *      param : {},//
 *      prop  {}//element tree 
 *      type : "post" //
 *      title: ""//
 * }
 * 
 */
export default {
    props:["option"],
    data() {
        return {
            data : [],
            checkStatus: false
        }
    },

    mounted() {
        this.init();
    },

    methods: {
        getCheck(){
            this.checkStatus = true;
        },
        getTreeData(){ 
            let orgMenu = [], org = this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()); 
            org.forEach(item =>{
               orgMenu.push({id: item}); 
            })
            return orgMenu
        },
        init() {
            let vm = this;
            vm.$http[vm.option.type](vm.option.url,vm.option.param).then((res)=>{
                let list = res.data || [];
                let prop = {
                    code: 'id',
                    parent: 'parentId',
                    children: 'children'
                }
                this.data = vm.$util.listToTree(list,prop);//
                this.$emit("finishLoadTree",list);
            }); 
        }, 
        reloadTree() {
            this.data = [];
            this.init();
        },

        handleNodeClick(data,node) {
            this.$emit("clickNode",data,node);
        },

        getCurrentNode(){
            return this.$refs.tree.getCurrentNode();
        },

        setCurrentNode(node){
            this.$refs.tree.setCurrentNode(node);
        },

        cancelNodeSelect(){
            this.$refs.tree.setCurrentKey(null);
        }
    },

    watch :{

    },

    computed :{

    },
    
    components :{

    }

}
</script>

<style  scoped>
.treeTitle{
    border: 1px solid #ccc;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;  
}
.treeContent{
    height: 100%;
}
</style>




