
import PermissionUser from '@/views/xinjiang/model/permission/PermissionUser'
const user = ({
    state : {
        user : getUser(),
        openEidt : false
    },
    mutations:{
        user_save(state,user){
            state.user = user;
        },

        user_clear(state){
            state.user = {};
        }
    },
    modules:{

    },
    actions:{
        
    }
});

function getUser(){
    let user = JSON.parse(sessionStorage.getItem("user")) || {};
    let permissionUser = new PermissionUser();
    permissionUser.setData(user);
    return permissionUser;
}

export default user;


